import appConstants from '../constants/appConstants';

/**
 * ***IMPORTANT***
 * The functions here are 'action creators'. Action creators
 * dispatch actions to trigger reducers, which is the only way
 * to update the redux store.
 */

/**
 * dispatches an action that alternatives are fetched
 * replaces the reducer state with the fetched items
 * @param {object} payload
 * @return {object} : action object
 */
export const alternativesFetch = (payload) => ({
  type: appConstants.get('FETCH_ALTERNATIVES'),
  payload,
});

