/**
 * dispatches an action to add alternatives
 * appends to the existing state
 * @param {object} payload
 * @return {object} : action object
 */
export const addAlternative = (payload) => ({
  type: appConstants.get('ADD_NEW_ALTERNATIVES'),
  payload,
});

/**
 * adds an alternative to a collection
 * @param {string} collectionId 
 * @param {string} alternativeId 
 * @param {array} collectionInputs
 * @param {array} collectionOutputs
 * @param {array} dimOrder
 * @return {object} : action object
 */
export const addAlternativeToCollection = (collectionId, alternativeId, collectionInputs, collectionOutputs, dimOrder) => {
  
  return ({
    type: appConstants.get('ADD_ALTERNATIVE_TO_COLLECTION'),
    payload: {
      collectionId, 
      alternativeId,
      collectionInputs,
      collectionOutputs,
      dimOrder
    }
  });
};

/**
 * updates an alternative in the store
 * with data coming from server
 * @param {string} viewId
 * @param {object} alternative
 * @return {object} : action object
 */
export const updateAlternative = (viewId, alternative) => {
  return ({
    type: appConstants.get('UPDATE_ALTERNATIVE'),
    payload: {
      viewId,
      alternative,
    }
  });
};

/**
 * dispatches an action to update
 * maximum number of columns of gridLayout
 * this is invoked whenever a collection is dragged to the
 * current edge of gridLayout
 * @param {object} layout
 * @return {object} : action object
 */
export const updateMaxColumns = (layout) => {
  return ({
    type: appConstants.get('UPDATE_MAX_COLUMNS'),
    width: Math.max(...layout.map((item) => item.x + item.w)) + 9,
    layout,
  });
}

/**
 * dispatches an action that creates a new collection
 * appends to existing list of collections
 * @param {array} altIds
 * @param {string} collectionName
 * @param {string} refId
 * @param {array} tags
 * @param {string} viewId
 * @param {boolean} isSelected
 * @param {boolean} isSinked
 * @param {number} viewType
 * @param {array} dimOrder
 * @param {array} inputs
 * @param {array} outputs
 * @param {array} destinations
 * @param {number} currentMode
 */
export const createCollection = (altIds, collectionName, refId,
  tags, viewId, isSelected, isSinked, viewType, dimOrder, inputs, outputs, destinations, currentMode, isEditingName, collectionNameView) => {
  return ({
    type: appConstants.get('CREATE_COLLECTION'),
    payload: {
      'altsid': altIds,
      'collectionName': collectionName,
      'refId': refId,
      'tags': tags,
      'viewId': viewId,
      'isSelected': isSelected,
      'isSinked': isSinked,
      'viewType': viewType,
      'dimOrder': dimOrder,
      'inputs':  inputs,
      'outputs': outputs,
      'destinations': destinations,
      'currentMode': currentMode,
      'isEditingName': isEditingName,
      'collectionNameView': collectionNameView,
    },
  });
};

