/**
 * clips a collection i.e. removes it from grid layout
 * it saves the collection's id, its alterntives and layout before doing that
 * @param {string} viewId
 * @param {string} refId
 * @param {array} altIDs
 * @param {array} layout
 * @return {object} : action object
 */
export const clipCollection = (viewId, refId, altIDs = [], layout = []) => ({
  type: appConstants.get('CLIP_COLLECTION'),
  payload: {
    'viewId': viewId,
    'refId': refId,
    'altsid': altIDs,
    'layout': layout,
  },
});

