import React, {Component} from 'react';
import {connect} from 'react-redux';
import Collections from '../components/collections';
import {
  updateMaxColumns,
  layoutFetch,
  selectCollection,
  deselectCollection,
  deselectAlternative,
  dehoverAlternative,
  clipAlternatives,
  clipCollection,
  clearAltsClipboard,
  clearColsClipboard,
  editNameForCollection,
  isEditingCollectionsName,
  removeFocus,
  showHeader,
  hideHeader,
  saveLayoutObject,
  hideSinkList,
  deselectAllAlternatives,
  deselectAllCollections,
  toggleViewMode,
  toggleAltView,
  alterAlternative,
  toggleSettings,
  toggleSearch,
  searchedCollectionSuccess,
  searchedCollectionFailed,
  emptyMissMatchArray,
  setCollectionDestination,
  setCollectionMode,
  updateCollectionTags,
  hoverAlternative,
  selectAlternative,
  updateLayoutForCollection,
  editCollectionName,
} from '../actions/updateActions';
import {
  refAlternativesToCollection,
  cloneAlternativesToCollection,  
  cloneEditAlternativesToCollection,
  cutAlternativesFromCollection,
  hoverAlternativeSpecial,
  editAlternatives,
  restoreAlternative,
  removeAlternativesFromCollection,
} from '../actions/alternativeActions';
import {
  sinkCollection,
  createNewCollection,
  copyWithRefCollection,
  cloneCollection,
  deepCloneCollection,
  cloneAltInNewCollection,
  deleteCollections,
  updateCollection,
} from '../actions/collectionActions';
import {
  openWebSocketConnection, 
  onMessageRecieved,
  sendMessage,
} from '../actions/websocketActions';
import {
  successMessage,
  warningMessage,
  errorMessage,
} from '../actions/notificationActions';
import {
  selectProject,
  createProject
} from '../actions/projectActions';
import {
  updateCompactionType,
  updateAlternativeSize,
  updateCollisionPrevention,
} from '../actions/settingActions';
import {renderIf} from '../util/util';

/**
 * This component connects the Collections component to the state.
 */
class CollectionContainer extends Component {
  /**
   * The component should render the presentational component Collections.
   * The component is only rendered after all details are fetched
   * @return {ReactComponent}
   */
  render() {

    const projectFetched = this.props.projects.get('projectFetched');
    const projectLoaded = this.props.projects.get('projectLoaded')
    const layoutsFetched = this.props.layouts.get('layoutsFetched');
    const alternativesFetched = this.props.alternatives.get('alternativesFetched');
    const collectionsFetched = this.props.collections.get('collectionsFetched');
    return ( 
        renderIf(
          projectFetched && layoutsFetched && alternativesFetched && collectionsFetched,
          <Collections {...this.props} />
        )
    );
  }
}

/**
 * state for the collection container
 * @param {object} state
 * @return {object}
 */
const mapStateToProps = (state) => ({
  alternatives: state.alternatives,
  collections: state.collections,
  user: state.auth,
  layouts: state.layouts,
  projects: state.projects,
  settings: state.settings,
  search: state.search,
  header: state.header,
  websocket: state.websocket,
})

/**
 * interactions for collection container
 * @param {function} dispatch
 * @return {object}
 */
const mapDispatchToProps = (dispatch) => ({

  /**
   * Dispatches an action to update the store that the websocket is open.
   */
  openWebSocketConnection: () => {
    dispatch(openWebSocketConnection());
  },

  /**
   * Dispatches an action wheen a message is received.
   * @param {object} ev
   */
  onMessageRecieved: (ev) => {
    dispatch(onMessageRecieved(ev));
  },

  /**
   * send a message.
   * @param {object} websocket
   * @param {String} data
   */
  sendMessage: (websocket, data) => {
    dispatch(sendMessage(websocket, data));
  },

  /**
   * selects a project.
   * @param {object} project
   */
  selectProject: (project) =>{
    dispatch(selectProject(project));
  },

  /**
   * creat a project.
   * @param {string} name
   * @param {string} description
   */
  createProject: (name,description) =>{    
    dispatch(createProject(name,description));
  },
  /**
   * Dispatches an action to increase the maximum number of columns
   * based on the end points of the layouts
   * @param {ImmutableList} layout
   */
  updateMaxColumns: (layouts) => {
    dispatch(updateMaxColumns(layouts));
  },

  /**
   * dipaches an action to delete an alternative from a collection
   * @param {int} altID: the alternative id to delete
   */
  cutAlternativesFromCollection: (altID) => {
    dispatch(cutAlternativesFromCollection(altID));
  },

  /**
   * dispatches an action to copy (with reference) an alternative
   * from one collection to another
   * @param {array} alts: alternatives to ref
   * @param {array} cols: destination refs
   */
  refAlternatives: (alts = [], cols = []) => {
    dispatch(refAlternativesToCollection(alts, cols));
  },
  /**
   * dispatches an action to copy (with reference) an alternative
   * from one collection to another
   * @param {string} viewId: the view ID
   * @param {string} refId: the reference ID
   * @param {string} collectionId: the collection ID
   * @param {string} newId: new ID
   */
  cloneAlternatives: (alts = [], cols = []) => {
    dispatch(cloneAlternativesToCollection(alts, cols));
  },

  cloneEditAlternatives: (alts = [], cols = []) => {
    dispatch(cloneEditAlternativesToCollection(alts, cols))
  },

  /**
   * dispatches an action to delete a collection
   * @param {string} collectionId: the id of the collection to delete
   */
  deleteCollections: (collectionId) => {
    dispatch(deleteCollections(collectionId));
  },

  /**
   * dispatches an action to select the collection with that ID
   * @param {string} viewId: the view ID
   */
  selectCollection: (viewId) => {
    dispatch(selectCollection(viewId));
  },

  /**
   * dispatches an action to deselect the collection with that ID
   * @param {string} viewId: the view ID
   */
  deselectCollection: (viewId) => {
    dispatch(deselectCollection(viewId));
  },

  /**
   * dispatches an action to copy the collection with that ID
   * into the collections clipboard
   * @param {string} viewId
   * @param {string} refId
   * @param {array} altIDs
   * @param {array} layout
   */
  clipCollection: (viewId, refId, altIDs = [], layout = []) => {
    dispatch(clipCollection(viewId, refId, altIDs, layout));
  },

  /**
   * dispatches an action to select the alternative with that ID
   * from the collection it was clicked on
   * @param {string} viewId: the alternative ID
   */
  selectAlternative: (viewId) => {
    dispatch(selectAlternative(viewId));
  },

  /**
   * dispatches an action to select the alternative with that ID
   * from the collection it was clicked on
   * @param {string} viewId: the alternative ID
   */
  deselectAlternative: (viewId) => {
    dispatch(deselectAlternative(viewId));
  },  /**
   * dispatches an action to select the alternative with that ID
   * from the collection it was clicked on
   * @param {string} viewId: the alternative ID
   */
  hoverAlternative: (viewId) => {
    dispatch(hoverAlternative(viewId));
  },

  hoverAlternativeSpecial: (viewId) => {
    dispatch(hoverAlternativeSpecial(viewId));
  },

  /**
   * dispatches an action to select the alternative with that ID
   * from the collection it was clicked on
   * @param {string} viewId: the alternative ID
   */
  dehoverAlternative: (viewId) => {
    dispatch(dehoverAlternative(viewId));
  },

  /**
   * dispatches an action to copy the alternative with that ID
   * from the collection it was clicked on
   * to the alternative clipboard
   * @param {string} viewId: the alternative ID
   * @param {string} refId: the alternative ID
   * @param {string} collectionId: the collection ID
   */
  clipAlternatives: (viewId, refId, collectionId) => {
    dispatch(clipAlternatives(viewId, refId, collectionId));
  },

  /**
   * dispatches an action to update the grid layout of the collections
   * @param {ImmutableList} layout
   */
  updateCollectionLayout: (layout) => {
    dispatch(layoutFetch(layout));
  },

  /**
   * dispatches an action to save the layout of the object
   * to the layout clipboard
   * @param {ImmutableList} layout
   */
  saveLayoutObject: (layout) => {
    dispatch(saveLayoutObject(layout));
  },

  /**
   * dispatches an action to create a new collection
   * @param {number} x: the x coordinate where the collection should be created
   * @param {number} y: the x coordinate where the collection should be created
   * @param {number} w: width of the collection that should be created
   * @param {number} h: height of the the collection that should be created
   * @param {array} altids=[]: the new alternative ids to add
   * @param {string} collectionName: name of the collection
   */
  createNewCollection: (x, y, w, h, altids, collectionName) => {
    dispatch(createNewCollection(x, y, w, h, altids, collectionName));
  },

  /**
   * dispatches an action to create a new collection as a ref
   * @param {number} x: the x coordinate where the collection should be created
   * @param {number} y: the x coordinate where the collection should be created
   * @param {number} w: width of the collection that should be created
   * @param {number} h: height of the the collection that should be created
   * @param {array} altids=[]: the new alternative ids to add
   * @param {string} refId: ref id for the collection to be created
   * @param {string} viewId: view id for the collection to be created
   */
  copyWithRefCollection: (viewId) => {
    dispatch(copyWithRefCollection(viewId));
  },

  /**
   * dispatches an action to create a new collection and copy the selected alterenatives in it
   * @param {number} x: the x coordinate where the collection should be created
   * @param {number} y: the x coordinate where the collection should be created
   * @param {number} w: width of the collection that should be created
   * @param {number} h: height of the the collection that should be created
   * @param {array} altids=[]: the new alternative ids to add
   * @param {string} refId: ref id for the collection to be created
   * @param {string} viewIds: view id for the alternatives to be created
   */
  cloneAltInNewCollection: (viewIds) => {
    dispatch(cloneAltInNewCollection(viewIds));
  },


  /**
   * dispatches an action to create a new collection
   * @param {int} x: the x coordinate where the collection should be created
   * @param {int} y: the x coordinate where the collection should be created
   * @param {int} w: width of the collection that should be created
   * @param {int} h: height of the the collection that should be created
   * @param {array} altids=[]: the new alternative ids to add
   * @param {int} refId: ref id for the collection to be created
   * @param {int} viewId: view id for the collection to be created
   */
  cloneCollection: (viewId) => {
    dispatch(cloneCollection(viewId));
  },

  /**
   * dispatches an action to create a new collection
   * @param {int} x: the x coordinate where the collection should be created
   * @param {int} y: the x coordinate where the collection should be created
   * @param {int} w: width of the collection that should be created
   * @param {int} h: height of the the collection that should be created
   * @param {array} altids=[]: the new alternative ids to add
   * @param {int} refId: ref id for the collection to be created
   * @param {int} viewId: view id for the collection to be created
   */
  deepCloneCollection: (viewId) => {
    dispatch(deepCloneCollection(viewId));
  },

  /**
  * dispatches an action to clear the alternatives clipboard
  */
  clearAltsClipboard: () => {
    dispatch(clearAltsClipboard());
  },

  /**
  * dispatches an action to clear the collections clipboard
  */
  clearColsClipboard: () => {
    dispatch(clearColsClipboard());
  },

  /**
   * dispatches an action to edit the name of a collection
   * @param {string} collectionId: the ID of the collection
   * @param {string} collectionName: the new name of the collection
   */
  editNameForCollection: (collectionId, collectionName) => {
    dispatch(editNameForCollection(collectionId, collectionName));
  },

  isEditingCollectionsName: (refId) => {
    dispatch(isEditingCollectionsName(refId));
  }, 
  
  removeFocus: () => {
    dispatch(removeFocus())
  },


  editCollectionName: (name) => {
    dispatch(editCollectionName(name))
  },

  /**
   * dispaches an action to show the header
   * @param {number} headerX: x coordinate
   * @param {number} headerY: y coordinate
   */
  showHeader: (headerX, headerY) => {
    dispatch(showHeader(headerX, headerY));
  },

  /**
   * dispaches an action to hide the header
   */
  hideHeader: () => {
    dispatch(hideHeader());
  },

  /**
   * dispaches an action to sink collection with ID
   * @param {string} collectionId: the ID of the collection
   */
  sinkCollection: (collectionId) => {
    dispatch(sinkCollection(collectionId));
  },

  /**
   * dispaches an action to hide the sink list
   */
  hideSinkList: () => {
    dispatch(hideSinkList());
  },

   /**
   * dispaches an action to deselect all alternatives
   */
  deselectAllAlternatives: () => {
    dispatch(deselectAllAlternatives());
  },

   /**
   * dispaches an action to deselect all collections
   */
  deselectAllCollections: () => {
    dispatch(deselectAllCollections());
  },

  /**
   * toggles collection view mode
   * @param {string} collectionId
   * @param {number} viewType
   */
  toggleViewMode: (collectionId, viewType) => {
    dispatch(toggleViewMode(collectionId, viewType));
  },

  /**
   * toggles alternative 3D view
   * @param {string} alternativeID
   */
  toggleAltView: (alternativeID) => {
    dispatch(toggleAltView(alternativeID));
  },

  alterAlternative: (alternativeID, parameter, newValue) => {
    dispatch(alterAlternative(alternativeID, parameter, newValue));
  },

   editAlternatives: (editedAlternatives) => {
    dispatch(editAlternatives(editedAlternatives));
  },

  restoreAlternative: (viewId) => {
    dispatch(restoreAlternative(viewId));
  },

  updateCollection: (collection) => {
    dispatch(updateCollection(collection));
  },

  removeAlternativesFromCollection: (alts = [] ) => {
    dispatch(removeAlternativesFromCollection(alts));
  },


  setAlternativeSize: (dimensions = []) => {
    dispatch(updateAlternativeSize(dimensions));
  },

  setCompactionType: (type) => {
    dispatch(updateCompactionType(type));
  },

  setCollisionPrevention: (payload) => {
    dispatch(updateCollisionPrevention(payload));
  },

  toggleSettings: () => {
    dispatch(toggleSettings());
  },

  toggleSearch: () => {
    dispatch(toggleSearch());
  },

  /**
   * sets validation to false
   * 
   */
  searchedCollectionSuccess: () => {
    dispatch(searchedCollectionSuccess());
  },

  /**
   *  sets validation to true
   */
  searchedCollectionFailed: (namesMissMatch) => {
    dispatch(searchedCollectionFailed(namesMissMatch));
  },

  emptyMissMatchArray: () => {
    dispatch(emptyMissMatchArray());
  },

  setCollectionDestination: (collection, destinations) => {
    dispatch(setCollectionDestination(collection, destinations))
  },

  setCollectionMode: (collection, mode) => {
    dispatch(setCollectionMode(collection, mode))
  },

  updateCollectionTags: (collectionId, tags) => {
    dispatch(updateCollectionTags(collectionId, tags))
  },

  updateLayoutForCollection: (layoutID) => {
    dispatch(updateLayoutForCollection(layoutID));
  },

  successMessage: (id, message) => {
    dispatch(successMessage(id, message));
  },

  warningMessage: (id, message) => {
    dispatch(warningMessage(id, message));
  },

  errorMessage: (id, message) => {
    dispatch(errorMessage(id, message));
  },
  
});


export default connect(
  mapStateToProps,
  mapDispatchToProps
)(CollectionContainer);
