  commitValueBands = (message, dim, end, val) => {
    if (message === 'OK') {
      this.props.setValueBands(dim, end, val);
    } else if (message === 'NaN') {
      this.setState({shouldWarn: true, warningMessage: 'Please enter a number.'})
    } else if (message === 'OoB') {
      this.setState({shouldWarn: true, warningMessage: 'Please enter a proper range.'})
    }
  }

