  reOrderDefaultOptions = () => {
    const currentDimensions = Object.assign([], this.state.dimensions);

    this.defaultOptions = [];

    currentDimensions.map((d)=> this.defaultOptions.push({value: d, label: d.toString()}));
    this.props.orderCollectionDimensions(this.props.collection.get('viewId'), currentDimensions);
  }

  /**
   * clicking on a path selects it
   * @param {event} e
   */
  selectPath = (e) => {
    const altsSelected = this.props.alternatives.get('alternatives').filter((alternative) => alternative.get('isSelected'))
      .filter((alternative) => (alternative.get('viewId') === e.target.getAttribute('id')));
      
        // if alternativesSelected.size is equal to zero, that means
        // this alternative is not selected
      if (!this.state.isLiveEditing) {
        if (altsSelected.size === 0) {
          this.props.selectAlternative(e.target.getAttribute('id'));

          if (this.state.isCloning === true) {
            this.cloneBases.push(e.target.getAttribute('id'));   
            this.props.cloneEditAlternatives([e.target.getAttribute('id')], [this.props.collection.get('viewId')])
          }
        } else {
          this.props.deselectAlternative(e.target.getAttribute('id'));
        }
      } else {
        if (altsSelected.size === 0) {
          this.props.deselectAllAlternatives();
          this.props.selectAlternative(e.target.getAttribute('id'));
        } else {
          this.props.deselectAlternative(e.target.getAttribute('id'));
        }
      }
  }

  /**
   * event handler for adding/removing parameters/dimensions to the chart
   * @param {object} options
   * @param {object} action
   */
  toggleParameter = (options, {action, removedValue}) => {
    const newValueBands = Object.assign({},this.state.valueBands);
    const currentDimensions = Object.assign([], this.state.dimensions)
    let index;
    this.defaultOptions = [];

    switch (action) {
    case 'remove-value':
      console.log("in remove value")
      this.props.alternatives.get('alternatives').filter((alt) => 
        this.props.collection.get('altsid').contains(alt.get('viewId'))).map((alt) => {
        this.props.removeDataField(alt, removedValue.value);
      });

      index = currentDimensions.indexOf(removedValue.value);
      currentDimensions.splice(index, 1);

      this.setState({dimensions: currentDimensions, valueBands: removeFieldFromDatum(newValueBands, removedValue.value)});
      currentDimensions.map((d) => this.defaultOptions.push({value: d, label: d.toString()}));
      this.props.orderCollectionDimensions(this.props.collection.get('viewId'), currentDimensions);
      if (this.state.isLocked) {
        this.props.lockAspectRatio(currentDimensions);
      }
      break;
    case 'select-option':
      this.props.alternatives.get('alternatives').filter((alt) =>
        this.props.collection.get('altsid').contains(alt.get('viewId'))).map((alt) => {
        this.props.addDataField(alt, options[options.length-1]['label']);
      });

      currentDimensions.push(options[options.length-1]['label']);

      this.setState({
        dimensions: currentDimensions,
        valueBands: addFieldToDatum(
          newValueBands, options[options.length-1]['label'],
          this.state.backupValueBands),
      });
      currentDimensions.map((d) => this.defaultOptions.push({value: d, label: d.toString()}));
      this.props.orderCollectionDimensions(this.props.collection.get('viewId'), currentDimensions);
      if(this.state.isLocked)
      {
        this.props.lockAspectRatio(currentDimensions);
      }
      break;
    default:
      console.log('no valid selection');
    }
  }


