  handleConfirmQuery = (committedQueries) => {
      if (committedQueries.length > 0) {
        if (this.props.collection.get('destinations').size === 0) {
          for (let i = 0; i < committedQueries.length; i++) {

            (function (query, index, operation, name) {
              setTimeout(function () {
                operation(0 + (index * 2), 5, 3, 5, query, name + '-query' + (index + 1));
              }, 3500 * i);
            })(committedQueries[i], i, this.props.createNewCollection, this.props.collection.get('collectionName'));
           
            this.props.addQuery(this.props.collection.get('collectionName') + '-query' + (i + 1), this.props.collection.get('collectionName'), 'someString', committedQueries[i])
            
          }
          toast.info('Please wait while queries are being committed...', { autoClose: 3500 * committedQueries.length, position: 'bottom-center', rtl: false })
          
        } else {
          for (let j = 0; j < this.props.collection.get('destinations').size; j++) {
            if (this.props.collection.get('destinations').toJS()[j]['__isNew__']) {
              for (let i = 0; i < committedQueries.length; i++) {
                (function (query, index, operation, name) {
                  setTimeout(function () {
                    operation(0 + (index * 2), 5, 2, 4, query, name);
                  }, 3500 * i);
                })(committedQueries[i], i, this.props.createNewCollection, this.props.collection.get('destinations').toJS()[j]['label']);
              }
            } else {
              for (let i = 0; i < committedQueries.length; i++) {
                (function (query, destination, operation) {
                  setTimeout(function () {
                    operation(query, [destination]);
                  }, 3500 * i);
                })(committedQueries[i], this.props.collection.get('destinations').toJS()[j]['value'], this.props.cloneAlternatives);

                this.props.addQuery(this.props.collection.get('collectionName') + '-query' + (i + 1), this.props.collection.get('collectionName'), 'someString', committedQueries[i])
              }
            }
          }
          toast.info('Please wait while queries are being committed...', { autoClose: 3000 * committedQueries.length * this.props.collection.get('destinations').size, position: 'bottom-center', rtl: false })
        }
      }

      this.setState({
        shouldWarn: false,
        warningMessage: '',
        confirmAction: null,
      })
  }

  handleBrushAxisClick = () => {
    const queries = Object.assign([], this.committedQueries);
    this.setState({shouldWarn: false, warningMessage: ''});
    this.toggleBrushMode();

    if (this.state.isBrushing) {
      this.props.infoRef.current.childNodes[0].childNodes[1].style.display = 'inline';
      this.props.infoRef.current.childNodes[0].childNodes[2].style.display = 'none';
      if (queries.length > 0) {
        this.setState({shouldWarn: true,  warningMessage: 'Do you want to commit these queries?', confirmAction: this.handleConfirmQuery.bind(this, queries) });
      }
    } else {
      this.props.infoRef.current.childNodes[0].childNodes[1].style.display = 'none';
      this.props.infoRef.current.childNodes[0].childNodes[2].style.display = 'inline';
    }
  }

