import React, {PureComponent} from 'react';
import {MdClose} from 'react-icons/md';
import {ToastContainer} from 'react-toastify';
import styles from './notBar.module.scss';
import 'react-toastify/dist/ReactToastify.css';

/**
 * @class NotBar
 * @extends React.PureComponent
 * @description this is the presentational component for NotBar, which handles the toast notifications
 */
class NotBar extends PureComponent {

  /**
   * @function hideHistoryPane
   * @memberof NotBar
   * @description hides the history pane upon click
   * @param {event} e 
   */
  hideHistoryPane = (e) => {
    e.stopPropagation();
    this.props.toggleHistory();
  }

  /**
  * @returns {ReactPureComponent}
  */
  render() {
  const errors = this.props.notifications.get('errors');
  const warnings = this.props.notifications.get('warnings');
  const successes = this.props.notifications.get('successes');

  const errorMessages = errors.map((error) => {
  return (
    <tr key={error.get('id')}>
      <td>
        {error.get('message')}
      </td>
    </tr>
  );
 });

 const warningMessages = warnings.map((warning) => {
  return (
    <tr key={warning.get('id')}>
      <td>
        {warning.get('message')}
      </td>
    </tr>
  );
 });

 const successMessages = successes.map((success) => {
  return (
    <tr key={success.get('id')}>
      <td>
        {success.get('message')}
      </td>
    </tr>
  );
 });

    return (
      <div>
        <ToastContainer
          className={styles['toast-container']}
          toastClassName={styles['each-toast']}
          position={'bottom-right'}
          draggable={false}
          closeButton={false}
          autoClose={3000}
          rtl={true}
        />
        <div> {this.props.notifications.get('showHistory') ? 
          <div className={styles['history-pane']} onClick={this.hideHistoryPane}>
            <span>
              <MdClose
                  className={styles['closeIcon']}
                  onClick={this.handleCloseClick}
                />
            </span>
            <div className={styles['success-pane']}> 
              <span className={styles['title-bar']}>{successes.size + ' messages'}</span><hr className={styles['hr-history-pane']}/> 
              <div>
                <table>
                  <tbody>
                  {successMessages}
                </tbody>
                </table>
              </div> 
            </div>
            <div className={styles['warning-pane']}> 
              <span className={styles['title-bar']}>{warnings.size + ' messages'}</span><hr className={styles['hr-history-pane']}/> 
              <div>
                <table>
                  <tbody>
                  {warningMessages}
                </tbody>
                </table>
              </div> 
            </div>
            <div className={styles['error-pane']}> 
              <span className={styles['title-bar']}>{errors.size + ' messages'}</span><hr className={styles['hr-history-pane']}/>
              <div>
                <table>
                  <tbody>
                    {errorMessages}
                  </tbody>
                </table>
              </div> 
            </div>
          </div> 
        : null} </div>
      </div>
    );
  }
}

export default NotBar;
