import React, { PureComponent } from "react";
import styles from './searchPanel.module.scss';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';


/**
 * @class SearchPanel
 * @extends React.PureComponent
 * @description this componet search through collections by name.
 */
class SearchPanel extends PureComponent {

    /**
     * @constructor
     * @param {object} props 
     */

    constructor(props){
        super(props)
        
        this.state = {
            searchOpen: true,
            searchText: "",
        }

    }

    /**
     * @function handleSearchClose
     * @memberof SearchPanel
     * @param {Object} e
     * @description hide the search panel 
     */
    handleSearchClose = (e) => {
        e.preventDefault();
        this.setState({ searchOpen: false})
        this.props.toggleSearch();
    }

    /**
     * @function handleSearchText
     * @description handle search text change
     * @memberof SearchPanel
     * @param {Object} e
     */
    handleSearchText = (e) => {
        this.setState({
            searchText: e.target.value,
        })
    }

    /**
     * @function handleSubmit
     * @memberof SearchPanel
     * @description hande search from submition
     * @param {object} e
     */
    handleSubmit = (e) => {
        e.preventDefault();
        
        if(this.state.searchText){
            this.props.handleSearch(this.state.searchText);
        }else{
            const empty = [];
            this.props.searchedCollectionFailed(empty);
        }
    }

    /**
     * @function removeValidation
     * @memberof SearchPanel
     * @deprecated sets vlidation to false
     */
    removeValidation = () => {
        if (this.props.search.get('showValidation')){
            this.props.searchedCollectionSuccess();
            this.props.emptyMissMatchArray();
        }
    }

    

    /**
     * @returns {ReactPureComponent}
     */
    render() {
        return (
            <div>
                <Dialog
                    className={styles['panel']}
                    open={this.state.searchOpen}
                    onClose={this.handleSearchClose}
                    aria-labelledby="search-dialog-title"
                >
                    <DialogTitle
                        id="search-dialog-title"
                    >
                        {"Collection Search"}
                    </DialogTitle>
                    <hr/>
                    <DialogContent className={styles['panel-content']}>
                        <form onSubmit={this.handleSubmit} 
                            className={styles['panel-form']}
                        >
                            <TextField
                                className={styles['panel-textField']}
                                onClick={this.removeValidation}
                                id="outlined-full-width"
                                label="Search"
                                placeholder="Enter Collection name"
                                variant="outlined"
                                onChange={this.handleSearchText}
                                value={this.state.searchText}
                                error={this.props.search.get('showValidation')}
                                helperText={
                                    this.props.search.get('showValidation') ? "Can't find collection " + this.props.search.get("missMatchCollectionNames").toString() : null}
                            >
                            </TextField>
                            <div
                             className={styles["panel-buttons-container"]}
                            >
                                <Button 
                                    type="submit" 
                                    color="primary"
                                    className={styles['panel-button']}>
                                    Search
                                </Button>
                                <Button
                                    onClick={this.handleSearchClose} 
                                    color="primary"
                                    className={styles['action-button']}
                                >
                                    Close
                                </Button>
                            </div>
                        </form>
                    </DialogContent>
                </Dialog>
            </div>
        );
    }
}

export default SearchPanel;