import {urls} from '../constants/urls';
import appConstants from '../constants/appConstants';
import { 
    userFetch,
    userFetchFailed,
    logoutUser,
} from "./updateActions";
import {

    errorMessage,
  } from './notificationActions';
import {toast} from 'react-toastify';
import {batch} from 'react-redux';
import { dispatch } from 'd3';

const time = new Date().getHours() + ':' + new Date().getMinutes() + ':' + new Date().getSeconds();

/**
 * @function register
 * @description register a new user
 * @memberof userActions
 * @returns {Promise}
 */
export const register = (username,password,  passwordConfirmation) => (dispatch) => {
    fetch(urls.get('user_register'), {
        method: "POST",
        headers: {'Content-Type': 'application/json'},
        body: JSON.stringify({
          username: username,
          password: password
        })        
    }).then((response) => response.json())
    .then((response)=> {
        if (response.success) {
            if (response.user && response.user.id) {
                localStorage.setItem(appConstants.get("USER_ID"),response.user.id);
                batch(() => {
                    dispatch(userFetch(response.user));
                });  
            }
        }else{
            batch(() => {
                dispatch(userFetchFailed());
                dispatch(errorMessage(toast.error('Register failed'), response.message + " " + time));
            });       
        }
    }).catch((err) => {
        batch(() => {
            dispatch(userFetchFailed());
            dispatch(errorMessage(toast.error('Login failed'), 'Login was not successful ' + time));
        });   
    })
}

/**
 * 
 * @param {String} username 
 * @param {String} password 
 */
export const login = (username, password, history) => (dispatch) =>{
    fetch(urls.get('user_login'), {
        method: "POST",
        headers: {'Content-Type': 'application/json'},
        body: JSON.stringify({
            username:username,
            password:password
        })
    }).then((response) => response.json())
    .then((response) => {
        if (response.success) {
            if (response.user && response.user.id) {
                localStorage.setItem(appConstants.get("USER_ID"),response.user.id);
                batch(() => {
                  dispatch(userFetch(response.user));
                  history.push("/projects");
                }); 
              }        
        }else {
            batch(() => {        
                dispatch(userFetchFailed());
                dispatch(errorMessage(toast.error('Login failed'), response.message + " " + time));
              }); 
        }
    }).catch((err) => {
        batch(() => {
            dispatch(userFetchFailed());
            dispatch(errorMessage(toast.error('Login failed'), 'Login was not successfull ' + time));
          });     
    })
}


/**
 * @function logout
 * @description log out of the account
 * @memberof userActions
 * 
 */
export const logout = () => (dispatch) => {
    localStorage.removeItem(appConstants.get("USER_ID"));
    sessionStorage.removeItem(appConstants.get("PROJECT_ID"));
    dispatch(logoutUser());
}

/**
 * 
 * @param {String} id 
 */
export const fetchUser = () => (dispatch) => {
    fetch(urls.get('user_fetch'))
    .then((response)=> response.json())
    .then((response) => {
        if (response.success) {
            if (response.user && response.user.id) {
                localStorage.setItem(appConstants.get("USER_ID"),response.user.id);
                batch(() => {
                  dispatch(userFetch(response.user));
                }); 
              }        
        }else {
            batch(() => {        
                dispatch(userFetchFailed());
                dispatch(errorMessage(toast.error('Login failed'), response.message + " " + time));
              }); 
        }
    }).catch((err) => {
        batch(() => {
            dispatch(userFetchFailed());
            dispatch(errorMessage(toast.error('Login failed'), 'Login was not successfull ' + time));
          });     
    })
}





