import React, {PureComponent} from 'react';
import styles from './header.module.scss';
import SinkList from '../sinkList/SinkList';
import {
  layoutUpdate,
} from '../../actions/collectionActions';

import {
  MdVerticalAlignBottom,
  MdSettings,
  MdSearch,
  MdHelpOutline,
  MdMailOutline,
  MdRoundedCorner
} from 'react-icons/md';
import * as d3 from 'd3';


// TODO: make it functional component

/**
 * @class Header     
 * @extends React.PureComponent
 * @description this is the presentational component of header (context) menu
 * @component 
 */
class Header extends PureComponent { 

  /**
   * @function handleSinkClick
   * @description when clicked, this function toggles the collection sink
   * @memberof Header
   * @param {event} e 
   */
  handleSinkClick = (e) => {
    e.stopPropagation();

    if (this.props.header.get('listShow')) {
      this.props.hideSinkList();
    } else {
      this.props.showSinkList();
    }
  }

  /**
   * @function handleHistoryClick
   * @description when clicked, this function toggle's the history page
   * @memberof Header
   * @param {event} e 
   */
  handleHistoryClick = (e) => {
    e.stopPropagation();
    this.props.toggleHistory();
  }

  /**
   * @function handleUnselectAllClick
   * @description when clicked, this function unselects all items (alternatives, collections etc.)
   * @memberof Header
   * @param {event} e 
   */
  handleUnselectAllClick = (e) => {
    e.stopPropagation();
    this.props.deselectAllCollections();
    this.props.deselectAllAlternatives();
    this.props.hideHeader();

    d3.selectAll(".alt")
      .style('border', null)
      .style('border-style', null)
      .style('border-color', null)
      .style('border-radius', null)
      .style('-webkit-box-shadow', null);

    d3.selectAll(".pc")
      .style('stroke', styles.colorUnselected)
      .style('opacity', 0.3);

    d3.selectAll(".tableList")
      .style('background-color', '#ffffff');
  }

  /**
   * @function handleSettingsClick
   * @description when clicked, this function toggles the settings pane
   * @memberof Header
   * @param {event} e 
   */
  handleSettingsClick = (e) => {
    e.stopPropagation();
    this.props.toggleSettings();
  }

  /**
   * @function handleSearchClick
   * @description when clicked, this function toggoles the search pane
   * @memberof Headr
   * @param {event} e
   */
  handleSearchClick = (e) => {
    e.stopPropagation();
    this.props.toggleSearch();
  }

  handleHelpClick = (e) => {
    e.stopPropagation();
    const newWindow = window.open("https://dstar-design-gallery.github.io/dstar-docs/" , '_blank', "docs")
  }

  handleGatherAll = (e) => {
    try {
      this.props.gatherAllCollections();
    } catch (err) {
      console.log(err)
    } finally {
      layoutUpdate(this.props.layouts.get('gridLayout').toJS());
    }
  }

  /**
   * The component should render the view for the header menu
   * @returns {ReactPureComponent}
   */
  render() {
    const positionOfContextMenu = {
      'position': 'absolute',
      'top': this.props.header.get('headerY'),
      'left': this.props.header.get('headerX'),
      'zIndex': 3,
    };

    const backgroundColorStyle = {backgroundColor: 'white'};

    return (
      <div style={positionOfContextMenu}>
        <input className={styles['right-click-menu']} id="right-click-menu" />
        <label htmlFor="right-click-menu" />
        <img style={backgroundColorStyle} src={require('../../resources/DStar.png')} alt="DStar-logo" onClick={this.handleGatherAll}/>
        <ul>
          <li title="Unselect All" className={styles['right-click-icon']} onClick = {this.handleUnselectAllClick}> <a style={backgroundColorStyle} ><MdRoundedCorner/></a> </li>
          <li title="Show History" className={styles['right-click-icon']} onClick = {this.handleHistoryClick}> <a style={backgroundColorStyle} ><MdMailOutline/></a> </li>
          <li title="Show Sink" className={styles['right-click-icon']} onClick = {this.handleSinkClick}> <a style={backgroundColorStyle} ><MdVerticalAlignBottom/></a> </li>
          <li title="Settings" className={styles['right-click-icon']} onClick = {this.handleSettingsClick}> <a style={backgroundColorStyle} ><MdSettings/></a></li>
          <li title="Search" className={styles['right-click-icon']} onClick={this.handleSearchClick} >  <a style={backgroundColorStyle} ><MdSearch/></a> </li>
          <li title="Help" className={styles['right-click-icon']} onClick = {this.handleHelpClick}> <a style={backgroundColorStyle} ><MdHelpOutline/></a></li>
          <div>{this.props.header.get('listShow') ? <SinkList {...this.props} /> : null}</div>
        </ul>
      </div>
    );
  }
}

export default Header;
