import React, { PureComponent } from 'react';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Dropdown from 'react-dropdown';
import 'react-dropdown/style.css';


/**
 * @class ProjectBox
 * @extends React.PureComponent
 * @description presentational component for the projectBox pane, which is a popup window
 */
class ProjectBox extends PureComponent {

  /**
   * @constructor
   * @param {object} props 
   */
  constructor(props) {
      super(props)

      this.state = {
          open: true
      }  

      this.selectedProject = {};  
      this.newProjectName = "";
      this.newProjectDescription = "";  
  }

  
  /**
   * @function handleCreate
   * @memberof projectBox
   * @description creating a new project 
   */
  handleCreate = () => {
    if (this.newProjectName !== "") {
      this.props.createProject(this.newProjectName,this.newProjectDescription);
      this.props.history.push("/project");
    }    
  };

  handleCancel = () => {
    this.props.isCreatingProject();
  };

  onProjectNameChange = (e) =>{
    this.newProjectName = e.target.value;
  }

  onProjectDescriptionChange = (e) =>{
    this.newProjectDescription = e.target.value;
  }

  /**
   * @description renders the view for projectBox component
   * @returns {ReactPureComponent}
   */
  render() {
      return (
          <div>
            <Dialog
              open={this.props.isCreating}
              onClose={this.handleClose}
              aria-labelledby="alert-dialog-title"
              aria-describedby="alert-dialog-description"
            >
              <DialogTitle id="alert-dialog-title"  style={{color: "black"}}>{"Projects"}</DialogTitle>
              <DialogContent>
                  <DialogContentText 
                    id="alert-dialog-description"
                    style={{color: "black"}}
                  >
                    Enter a name and description for the new project.
                  </DialogContentText>
                  <TextField
                    autoFocus
                    margin="dense"
                    id="project-name"
                    label="project name"
                    onChange={this.onProjectNameChange}
                    type="text"
                    fullWidth
                    InputLabelProps={{
                      style: { color: '#000' },
                    }}
                  />
                  <TextField
                    autoFocus
                    margin="dense"
                    id="project-description"
                    label="project description"
                    onChange={this.onProjectDescriptionChange}
                    type="text"
                    fullWidth
                    InputLabelProps={{
                      style: { color: '#000' },
                    }}
                  />
                <DialogActions>
                  <Button onClick={this.handleCancel} color="primary" >
                    Cancel
                  </Button>
                  <Button onClick={this.handleCreate} color="primary" autoFocus>
                    create
                  </Button>
                </DialogActions>
              </DialogContent>
            </Dialog>
          </div>
        );
  }
}

export default ProjectBox;