import appConstants from '../constants/appConstants';
import Immutable from 'immutable';

const initialState = Immutable.fromJS({
    showSearch: false,
    showValidation: false,
    missMatchCollectionNames: [],
})

export default (state = initialState, action = {}) => {
    switch(action.type){
        case appConstants.get("FETCHING_PROJECT"):
            return state
                    .set('showSearch', false)
                    .set('showValidation', false)
                    .set(' missMatchCollectionNames', Immutable.fromJS([]))
        case appConstants.get('TOGGLE_SEARCH'):
            return state.set("showSearch", !state.get("showSearch")).set("showValidation", false);

        case appConstants.get("SEARCH_COLLECTION_FALID"):
            return state.set("showValidation", true).set('missMatchCollectionNames', Immutable.fromJS(action.payload));

        case appConstants.get("EMPTY_MISS_MATCH_NAME_ARRAY_SEARCH"):
            return state.set("missMatchCollectionNames", []);

        case appConstants.get("SEARCHED_COLLECTION"):
            return state.set("showValidation", false);

        default:
            return state;
    }
};