import Projects from "../components/projects/Projects";

export const doesProjectExists = (projects, name) => {
    let exists = false;
    let filterProject  = projects.filter((pro) => pro.get("name") === name);
    if(filterProject.size > 0){
        exists = true
    }else{
        exists = false
    }
    // console.log(exists)
    return exists;    
}