/**
 * Action dispatchers for operations on alternative objects
 * @namespace alternativeActions
 */

import {urls} from '../constants/urls';
import {
  findCollectionsFromID,
  fetchCollectionsNotID,
} from '../util/collections';
import {
  fetchAlternativesNotID,
  findAlternativesFromID,
  findAlternativeRelativesFromID,
} from '../util/alternatives';
import {
  shapeAlternative,
  shapeCollection,
} from '../util/util';
import {
  collectionsFetch,
  selectAlternative,
  orderAlternatives,
  hoverAlternative,
  dehoverAllAlternative,
  addAlternative,
  addAlternativeToCollection,
  deselectAlternative,
  setLoadingState,
  removeAlternatives
} from './updateActions';
import {
  successMessage,
  warningMessage,
  errorMessage,
} from './notificationActions';
import {toast} from 'react-toastify';
import {batch} from 'react-redux'

const time = new Date().getHours() + ':' + new Date().getMinutes() + ':' + new Date().getSeconds();

/**
 * @function createNewAlternative
 * @description creates a new alternative from a given set of base-alternatives and an object of sketch points
 * @memberof alternativeActions
 * @param {array} viewIds - a set of base-alternatives
 * @param {object} sketchData - an object of sketch points
 * @returns {redux-thunk} middleware - fetch call to update the database and then the redux-store
 */
export const createNewAlternative = (viewIds, sketchData) => (dispatch) => {
    return fetch(urls.get('alternative_sketching'), {
      method: 'POST',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify({
        ids: viewIds.toJS(),
        values: sketchData
      })
    }).then(response => response.json())
      .then((data) => {
        //TODO: check if success!
        dispatch(successMessage(toast.success('New alternative created'), 'new alternative created' + time));
      }).catch((err) => {
        console.log(err);
        dispatch(errorMessage(toast.error('New alternative creation failed'), 'new alternative creation failed' + time));
      });
};

/**
 * @function restoreAlternative
 * @description initiates a *round-trip* for a given alternative
 * @memberof alternativeActions
 * @param {string} viewId - viewId of the alternative
 * @returns {redux-thunk} middleware - fetch call to update the database and then the redux-store
 */
export const restoreAlternative = (viewId) => () => {
    return fetch(urls.get('alternativeRestore'), {
      method: 'POST',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify({viewId : viewId})
    }).then(response => response.json())
      .then((data) => {
        return data;
    }).catch((err) => {
        console.log(err);
    });
};

/**
 * @function cutAlternativesFromCollection
 * @description dispatches an action to cut an alternative from the collection
 * @memberof alternativeActions
 * @param {string} altID - viewId of the alternative
 * @returns {void}
 */
export const cutAlternativesFromCollection = (altID) => (dispatch, getState) => {
    try {
      dispatch(deselectAlternative(altID));
      const collections = getState().collections.get('collections');
      const alternatives = getState().alternatives.get('alternatives');
      const alternative = findAlternativesFromID(alternatives, altID); 
      const otherCollections = fetchCollectionsNotID(collections, alternative.get("collectionViewId"));
      const collection = findCollectionsFromID(collections, alternative.get("collectionViewId"));
      const oldAlts = collection.get('altsid');
      const filteredAlts = fetchAlternativesNotID(oldAlts, altID);
      const updatedCollection = collection.set('altsid', filteredAlts);
      const newCollections = otherCollections.push(updatedCollection);
      dispatch(collectionsFetch(newCollections));
    } catch (e) {
      console.error(e.message);
    }
  };


/**
 * @function removeAlternativesFromCollection
 * @description removes a list of alternatives from a collection
 * @memberof alternativeActions
 * @param {ImmutableList} alts - an immutable list of alternatives
 * @returns {redux-thunk} middleware - fetch call to update the database and then the redux-store
 */
export const removeAlternativesFromCollection = (alts) => (dispatch, getState) => {
  if (alts.size > 0) {
    const refIds = [];
    const collectionRefId = [];
  
    alts.map((alt) => {
      refIds.push(alt.get('refId'));
      collectionRefId.push(alt.get('collectionRefId'));
    });
    
    return fetch(urls.get('alternativesRemove'), {
      method: 'delete',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify({refIds:refIds , collectionRefId:collectionRefId}),
    }).then(response => response.json())
    .then((data) => {
      data.map((alt) => {
        batch(() => {
          dispatch(deselectAlternative(alt)); 
          dispatch(removeAlternatives(alt));
          dispatch(cutAlternativesFromCollection(alt));
        })
      });
      dispatch(successMessage(toast.success('Alternative(s) removed'), 'alternative(s) removed ' + time));
    }).catch((err) => {
      console.log(err);
      dispatch(errorMessage(toast.error('Alternative(s) not removed'), 'alternative(s) not removed ' + time));
    });
  } else {
    dispatch(warningMessage(toast.warning('please select an alternative'), 'please select an alternative' + time));
  }
 
}

/**
 * @function refAlternativesToCollection
 * @description refs alternative(s) to collection(s)
 * @memberof alternativeActions
 * @param {array} alts - an array of alternative viewIDs to ref
 * @param {array} cols - an array of collection viewIDs as destination
 * @returns {redux-thunk} middleware - fetch call to update the database and then the redux-store
 */
export const refAlternativesToCollection = (alts, cols) => (dispatch, getState) => {
    if (alts.length > 0 && cols.length > 0) {
      const toastyVar =  toast.info("Copying Alts To collection", {
        toastId: "CopyAltsWithRef",
        autoClose: false,
        closeOnClick: false,
        pauseOnHover: false,
        progress: 0.8,
        position: 'bottom-center',
        rtl: true,
      });
      return fetch(urls.get('alternativeCopy'), {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          source: alts,
          destination: cols,
        })
      }).then(response => response.json())
        .then((data) => {
          
          toast.update(toastyVar, {progress: 0.5});

          data.forEach((datum) => {

            const inputs = getState().alternatives.get('alternatives').toJS()[0]['params'];
            const outputs = getState().alternatives.get('alternatives').toJS()[0]['outputs'];

            const newCollection = shapeCollection(datum.collection, Object.keys(inputs), Object.keys(outputs));

            datum.alts.map((alternative) => {
              dispatch(addAlternative(shapeAlternative(alternative, inputs, outputs)));
            });

            datum.alts.map((alt, index) => {
              batch(() => {
                // TODO: fix collection inputs/outputs/dimOrder
                dispatch(addAlternativeToCollection(datum.collectionId, alt.viewId, newCollection.inputs, newCollection.outputs, newCollection.dimOrder));
                dispatch(deselectAlternative(alts[index]));
                dispatch(hoverAlternativeSpecial(alt.viewId));
              });
            });
          });

          toast.update(toastyVar, {progress: 0, closeOnClick: true, autoClose: 1});
          
          dispatch(successMessage(toast.success('Alternative(s) copied'), 'alternative(s) copied ' + time));
      }).catch((err) => {
        
        toast.update(toastyVar, {progress: 0, closeOnClick: true, autoClose: 1});

        dispatch(errorMessage(toast.error('Alternative(s) not copied'), 'alternative(s) not copied ' + time));
      })
    } else {
      dispatch(warningMessage(toast.warning('please select an alternative and a collection'), 'please select an alternative and a collection' + time));
    }
  };
    
 

/**
 * @function cloneAlternativesToCollection
 * @description clone alternative(s) to collection(s)
 * @memberof alternativeActions
 * @param {array} alts - an array of alternative viewIDs to clone
 * @param {array} cols - an array of collection viewIDs as destination
 * @returns {redux-thunk} middleware - fetch call to update the database and then the redux-store
 */
export const cloneAlternativesToCollection = (alts, cols) => (dispatch, getState) => {

  if (alts.length > 0 && cols.length > 0) {
    const toastyVar =  toast.info("Cloning alts to collection", {
      toastId: "CloneAltsToCollection",
      autoClose: false,
      closeOnClick: false,
      pauseOnHover: false,
      progress: 0.8,
      position: 'bottom-center',
      rtl: true,
    });

    return fetch(urls.get('alternativeClone'), {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        source: alts,
        destination: cols,
      }),
    }).then(response => response.json())
      .then((data) => {
        
        toast.update(toastyVar, {progress: 0.5});

        data.forEach((datum) => {

          const inputs = getState().alternatives.get('alternatives').toJS()[0]['params'];
          const outputs = getState().alternatives.get('alternatives').toJS()[0]['outputs'];

          const newCollection = shapeCollection(datum.collection, Object.keys(inputs), Object.keys(outputs));

          datum.alts.map((alternative) => {
            dispatch(addAlternative(shapeAlternative(alternative, inputs, outputs)))
          });

          datum.alts.map((alt, index) => {
            batch(() => {
              // TODO: fix collection inputs/outputs/dimOrder
              dispatch(addAlternativeToCollection(datum.collectionId, alt.viewId, newCollection.inputs, newCollection.outputs, newCollection.dimOrder));
              dispatch(deselectAlternative(alts[index]));
              dispatch(hoverAlternativeSpecial(alt.viewId));
            });
          });
        });
        toast.update(toastyVar, {
          progress: 0,
          closeOnClick: true,
          autoClose: 1,
        })
        dispatch(successMessage(toast.success('Alternative(s) cloned'), 'alternative(s) cloned ' + time));
    }).catch((err) => {
      toast.update(toastyVar, {
        progress: 0,
        closeOnClick: true,
        autoClose: 1,
      })
      dispatch(errorMessage(toast.success('Alternative(s) not cloned'), 'alternative(s) not cloned ' + time));
    })
  } else {
    dispatch(warningMessage(toast.warning('Please select an alternative and a collection'), 'please select an alternative and a collection' + time));
  }
};

/**
 * @function cloneEditAlternativesToCollection
 * @description clones alternative(s) to collection(s) and selects them to edit
 * @memberof alternativeActions
 * @param {array} alts - an array of alternative viewIDs to ref
 * @param {array} cols - an array of collection viewIDs as destination
 * @returns {redux-thunk} middleware - fetch call to update the database and then the redux-store
 */
export const cloneEditAlternativesToCollection = (alts, cols) => (dispatch, getState) => {
  return fetch(urls.get('alternativeCloneEdit'), {
    method: 'POST',
    headers: {'Content-Type': 'application/json'},
    body: JSON.stringify({
      source: alts,
      destination: cols,
    }),
  }).then(response => response.json())
    .then((data) => {
      data.forEach((datum) => {

        const inputs = getState().alternatives.get('alternatives').toJS()[0]['params'];
        const outputs = getState().alternatives.get('alternatives').toJS()[0]['outputs'];

        const newCollection = shapeCollection(datum.collection, Object.keys(inputs), Object.keys(outputs));

        datum.alts.map((alternative) => {
          dispatch(addAlternative(shapeAlternative(alternative, inputs, outputs)))
        });
        datum.alts.map((alt, index) => {
          batch(() => {

            // TODO: fix collection inputs/outputs/dimOrder
            dispatch(addAlternativeToCollection(datum.collectionId, alt.viewId, newCollection.inputs, newCollection.outputs, newCollection.dimOrder));
            dispatch(deselectAlternative(alts[index]));
            dispatch(selectAlternative(alt.viewId));
          });
        });
      });
        dispatch(successMessage(toast.success('Alternative cloned'), 'alternative cloned ' + time));
  }).catch((err) => {
    console.log(err);
    dispatch(errorMessage(toast.success('Alternative not cloned'), 'alternative not cloned ' + time));
  })

} 

/**
 * @function editAlternatives
 * @description edits alternatives
 * @memberof alternativeActions
 * @param {object} editedAlts - an object composed of alternative viewIDs and edited params
 * @returns {redux-thunk} middleware - fetch call to update the database and then the redux-store
 */
export const editAlternatives = (editedAlts) => (dispatch, getState) => {
  Object.keys(editedAlts).map((key) => {
    dispatch(setLoadingState(key, true));
    findAlternativeRelativesFromID(getState().alternatives.get('alternatives'), key)
      .map((alt) => {
        dispatch(setLoadingState(alt.get('viewId'), true))
      })
  });

  return fetch(urls.get('alternativeEdit'), {
      method: 'POST',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify(editedAlts),
    }).then(response => response.json())
      .then((data) => {

      if(data.success === true) {
        dispatch(successMessage(toast.success('Alternative(s) edited'), 'alternative(s) edited ' + time));
      } else {
        dispatch(warningMessage(toast.success('Edit failed'), 'edit failed ' + time));
      }
    }).catch((err) => {
      console.log(err);
      dispatch(errorMessage(toast.success('Alternative(s) not edited'), 'alternative(s) not edited ' + time));
    })
  };

/**
 * @function selectAlternatives
 * @description selects and orders the alternative with the given viewId
 * @memberof alternativeActions
 * @param {string} viewId - viewID of the alternative
 * @returns {void}
 */
export const selectAlternatives = (viewId) => (dispatch) => {
  dispatch(selectAlternative(viewId));
  dispatch(orderAlternatives(viewId));
};

/**
 * @function hoverAlternative
 * @description hovers and orders the alternative with the given viewId
 * @memberof alternativeActions
 * @param {string} viewId - viewID of the alternative
 * @returns {void}
 */
export const hoverAlternatives = (viewId) => (dispatch) => {
  dispatch(dehoverAllAlternative());
  dispatch(hoverAlternative(viewId));
  dispatch(orderAlternatives(viewId));
};

/**
 * @function hoverAlternativeSpecial
 * @description hovers and orders the alternative with the given viewId (special: doesn't dehover the rest)
 * @memberof alternativeActions
 * @param {string} viewId - viewID of the alternative
 * @return {void}
 */
export const hoverAlternativeSpecial = (viewId) => (dispatch) => {
  dispatch(hoverAlternative(viewId));
  // dispatch(orderAlternatives(viewId));
};