import appConstants from '../constants/appConstants';
import {
  fetchQueriesNotID,
  fetchBrushesNotID,
} from '../util/queries';
import Immutable from 'immutable';

const initialState = Immutable.fromJS({
    queries: [],
    brushes: [],
});

export default (state = initialState, action={}) => {
  switch (action.type) {
    case appConstants.get("FETCHING_PROJECT"):
    return state
              .set('queries', Immutable.fromJS([]))
              .set('brushes',  Immutable.fromJS([]))
    case appConstants.get('ADD_QUERY'): 
      return state.update('queries', (queries) => queries.push(Immutable.fromJS(action.payload)));

    case appConstants.get('REMOVE_QUERY'): 
      return state.set('queries', fetchQueriesNotID(state.get('queries'), action.payload.queryId));
    
    case appConstants.get('CLEAR_ALL_QUERIES'): 
      return state.update('queries', (queries) => queries.clear());
    
    case appConstants.get('ADD_BRUSH'): 
      return state.update('brushes', (brushes) => brushes.push(Immutable.fromJS(action.payload)));

    case appConstants.get('REMOVE_BRUSH'): 
      return state.set('brushes', fetchBrushesNotID(state.get('brushes'), action.payload.queryId));
    
    case appConstants.get('CLEAR_ALL_BRUSHES'): 
      return state.update('brushes', (brushes) => brushes.clear());

  default:
    return state;
  }
};