import React, { PureComponent } from 'react';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import 'react-dropdown/style.css';


/**
 * @class ProjectBox
 * @extends React.PureComponent
 * @description presentational component for the projectBox pane, which is a popup window
 */
class EditProjectBox extends PureComponent {

  /**
   * @constructor
   * @param {object} props 
   */
  constructor(props) {
      super(props)

      this.state = {
          open: true
      }  

      this.selectedProject = {};  
      this.newProjectName = "";
      this.newProjectDescription = "";  
  }

  componentDidMount() {
      this.setState({
        newProjectName: this.props.project.get("name"),
        newProjectDescription: this.props.project.get("description")
      })
  }

  
  /**
   * @function handleCUpdate
   * @memberof editProjectBox
   * @description edit a new project 
   */
  handleUpdate = () => {
    if (this.state.newProjectName !== "") {
        const editElments = {
          projectName: this.state.newProjectName,
          projectDescription: this.state.newProjectDescription,
          projectId: this.props.project.get("id"),
        }
        this.props.editProject(editElments);
    }    
  };

  handleCancel = () => {
    this.props.openEditProject(null);
  };

  /**
   * @function onProjectNameChange
   * @param {Object } e 
   * @description handle name change amd updates newProjectName state
   */
  onProjectNameChange = (e) =>{
    this.setState({
        newProjectName: e.target.value
    }) 
}

  /**
   * @function  onProjectDescriptionChange 
   * @param {Object} e
   * @description handle description text change and updates newProjectDescription state 
   */
  onProjectDescriptionChange = (e) =>{
    this.setState({
        newProjectDescription: e.target.value
    }) 
  }

  /**
   * @description renders the view for projectBox component
   * @returns {ReactPureComponent}
   */
  render() {
      return (
          <div>
            <Dialog
              open={this.props.isCreating}
              onClose={this.handleClose}
              aria-labelledby="alert-dialog-title"
              aria-describedby="alert-dialog-description"
            >
              <DialogTitle id="alert-dialog-title"  style={{color: "black"}}>{"Projects"}</DialogTitle>
              <DialogContent>
                  <DialogContentText 
                    id="alert-dialog-description"
                    style={{color: "black"}}
                  >
                    Enter a name and description for the new project.
                  </DialogContentText>
                  <TextField
                    autoFocus
                    margin="dense"
                    id="project-name"
                    label="project name"
                    value={this.state.newProjectName}
                    onChange={this.onProjectNameChange}
                    type="text"
                    fullWidth
                    InputLabelProps={{
                      style: { color: '#000' },
                    }}
                  />
                  <TextField
                    autoFocus
                    margin="dense"
                    id="project-description"
                    label="project description"
                    onChange={this.onProjectDescriptionChange}
                    value={this.state.newProjectDescription}
                    type="text"
                    fullWidth
                    InputLabelProps={{
                      style: { color: '#000' },
                    }}
                  />
                <DialogActions>
                  <Button onClick={this.handleCancel} color="primary" >
                    Cancel
                  </Button>
                  <Button onClick={this.handleUpdate} color="primary" autoFocus>
                    Update
                  </Button>
                </DialogActions>
              </DialogContent>
            </Dialog>
          </div>
        );
  }
}

export default EditProjectBox;