import React, {PureComponent}  from 'react';
import styles from './auth.module.scss';
import Navigation from '../navigation';
import {toast} from 'react-toastify';
import NotificationContainer from '../../containers/NotificationContainer';




class Auth extends PureComponent {

    constructor(props) {
        super(props)
  
        this.state = {
            username: "",
            password: "",
            passwordConfirmation: "",
        }
    }

   

    componentDidUpdate(prevProps, prevState, snapshot) {
        if(!prevProps.auth.get("userFetched") && this.props.auth.get("userFetched")){
            this.props.history.push("/projects");
        }   
    }
    
    onUsernameChange = (e) =>{
        this.setState({
            username:  e.target.value,  
        });
      }
    
    onPasswordChange = (e) =>{
        this.setState({
            password: e.target.value,
        });
    }

    onPasswordConfirmationChange = (e) => {
        this.setState({
            passwordConfirmation: e.target.value
        });
    }

    handleRegister = (e) => {
        e.preventDefault();

        if (this.state.username !== "" && this.state.password !== "" &&  this.state.passwordConfirmation !== "" && (this.state.password === this.state.passwordConfirmation)) {
            this.props.register(this.state.username, this.state.password, this.state.passwordConfirmation);
        }else if(this.state.username === null || this.state.username === ""){
            this.props.errorMessage(toast.error("Name can't be empty"), " Name can't be empty " + 0)
        }else if(this.state.password === null || this.state.password === ""){
            this.props.errorMessage(toast.error("Password can't be empty"), " Password can't be empty " + 0)
        }else if(this.state.passwordConfirmation === null || this.state.passwordConfirmation === ""){
            this.props.errorMessage(toast.error("Password can't be empty"), " Password can't be empty " + 0)
        }else if(this.state.password !== this.state.passwordConfirmation){
            this.props.errorMessage(toast.error("Password does not match Password Confirmation"), "Password does not match Password Confirmation " + 0)
        }
    }

    handleLogoClicked = () => {
        this.props.history.push("/")
      }


    render(){
        return(
            <div className={styles["mainContainer"]}>
                <Navigation
                    clicked={this.handleLogoClicked} 
                />
                <main className={styles["content"]}>
                <NotificationContainer />
                    <div className={styles["titleContainer"]}>
                        <div className={styles["titleBox"]}>
                            <p className={styles["title"]}>
                                Join Now.
                            </p>
                        </div>
                    </div>
                    <div className={styles["authContainer"]}>
                        <div className={styles["signupBox"]}>
                            <form onSubmit={this.handleRegister}>
                                <div className={styles["inputContainer"]}>
                                    <p className={styles["inputStyle"]}>
                                        Name:
                                    </p>
                                    <input 
                                        type="text"
                                        name="name"
                                        onChange={this.onUsernameChange}
                                        value={this.state.username}
                                        className={styles["textInput"]}
                                        autoComplete="name"
                                        
                                    />
                                </div>
                                <div className={styles["inputContainer"]}>
                                    <p className={styles["inputStyle"]}>
                                        Password:
                                    </p>
                                    <input
                                       type="password"
                                       name="password"
                                       onChange={this.onPasswordChange}
                                       value={this.state.password}
                                       className={styles["textInput"]}
                                       autoComplete="new-password"
                                    />
                                </div>
                                <div className={styles["inputContainer"]}>
                                    <p className={styles["inputStyle"]}>
                                        Password Confirmation:
                                    </p>
                                    <input 
                                        type="password"
                                        name="password-confirmation"
                                        onChange={this.onPasswordConfirmationChange}
                                        className={styles["textInput"]}
                                        autoComplete="new-password"
                                    />
                                </div>

                                <div className={styles["buttonContainer"]}>
                                    <input
                                        type="submit" 
                                        value="Join Now"
                                        className={styles["submitInput"]}
                                    />
                                </div>
                            </form>
                        </div>
                    </div>
                </main>
            </div>
        )
    }
}

export default Auth;

