import React, { Component } from 'react';
import { connect } from 'react-redux';
import Parallel from '../components/parallel/Parallel';
import {
    newCartesianProduct,
    newParameterSpectra,
    newSensitivityAnalysis,
    newSpectralCartesianProduct,
    newSampling,
  } from '../actions/parallelActions';
  import {
    alterAlternative,
    addDataField,
    removeDataField,
    orderCollectionDimensions,
    setValueBands,
    addQuery,
    removeQuery,
    clearAllQueries,
    addBrush,
    removeBrush,
    clearAllBrushes,
  } from '../actions/updateActions';
  import {
    cloneEditAlternativesToCollection,
    createNewAlternative,
    editAlternatives,
  } from '../actions/alternativeActions';

class ParallelContainer extends Component {
    render() {
        return (
            <Parallel key={'parallelView-' + this.props.collection.get('viewId')} {...this.props} />
        );
    }
}

const mapStateToProps = (state) => ({
    scales: state.scales,
    queries: state.queries
  });
  
  const mapDispatchToProps = (dispatch) => ({
  
      alterAlternative: (alternativeID, parameter, newValue) => {
        dispatch(alterAlternative(alternativeID, parameter, newValue));
      },
  
      editAlternatives: (editedAlternatives) => {
        dispatch(editAlternatives(editedAlternatives));
      },
  
      cloneEditAlternatives: (alts = [], cols = []) => {
        dispatch(cloneEditAlternativesToCollection(alts, cols))
      },
  
      createNewAlternative: (alternativeID, newId, sketchData) => {
        dispatch(createNewAlternative(alternativeID, newId, sketchData));
      },
  
      newCartesianProduct: (alternativeIDs , keySets, destinations) => {
        dispatch(newCartesianProduct(alternativeIDs , keySets , destinations));
      },
  
      
      newParameterSpectra: (alternativeIDs , keySets, paramaterSpectraAltNum, paramaterSpectraDelta, destinations) => {
        dispatch(newParameterSpectra(alternativeIDs , keySets, paramaterSpectraAltNum, paramaterSpectraDelta, destinations));
      },
    
      newSensitivityAnalysis: (alternativeIDs, sensitivityAnalysisDelta, destinations) => {
        dispatch(newSensitivityAnalysis(alternativeIDs, sensitivityAnalysisDelta, destinations));
      },
    
      newSpectralCartesianProduct: (alternativeIDs, keySets , collectionID, destinations) => {
        dispatch(newSpectralCartesianProduct(alternativeIDs, keySets , collectionID, destinations));
      },
    
      newSampling: (altIds , brushes, destinations) => {
        dispatch(newSampling(altIds , brushes, destinations));
      },
    
      addDataField: (alternative, field) => {
        dispatch(addDataField(alternative, field));
      },
    
      removeDataField: (alternative, field) => {
        dispatch(removeDataField(alternative, field));
      },
    
      orderCollectionDimensions: (viewId, dimOrder) => {
        dispatch(orderCollectionDimensions(viewId, dimOrder));
      },
  
      setValueBands: (dim, end, val) => {
        dispatch(setValueBands(dim, end, val))
      },
    
      addQuery: (queryId, fromCollection, queryString, selections) => {
        dispatch(addQuery(queryId, fromCollection, queryString, selections))
      },
    
      removeQuery: (queryId) => {
        dispatch(removeQuery(queryId))
      },
    
      clearAllQueries: () => {
        dispatch(clearAllQueries())
      },
    
      addBrush: (brush, colId) => {
        dispatch(addBrush(brush, colId))
      },
    
      removeBrush: (brushId) => {
        dispatch(removeBrush(brushId))
      },
    
      clearAllBrushes: () => {
        dispatch(clearAllBrushes())
      },
  });

export default connect(mapStateToProps, mapDispatchToProps)(ParallelContainer);