import appConstants from '../constants/appConstants';
import Immutable from 'immutable';

const initialState = Immutable.fromJS({
  headerShow: false,
  listShow: false,
  headerX: 0,
  headerY: 0,
});

export default (state = initialState, action={}) => {
  switch (action.type) {
  case appConstants.get('SHOW_HEADER'):
    return state.set('headerShow', true)
      .set('headerX', action.headerX)
      .set('headerY', action.headerY);

  case appConstants.get('HIDE_HEADER'):
    return state.set('headerShow', false)
      .set('headerX', 0)
      .set('headerY', 0);

  case appConstants.get('SHOW_SINK_LIST'):
    return state.set('listShow', true);

  case appConstants.get('HIDE_SINK_LIST'):
    return state.set('listShow', false);

  default:
    return state;
  }
};
