import React, { PureComponent } from 'react';
import styles from './parallelMenu.module.scss';
import classnames from 'classnames';
import {IconContext} from 'react-icons';
import Dropdown from 'react-dropdown';
import 'react-dropdown/style.css';

import {
    MdCreate,
    MdSettings,
    MdLightbulbOutline,
    MdSearch,
    MdLockOpen,
    MdLockOutline,
} from 'react-icons/md';

import {
    Search,
    Edit,
    Generate,
    Settings,
    Brush,
    Pareto,
    Sketch,
    EditRef,
    EditClone,
    DirectEditRef,
    CartesianProduct,
    SpectralCartesianProduct,
    Sensitivity,
    Perturbation,
    ParameterSpectra,
    Coords,
} from '../../icons'


const colorCurrentCommand = styles.colorCurrentCommand;

class ParallelMenu extends PureComponent {

    constructor(props) {
        super(props);
        
        this.state = {
            showSearch:   false,
            showEdit:     false,
            showGenerate: false,
            showSettings: false,
            showUnlock:   true,
        }
    }

    handleClick = (group) => {

        switch (group) {
            case "search":
                this.setState({
                    showSearch: !this.state.showSearch,
                    showEdit: false,
                    showGenerate: false,
                    showSettings: false,
                });
            break;

            case "edit":
                this.setState({
                    showEdit: !this.state.showEdit,
                    showSearch: false,
                    showGenerate: false,
                    showSettings: false,
                });
            break;
            
            case "generate":
                this.setState({
                    showGenerate: !this.state.showGenerate,
                    showSearch: false,
                    showEdit: false,
                    showSettings: false,
                });
            break;
            
            case "settings":
                this.setState({
                    showSettings: !this.state.showSettings,
                    showSearch: false,
                    showGenerate: false,
                    showEdit: false,
                });
            break;
            case "unlock":
                this.setState({
                    showUnlock: false,
                })
            break;
            case "lock":
                this.setState({
                    showUnlock: true,
                })
            break;
            default:
            break;
        } 
    }

    handleUnlockclick = () => {
        this.props.lockAspectRatio(true);
        this.handleClick("unlock")
    }

    handleLockClick = () => {
        this.props.lockAspectRatio(false);
        this.handleClick("lock")
    }

    

    render() {

        const searchButtonToggle = classnames({
            [styles['button-visible']]: this.state.showSearch === true,
            [styles['button-hidden']]:  this.state.showSearch === false,
        });

        const editButtonToggle = classnames({
            [styles['button-visible']]: this.state.showEdit === true,
            [styles['button-hidden']]:  this.state.showEdit === false,
        });
        
        const generateButtonToggle = classnames({
            [styles['button-visible']]: this.state.showGenerate === true,
            [styles['button-hidden']]:  this.state.showGenerate === false,
        });

        const settingsButtonToggle = classnames({
            [styles['button-visible']]: this.state.showSettings === true,
            [styles['button-hidden']]:  this.state.showSettings === false,
        });

        const searchButtonStyle = (this.state.showSearch || this.props.isBrushing || this.props.isSampling ) ? {backgroundColor: colorCurrentCommand} : {};
        const editButtonStyle = (this.state.showEdit || this.props.isSketching || this.props.isAltering || this.props.isLiveEditing || this.props.isCloning)  ? {backgroundColor: colorCurrentCommand} : {};
        const generateButtonStyle = (this.state.showGenerate ||  this.props.isCPing || this.props.isPSing || this.props.isSAing ||  this.props.isSCPing) ? {backgroundColor: colorCurrentCommand} : {};
        const settingsButtonStyle = (this.state.showSettings || this.props.isToggling ) ? {backgroundColor: colorCurrentCommand} : {};
        const unlockStyle          = (this.state.showUnlock ) ? {backgroundColor: colorCurrentCommand} : {};
        const brushButtonStyle = this.props.isBrushing ? {backgroundColor: colorCurrentCommand} : {};
        const sketchButtonStyle = this.props.isSketching ? {backgroundColor: colorCurrentCommand} : {};
        const alterButtonStyle = this.props.isAltering ? {backgroundColor: colorCurrentCommand} : {};
        const sampleButtonStyle = this.props.isSampling ? {backgroundColor: colorCurrentCommand} : {};
        const liveEditButtonStyle = this.props.isLiveEditing ? {backgroundColor: colorCurrentCommand} : {};
        const cloneButtonStyle = this.props.isCloning ? {backgroundColor: colorCurrentCommand} : {};
        const toggleButtonStyle = this.props.isToggling ? {backgroundColor: colorCurrentCommand} : {};
        const cpButtonStyle = this.props.isCPing ? {backgroundColor: colorCurrentCommand} : {};
        const parameterSpectraButtonStyle = this.props.isPSing ? {backgroundColor: colorCurrentCommand} : {};
        const sensitivityAnalysisButtonStyle = this.props.isSAing ? {backgroundColor: colorCurrentCommand} : {};
        const spectralCartesiaProductButtonStyle = this.props.isSCPing ? {backgroundColor: colorCurrentCommand} : {};

        const options = [
            0,1,2,3,4,5,6
        ];
        const defaultOption = options[0];


        return (
            <div className={styles['button-div']}>
                <IconContext.Provider value={{size: '1.80em'}}>
                <MdSearch
                    title='search' 
                    className={styles['button']}
                    style={searchButtonStyle}
                    onClick={this.handleClick.bind(this, 'search')}
                />
                </IconContext.Provider>
                    <Brush
                        height={"12mm"}
                        width={"12mm"} 
                        title='brush axis' 
                        className={searchButtonToggle} 
                        style={brushButtonStyle} 
                        onClick={this.props.handleBrushAxisClick}/>
                    <Pareto
                        height={"12mm"}
                        width={"12mm"} 
                        title='sample axis' 
                        className={searchButtonToggle} 
                        style={sampleButtonStyle} 
                        onClick={this.props.handleSampleAxisClick}/>
                <IconContext.Provider value={{size: '1.80em'}}>
                <MdCreate
                    title='edit' 
                    className={styles['button']}
                    style={editButtonStyle}
                    onClick={this.handleClick.bind(this, 'edit')}
                />
                </IconContext.Provider>
                    <Sketch
                        height={"12mm"}
                        width={"12mm"} 
                        title='sketch alternative' 
                        className={editButtonToggle} 
                        style={sketchButtonStyle} 
                        onClick={this.props.handleSketchAlternativeClick}/> 
                    <EditRef
                        height={"12mm"}
                        width={"12mm"} 
                        title='edit alternative' 
                        className={editButtonToggle} 
                        style={alterButtonStyle} 
                        onClick={this.props.toggleAlterMode}/>
                    <EditClone
                        height={"12mm"}
                        width={"12mm"}
                        title='clone-edit alternative'
                        className={editButtonToggle} 
                        style={cloneButtonStyle} 
                        onClick={this.props.handleCloneEditAlternativeClick}/> 
                    <DirectEditRef
                        height={"12mm"}
                        width={"12mm"}
                        title='live-edit alternative' 
                        className={editButtonToggle} 
                        style={liveEditButtonStyle} 
                        onClick={this.props.toggleLiveEditMode}/>
                <IconContext.Provider value={{size: '1.80em'}}>
                <MdLightbulbOutline
                    title='generate' 
                    className={styles['button']}
                    style={generateButtonStyle}
                    onClick={this.handleClick.bind(this, 'generate')}
                />
                </IconContext.Provider>
                    <CartesianProduct
                        height={"12mm"}
                        width={"12mm"} 
                        title='cartesian product' 
                        className={generateButtonToggle} 
                        style={cpButtonStyle} 
                        onClick={this.props.handleCartesianProductClick}/>
                    <SpectralCartesianProduct
                        height={"12mm"}
                        width={"12mm"} 
                        title='spectral cartesian product' 
                        className={generateButtonToggle}
                        style={spectralCartesiaProductButtonStyle} 
                        onClick={this.props.handleSpectralCartesianProductClick}/>
                    <Perturbation
                        height={"12mm"}
                        width={"12mm"} 
                        title='perturbation' 
                        className={generateButtonToggle}
                        style={sensitivityAnalysisButtonStyle} 
                        onClick={this.props.handleSensitivityAnalysisClick}/>
                    {this.props.isSAing ?
                        <input type="text" onChange={this.props.onSensitivityAnalysisDeltaChange}  placeholder="delta"/>                         
                        : null
                    }
                    <ParameterSpectra
                        height={"12mm"}
                        width={"12mm"} 
                        title='parameter spectra/sensitivity analysis' 
                        className={generateButtonToggle}
                        style={parameterSpectraButtonStyle} 
                        onClick={this.props.handleParameterSpectraClick}/>
                    {this.props.isPSing ?  
                        <div>
                            <Dropdown options={options} onChange={this.props.onParameterSpectraSelect} value={defaultOption} placeholder="arity"/>
                            <input type="text" onChange={this.props.onParameterSpectraDeltaChange}  placeholder="delta"/> 
                        </div>
                        : null
                    }
                   
                <IconContext.Provider value={{size: '1.80em'}}>
                <MdSettings
                    title='settings' 
                    className={styles['button']}
                    style={settingsButtonStyle}
                    onClick={this.handleClick.bind(this, 'settings')}
                /> 
                </IconContext.Provider>  
                    <Coords 
                        height={"12mm"}
                        width={"12mm"}
                        title='toggle dimension' 
                        className={settingsButtonToggle}
                        style={toggleButtonStyle} 
                        onClick={this.props.selectParameter}/> 
                <IconContext.Provider value={{size: '1.80em'}}>
                    {
                        this.state.showUnlock ?
                        <MdLockOpen
                            title='lock aspect ratio' 
                            className={styles['button']}
                            onClick={this.handleUnlockclick}
                        />
                        :
                        <MdLockOutline
                            title='lock aspect ratio' 
                            className={styles['button']}
                            onClick={this.handleLockClick}
                            style={{
                                backgroundColor: colorCurrentCommand
                            }}
                        />
                    }
                  
                </IconContext.Provider>  
        </div>
        );
    }
}

export default ParallelMenu
