import React, {PureComponent} from 'react';
import ReactGridLayout from 'react-grid-layout';
import styles from './alternatives.module.scss';
import Alternative from './alternative';
import {generateLayoutForAlternatives} from '../../util/alternatives';
import {calculateWidth} from '../../util/collections';
import layoutConstants from '../../constants/layout';

/**
 * @class Alternatives
 * @extends React.PureComponent
 * @description presentational component of the all the alternatives within the collection. all alternatives are rendered within a react-grid to enable drag & drop
 */
class Alternatives extends PureComponent {
  /**
   * @constructor
   * @param {object} props - component props
   */
  constructor(props) {
    super(props);

    this.state = {
      layouts: [],
      // wasDragged: false,
    }
  }

  /**
   * @function renderAlternatives
   * @description Using viewId for key's as key must be unique for each item rendered
   * @memberof Alternatives
   * @param {ImmutableList} alternativesInCollection - a alist of alternative viewIDs in the collection
   * @param {Object} props
   * @returns {ReactPureComponent}
   */
  renderAlternatives = (alternativesInCollection, props={}) => (
    alternativesInCollection.map((alternative) => (
      <div 
          key={alternative.get('viewId')}
          data-grid={this.state.layouts.find((l) => {return l.i === alternative.get('viewId')})}>
        <Alternative
          isResizable={true} 
          alternative={alternative}
          key={alternative.get('viewId')}
          layoutForAlt={this.state.layouts.find((l) => {return l.i === alternative.get('viewId')})}          
          {...props} />
      </div>
    )));

  /**
   * @function onLayoutChange
   * @description updates the collection layouts when triggered
   * @memberof Alternatives
   * @param {object} layout - a ReactGridLayout layout object
   */
  onLayoutChange = (layout) => {
    this.setState({layouts: layout});
  }

  // handleAltDrag = (object) => {
  //   if(!this.state.wasDragged){
  //     this.setState({
  //       wasDragged: true,
  //     })
  //   }
  // }

  // changeWasDragged = () => {
  //   this.setState({
  //     wasDragged: false,
  //   })
  // }

  /**
   * @description renders the view for all the alternatives within the collections. ReactGridLayout is used to allow for drag within the collections
   * @returns {ReactPureComponent} all alternative components as ReactGridLayout items
   */
  render() {
    // what the width should be
    const width = calculateWidth(this.props.layoutForCollection.get('w'), 5, layoutConstants.get('CELL_DIM'), 0);
    const numColumns = Math.floor((width - 40) / 170);
    
    const alternativesLayout = generateLayoutForAlternatives(
      this.props.altsInCollection.map(alt => alt.get('viewId')),
      numColumns
    );
    
    return (
      <ReactGridLayout
        width={numColumns * this.props.alternativeImageSize}
        rowHeight={(this.props.alternativeImageSize - 20)}
        isResizable
        cols= {numColumns}
        onLayoutChange={this.onLayoutChange}
        className={styles['alternative-container']}
        item={this.props.altsInCollection.size}
        compactType={'horizontal'}
        margin={[10, 10]}
        containerPadding={this.margin}
        layout={alternativesLayout.toJS()}
        useCSSTransforms={false}
      >
        {this.renderAlternatives(this.props.altsInCollection, {
          alternativeImageSize: this.props.alternativeImageSize,
          selectAlternative: this.props.selectAlternative,
          deselectAlternative: this.props.deselectAlternative,          
          hoverAlternative: this.props.hoverAlternative,
          dehoverAlternative: this.props.dehoverAlternative,
          removeAlternativesFromCollection: this.props.removeAlternativesFromCollection,
          toggleAltView: this.props.toggleAltView,
          restoreAlternative: this.props.restoreAlternative,
          selectedAlts: this.props.selectedAlts,
        })}
      </ReactGridLayout>
    );
  }
}

export default Alternatives;
