
export const create2DArray = (rows) => {
    let arr = [];
      for (let i=0; i<rows; i++) {
        arr[i] = [];
      }
    return arr;
}

export const countUnique = (iterable) => {
    return new Set(iterable).size;
}

export const groupBy = (list, keyGetter) => {
    const map = new Map();
    list.forEach((item) => {
         const key = keyGetter(item);
         const collection = map.get(key);
         if (!collection) {
             map.set(key, [item]);
         } else {
             collection.push(item);
         }
    });
    return map;
}

export const pairwise = (result, func) => {
    for (let i=0; i < result.length - 1; i+=2) {
        func(result[i], result[i + 1]);
    }
}

export const makeName = (str) => {
  var tmpStr = str.split("_brush");
  return tmpStr;
}

export const createSimpleQuery = (query) => {
  let simpleQuery = {};
  for(let j = 0; j < query.length; j++) {
    for(let k = 0; k < query[j].length; k++) {             
      for (var m = 0; m < query[j][k].length; m++) {
        let rangeName = makeName(query[j][k][m]);
        if (!simpleQuery[rangeName[0]]) {
          simpleQuery[rangeName[0]] = {};
        }
        if (!simpleQuery[rangeName[0]][rangeName[1]]) {
          simpleQuery[rangeName[0]][rangeName[1]] = rangeName[0] + rangeName[1];
        }
      }              
    }
  }

  return simpleQuery;
}

// expects pairings: joinHandles
// returns query in strings
// joinHandles

export function queryStringGenerator(query) {
  let qss = "";

  for(let i=0; i< query.length; i++) {
    let simpleQuery = createSimpleQuery(query[i]);

    if (query.length > 1) {
      qss += '(';
    }                    

    let dimCount = 0;

    for (let dim in simpleQuery) {
      dimCount ++;
      if (Object.keys(simpleQuery[dim]).length > 1) {
        qss += '(';
          let rangeCount = 0;
          for(let range in simpleQuery[dim]){
            rangeCount ++;
            qss += simpleQuery[dim][range];
            if (rangeCount !== Object.keys(simpleQuery[dim]).length) {
              qss += " or "
            }
          }
        qss += ')';
      } else {
        qss += dim;
      }

      if (dimCount !== Object.keys(simpleQuery).length) {
        qss += " and ";
      }
    }

    if (query.length > 1) {
      qss += ' ) ';
      if (i !== query.length-1) {            
        qss += ' OR ';
      }            
    }
  } 
  
  return qss;
}