/**
 * Action dispatchers for operations using websockets
 * @namespace websocketActions
 */

import appConstants from '../constants/appConstants';
import Immutable from 'immutable';
import {
  addAlternative,
  createCollection,
  collectionsFetch,
  addLayoutObject,
  addAlternativeToCollection,
  valueBandsFetch,
  keySetsFetch,
  updateAlternative,
  setLoadingState,
  selectAlternative,
} from './updateActions';
import {
  hoverAlternativeSpecial,
} from './alternativeActions';
import {
  findCollectionsFromName,
} from '../util/collections';
import {
  findCollectionsFromID,
  fetchCollectionsNotID,
} from '../util/collections';
import {
  removeFieldFromDatum,
  shapeAlternative,
  shapeCollection,
} from '../util/util';
import {batch} from 'react-redux';

// TODO: add notifications/toasts below

/**
 * @function openWebSocketConnection
 * @description opens websocket connection
 * @memberof websocketActions
 * @returns {action}
 */
export const openWebSocketConnection = () => ({
  type: appConstants.get('OPEN_WEB_SOCKET_CONNECTION'),
});

/**
 * @function closeWebSocketConnection
 * @description closes websocket connection
 * @memberof websocketActions
 * @returns {action}
 */
export const closeWebSocketConnection = () => ({
  type: appConstants.get('OPEN_WEB_SOCKET_CONNECTION'),
});

/**
 * @function setNewWebsocket
 * @description dispatches an action that to set a new Websocket connection
 * @memberof websocketActions
 * @param {object} payload
 * @returns {action}
 */
export const setNewWebsocket = (payload) => ({
  type: appConstants.get('SET_NEW_WEBSOCKET'),
  payload,
});

/**
 * @function sendMessage
 * @description  sends a message through websocket
 * @memberof websocketActions
 * @param {object} websocket
 * @param {object} message
 * @returns {action}
 */
export const sendMessage = (websocket, message) => () => {
  if (websocket) {
    if (websocket.state.ws.readyState === 1) {
      const userId = localStorage.getItem(appConstants.get('USER_ID'));
      const projectId = sessionStorage.getItem(appConstants.get('PROJECT_ID'));
      message.projectId = projectId;
      message.userId = userId;
      const data = JSON.stringify(message);
      websocket.sendMessage(data);
    }
  }
};

/**
 * @function onMessageRecieved
 * @description  responds to message received with redux actions
 * @memberof websocketActions
 * @param {object} response
 * @returns {action}
 */
export const onMessageRecieved = (response) => (dispatch) => {
  const {type, data} = JSON.parse(response);
  switch (type) {
  case 'NEW':
    batch(() => {
      dispatch(addAlternative(shapeAlternative(data.alt, data.alt.params, data.alt.outputs)));
      dispatch(hoverAlternativeSpecial(data.alt.viewId));
      dispatch(manageNewAlternative(data.alt, data.collection, data.valueBands, data.keySets));
      dispatch(addRefs(data.refResult));
    });
    break;
  case 'EDIT':
    dispatch(editNewAlternative(data.alts, data.valueBands));
    break;
  case 'NEWALT':
    dispatch(addNewAlternative(data.alt, data.collection));
    break;
  default:
    break;
  }
};


export const manageNewAlternative = (alts, newCollection, valueBands, keySets) => (dispatch, getState) => {
  const coll = getState().collections.get('collections');
  const result = findCollectionsFromID(coll, alts.collectionViewId);

  batch(() => {
    dispatch(valueBandsFetch(valueBands));
    dispatch(keySetsFetch(keySets));
  });


  if (!result) {

    const inputs = getState().alternatives.get('alternatives').size > 0 ? getState().alternatives.get('alternatives').toJS()[0]['params'] : {};
    const outputs = getState().alternatives.get('alternatives').size > 0 ? getState().alternatives.get('alternatives').toJS()[0]['outputs'] : {};

    newCollection = shapeCollection(newCollection, Object.keys(inputs), Object.keys(outputs))

    const collectionViewId = newCollection.viewId;
    
    const layoutObject = Immutable.fromJS(
      {x:2, y:2, w:3, h:5 , i: collectionViewId, minW: 3, minH: 5, moved: false, static: false}
    );

    batch(() => {
      dispatch(addLayoutObject(layoutObject));
      dispatch(createCollection([alts._id], newCollection.collectionName, newCollection.refId,
        newCollection.tags, newCollection.viewId,
        false, false, false, newCollection.viewType, getState().scales.get('keySets'), newCollection.inputs, newCollection.outputs, [], {}));
    });

  } else {
    const otherCollections = fetchCollectionsNotID(coll, result.get("viewId"));
    const updatedCollection = result.update('altsid',
      (altsid) => altsid.push(alts._id));

    const updatedCollections = otherCollections.push(updatedCollection);
    dispatch(collectionsFetch(updatedCollections));    
  }

};

export const addRefs = (data) => (dispatch, getState) => {

  const inputs = getState().alternatives.get('alternatives').toJS()[0]['params'];
  const outputs = getState().alternatives.get('alternatives').toJS()[0]['outputs'];

  data.forEach((datum) => {
    datum.alts.map((alternative) => {
      dispatch(addAlternative(shapeAlternative(alternative, inputs, outputs)))
    });

    datum.alts.map((alt) => {
      batch(() => {
        dispatch(addAlternativeToCollection(datum.collectionId, alt.viewId));
        dispatch(selectAlternative(alt.viewId));
      });
    });
  });
};

export const editNewAlternative = (alts, valueBands) => (dispatch, getState) => {

  const collections = getState().collections.get('collections')
  const keySets = getState().scales.get('keySets').toJS();
  
  const inputs = getState().alternatives.get('alternatives').toJS()[0]['params'];
  const outputs = getState().alternatives.get('alternatives').toJS()[0]['outputs'];

    alts.map((alt) => {
      const newAlternative = shapeAlternative(alt, inputs, outputs);

      const fromCollection = findCollectionsFromName(collections, newAlternative.collectionName);
      const dimOrder = fromCollection.get('dimOrder').toJS();

      let parallelData = newAlternative.parallelData;

      let difference = keySets.filter((key) => !dimOrder.includes(key));

      difference.forEach((dim) => parallelData = removeFieldFromDatum(parallelData, dim));
      
      newAlternative.parallelData = parallelData;

      dispatch(updateAlternative(newAlternative.viewId, newAlternative));
      dispatch(setLoadingState(newAlternative.viewId, false));
    })

    dispatch(valueBandsFetch(valueBands));    
};

/**
 * @function addNewAlternative
 * @description  dispatches an action to add a new alternative
 * @memberof websocketActions
 * @param {array} alts
 * @param {array} collections
 * @returns {action}
 */
export const addNewAlternative = (alts, collections) => (dispatch) => {
  batch(() => {
    dispatch(editNewAlternative(alts));
    dispatch(manageNewAlternative(alts, collections));
  });
};
