import React, { PureComponent } from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';


/**
 * @class MessageBox
 * @extends React.PureComponent
 * @description presentational component for the messageBox pane, which is a popup window
 */
class MessageBox extends PureComponent {

  /**
   * @constructor
   * @param {object} props 
   */
  constructor(props) {
      super(props)

      this.state = {
          open: true,
          pressedOk: false, 
          pressedcancel: false,
      }
  }

  /**
   * @function handleOk
   * @memberof messageBox
   * @description confirming or cancelling the popup window content
   */
  handleOk = () => {
    if (this.props.confirmAction != null) {
      this.props.confirmAction();
    }
    
    this.setState({open: false});
  };

  handleCancel = () => {
    this.setState({open: false});
    if(this.props.confirmAction != null) {
      this.props.handleDeleteCancel();
    }
  };

  /**
   * @description renders the view for messageBox component
   * @returns {ReactPureComponent}
   */
  render() {
      return (
          <div>
            <Dialog
              open={this.state.open}
              onClose={this.handleClose}
              aria-labelledby="alert-dialog-title"
              aria-describedby="alert-dialog-description"
            >
              <DialogTitle id="alert-dialog-title">{"Warning"}</DialogTitle>
              <DialogContent>
                <DialogContentText id="alert-dialog-description">
                  {this.props.message}
                </DialogContentText>
              </DialogContent>
              <DialogActions>
                <Button onClick={this.handleCancel} color="primary" >
                  Cancel
                </Button>
                <Button onClick={this.handleOk} color="primary" autoFocus>
                  OK
                </Button>
              </DialogActions>
            </Dialog>
          </div>
        );
  }
}

export default MessageBox;