import React, {PureComponent} from 'react';
import classnames from 'classnames';
import {
  MdVerticalAlignBottom,
  MdDelete,
  MdDeleteForever,
  MdCheckCircle,
  MdCancel,
} from 'react-icons/md';
import {
  IoMdMove,
} from 'react-icons/io';
import {FaHashtag} from 'react-icons/fa';
import {IconContext} from 'react-icons';
import AlternativeContainer from '../../../containers/AlternativeContainer';
import {
  renderIf,
} from '../../../util/util';
import {
  getCollectionName,
  getAlternativesInCollection,
} from '../../../util/collections';
import styles from './collection.module.scss';
import {getLayoutForCollection} from '../../../util/layouts';
import ParallelContainer from '../../../containers/ParallelContainer';
import ListContainer from '../../../containers/ListContainer';
import InfoBar from '../../infoBar/InfoBar';
import TagBar from '../../tagBar/TagBar';
import Select from 'react-select';
import CreatableSelect from 'react-select/creatable';
import * as d3 from 'd3';

/**
 * @constant
 * @type {array}
 * @default
 */
const options = [
  {value: 1, label: 'folder view'},
  {value: 2, label: 'parallel view'},
  {value: 3, label: 'list view'},
];

/**
 * @class Collection
 * @extends React.PureComponent
 * @description presentational component of a single collection containing **Alternatives** component.
 */
class Collection extends PureComponent {

  /**
   * @constructor
   * @param {object} props - component props
   */
  constructor(props) {
    super(props);
    this.state = {
      width: 0,
      height: 0,
      isTagging: false,
      isDragging: false,
      collectionName: '',
      shouldFocus: null,
    }

    this.infoRef = React.createRef();
    this.destinationOptions = [];
    this.tagOptions = [];
  
  }

  /**
  * @description gets the width and height after the component mounts
  */
  componentDidMount = () => {
    const obj = this.cmpEl.getBoundingClientRect();
    this.setState({
      width: obj.width,
      height: obj.height,
      collectionName: getCollectionName(this.props.collection),
    });
  }

  /**
  * @description gets the width and height after the component updates
  */
  componentDidUpdate = (prevProps, prevState, snapshot) => {
    const obj = this.cmpEl.getBoundingClientRect();
    this.setState({width: obj.width});
    this.setState({height: obj.height});
    
    if (prevProps.collection.get("collectionName") !== this.props.collection.get("collectionName")) {
      this.setState({
        collectionName: this.props.collection.get("collectionName"),
      });
    }

    if(this.props.shouldRemoveFocus){
      this.setState({
        shouldFocus: null,
      })
      this.props.removeFocus();
    }

    if (prevProps.collection.get("isEditingName") !==  this.props.collection.get("isEditingName")) {
      let element = document.getElementById(this.state.shouldFocus);

      if (element !== null) {
        element.select();
      }
    }
  }

  
  /**
   * @function loadTags
   * @description loads the tags that are available to the collection
   * @memberof Collection
   * @returns {void}
   */
  loadTags = () => {
    this.tagOptions = this.props.collection.get('tags').toJS().map(tag => {
      return {'label': tag, 'value': tag, '__isNew__': false}
    });
  }

  toggleTagging = () => {
    this.setState({isTagging: !this.state.isTagging});
  }

  toggleEditViewForCollection = (event) => {
    event.stopPropagation();
    event.preventDefault();
    this.props.isEditingCollectionsName(this.props.collection.get('refId') );
    this.setState({shouldFocus: this.props.collection.get('viewId')});
  }

  handleCloseClick = (e) => {
    e.stopPropagation();
    this.props.deleteCollections(this.props.collection.get('viewId'));
  };

  handleEmptyClick = (e) => {
    e.stopPropagation();
    const selectedAlternatives  = this.props.alternatives.get('alternatives')
        .filter((alternative) => alternative.get('isSelected') && alternative.get('collectionViewId') === this.props.collection.get('viewId'));    
    this.props.removeAlternativesFromCollection(selectedAlternatives);
  }

  /**
   * minimize click
   * @param {event} e
   */
  handleMinimizeClick = (e) => {
    e.stopPropagation();

    this.props.toggleViewMode(this.props.collection.get('viewId'), 1);

    this.props.saveLayoutObject(getLayoutForCollection(
      this.props.layouts,
      this.props.collection.get('viewId'))
    );

    this.props.toggleSink(this.props.collection.get('viewId'));
    this.props.deselectCollection(this.props.collection.get('viewId'));
  }

  /**
   * @function handleSelect
   * @description when clicked, appends a collection's ID to an array of selected collections
   * @memberof Collection
   */
  handleSelect = () => {
    if (!this.props.collection.get('isSelected')) {
      this.props.selectCollection(this.props.collection.get('viewId'));
    } else {
      this.props.deselectCollection(this.props.collection.get('viewId'));
    }
  }

  handleHovsClick = (e) => {
    e.stopPropagation();
    e.preventDefault();

    this.props.alternatives.get('alternatives').filter((alt) => 
      this.props.collection.get('altsid').contains(alt.get('viewId'))).map((d) => {

        const hoveredRelatives = this.props.alternatives.get('alternatives')
        .filter((alternative) => d.get('isHovered') || (alternative.get('isHovered') && 
          alternative.get('refId') === d.get('refId') &&
          !this.props.collection.get('altsid').contains(alternative.get('viewId'))));

          if (hoveredRelatives.size !== 0) {
            this.props.selectAlternative(d.get('viewId'));

            d3.select(".alt-view-" + d.get('viewId')) 
              .style('border', '1.5px')
              .style('border-style', 'solid')
              .style('border-color', styles.colorSelected)
              .style('border-radius', '7px')
              .style('-webkit-box-shadow', '0px 0px 5px ' + styles.colorSelected);
          }
      });
  }

  handleRefsClick = (e) => {
    e.stopPropagation();
    e.preventDefault();

    this.props.alternatives.get('alternatives').filter((alt) => 
      this.props.collection.get('altsid').contains(alt.get('viewId'))).map((d) => {

        const selectedRelatives = this.props.alternatives.get('alternatives')
          .filter((alternative) => alternative.get('isSelected') && 
            alternative.get('refId') === d.get('refId') &&
            !this.props.collection.get('altsid').contains(alternative.get('viewId')));

          if (selectedRelatives.size !== 0) {
            this.props.selectAlternative(d.get('viewId'));

            d3.select(".alt-view-" + d.get('viewId')) 
              .style('border', '1.5px')
              .style('border-style', 'solid')
              .style('border-color', styles.colorSelected)
              .style('border-radius', '7px')
              .style('-webkit-box-shadow', '0px 0px 5px ' + styles.colorSelected);
          }
      });
  }

  handleUnselectClick = (e) => {
    e.stopPropagation();
    e.preventDefault();

    getAlternativesInCollection(this.props.collection, this.props.alternatives.get('alternatives'))
      .map((alt) => {
        this.props.deselectAlternative(alt.get('viewId'));

        d3.selectAll(".alt-ref-" + alt.get('refId'))
          .style('border', null)
          .style('border-style', null)
          .style('border-color', null)
          .style('border-radius', null)
          .style('-webkit-box-shadow', null);
      });
  }


  handleBlur = (e) => {
    if (e.key === 'Enter') {
      let name = "untitled";
      let copy = false;

      if (e.target.value.includes('#') && '#' + this.props.collection.get('collectionName') !== e.target.value) {
        copy = true;
      }
        name = e.target.value.replace(/#/g,"").replace(/ /g,"-")
   
        if (this.props.collection.get("collectionName") !== name && name.length > 0) {
          const nameExists = this.props.nameExists(name, this.props.collection);

          if (this.props.isMultipleInputs) {
            if (!nameExists) {

              let object = {
                name: name, 
                collection: this.props.collection
              }

              this.props.updateMultipleNames(object);

              this.setState({
                shouldFocus: null,
              });

            } else {
            
              this.props.isEditingCollectionsName(this.props.collection.get("refId"))
              this.setState({
                shouldFocus: null,
              })
            }
          } else {
            if (!nameExists) {
              try {
                const collection =  this.props.collection.toJS();
                      collection.collectionName = name + "-" + collection.collectionNameView
                      this.props.updateCollection(collection);
                    
              } catch (err) {
                console.log(err)
              } finally {
                this.props.editNameForCollection(this.props.collection.get('viewId'), name);
                this.setState({
                  shouldFocus: null,
                });            
              }
            } else {
              this.props.isEditingCollectionsName(this.props.collection.get("refId")) 
              this.setState({
                shouldFocus: null,
              })
            }
          }
        } else {
          this.props.nameHasTheSameValue(this.props.collection.get('refId'));
          this.setState({
            collectionName: this.props.collection.get("collectionName"),
            shouldFocus: null,
          })
        }
      } else {
        // console.log("in blur");
      }
  }

  handleTaggingSelect = (newValue, actionMeta) => {
    if (newValue) {
      try {
        const colToUpdate = this.props.collection.toJS();
        colToUpdate.tags = newValue.map(v => v.value);
        this.props.updateCollection(colToUpdate);
      } catch (err) {
        console.log(err);
      } finally {
        this.props.updateCollectionTags(this.props.collection.get('viewId'), newValue.map(v => v.value));
      }
    } else {
      try {
        const colToUpdate = this.props.collection.toJS();
        colToUpdate.tags = [];
        this.props.updateCollection(colToUpdate);
      } catch (err) {
        console.log(err);
      } finally {
        this.props.updateCollectionTags(this.props.collection.get('viewId'), []);
      }
    }
  }

  handleViewSelect = (mode) => {
      switch (mode.label) {
      case 'folder view':
        this.props.toggleViewMode(this.props.collection.get('viewId'), mode.value);
        break;
      case 'parallel view':
        this.props.toggleViewMode(this.props.collection.get('viewId'), mode.value);
        break;
      case 'list view':
        this.props.toggleViewMode(this.props.collection.get('viewId'), mode.value);
        break;
      default:
        console.log('no valid selection');
      }
  }

  handleDestinationSelect = (newValue) => {
    if (!newValue) {
      this.props.setCollectionDestination(this.props.collection.get('viewId'), []);
    } else {
      this.props.setCollectionDestination(this.props.collection.get('viewId'), newValue);
    }
  };

  /**
   * to prevent double click from creating a new collection
   * @param {event} e
   */
  handleDoubleClick = (e) => {
    e.stopPropagation();
    e.preventDefault();
  }

  handleFocus = (e) => {
    e.target.select();
  }

  handleKeyPress = (e) => {

    if (e.key === 'Enter') {
      this.handleBlur(e);
    }
  }

  handleDrag = (e) => {
    e.stopPropagation();
    e.preventDefault();
    this.setState({isDragging: !this.state.isDragging});
  }

  handleDragClick = (e) => {
    e.stopPropagation();
    e.preventDefault();
  }

  handleTextChange = (e) => {
    e.stopPropagation();
    e.preventDefault();
    let name = e.target.value.replace(/#/g,"").replace(/ /g,"-")

    const object  = {
      refId: this.props.collection.get('refId'),
      viewId: this.props.collection.get('viewId')
    }

    this.props.changeMultipleName(object)

    this.setState({
      collectionName: name
    });   
  }

  handleAspectRatio = (dimensions) => {
    
    const object = {
      viewId: this.props.collection.get("viewId"),
      width: 0,
      height: 6, // best height for readability
      isResizable: false,
    } 

    object.width = Math.floor(dimensions.length * 0.4) > 4 ? Math.floor(dimensions.length * 0.4) : 4;
  
    this.props.updateAspectRatio(object)
  }

  handleUnlockAspectRatio = () => {
    let object = {
      viewId: this.props.collection.get("viewId"),
      isResizable: true,
    } 
    this.props.updateUnlockAspectRatio(object);
  }

  /**
   * @returns {ReactPureComponent}
   */
  render() {
    const collectionHeight = getLayoutForCollection(this.props.layouts, this.props.collection.get('viewId')).get('h');

    // this is to figure out which style to apply to the current collection
    // collection selected has a blue border, other collections with the same (ref) id
    // are highlighted with yellow border
    let situation = 'C';
    
    if (this.props.collection.get('isSelected')) {
      situation = 'A';
    } else if (!this.props.collection.get('isSelected') && this.props.selectedCols.includes(this.props.collection.get('refId'))) {
      situation = 'B';
    }

    
    const selectStyleName = classnames({
      [styles['collection']]: situation === 'C',
      [styles['collection-selected']]: situation === 'A',
      [styles['collection-related']]: situation === 'B',
    });

    const dragStyleName = classnames({
      [styles['start-drag']]: !this.state.dragging,
      'drag-handle': true,
      [styles['dragging']]: this.state.dragging,
    });


    const customStyles = {
      container: (base, state) => {
        return ({...base, zIndex: state.isFocused ? '998' : '1'}); // Only when current state focused
      },
    };
    
    this.destinationOptions = this.props.loadOptions(this.props.collection.get('viewId'));

    this.loadTags();
    return (
      <div 
        key = {'collection-box-' + this.props.collection.get('viewId')}
        ref={(el) => this.cmpEl = el}
        className={selectStyleName}
        style={this.props.style}
        onDoubleClick={this.handleDoubleClick}>
        <div 
          onClick={this.handleSelect} 
          className={styles.pointer2} 
        >
          <div className={styles['icon-handle']}>
          <span>
            <span>
              <IconContext.Provider value={{size: '1.25em'}}>
                <IoMdMove title='move'
                  className={dragStyleName}
                  onClick={this.handleDragClick}
                  onDragStart={this.handleDrag}
                  onDragEnd={this.handleDrag}
                />
              </IconContext.Provider>
            </span>
            <span style={{marginLeft: '12px'}}>
              <IconContext.Provider value={{size: '1.25em', color: styles.colorSelected, paddingLeft: '2px'}}>
                  <MdCancel title='unselect'
                    onClick={this.handleUnselectClick}
                    className={styles.pointer}
                  />
              </IconContext.Provider>
              </span>
            <span>
              <IconContext.Provider value={{size: '1.25em', color: styles.colorHovered}}>
                  <MdCheckCircle title='select hovered'
                    className={styles.pointer}
                    onClick={this.handleHovsClick}
                  />
              </IconContext.Provider>
              </span>
              <span>
              <IconContext.Provider value={{size: '1.25em', color: styles.colorSelectedRelated}}>
                  <MdCheckCircle title='select refed'
                    className={styles.pointer}
                    onClick={this.handleRefsClick}
                  />
              </IconContext.Provider>
              </span>
            </span>
            {renderIf(
              !this.props.collection.get("isEditingName"),
              (
                <div 
                  className={styles['collection-name-div']}
                  onDoubleClick={this.toggleEditViewForCollection}>
                  <div className={styles['collection-title-div']}>
                     <h4
                      className={styles['collection-name']}
                      onDoubleClick={this.toggleEditViewForCollection}>
                      {getCollectionName(this.props.collection)}
                    </h4> 
                  </div>
                    
                  <div className={styles['collection-titleView-div']}>
                    <h4
                      className={styles['collection-name-view']}
                    >
                      {this.props.collection.get("collectionNameView")}
                    </h4>
                  </div>
                  
                </div>  
              ),
             <div className={styles['collection-name-div']}>
              <div className={styles['collection-input-div']}>
                  <input
                    id={this.props.collection.get('viewId')}
                    type="text"
                    onFocus={this.handleFocus}
                    onBlur={this.handleBlur}
                    onKeyPress={this.handleKeyPress}
                    className={styles['textInputStyle']}
                    onChange={this.handleTextChange}
                    value={this.state.collectionName}
                  />
              </div>

              <div className={styles['collection-titleView-div']}>
                    <h4 className={styles['collection-name-view']}>
                      {this.props.collection.get("collectionNameView")}
                    </h4>
              </div>
             </div>
            )}
            <span>
              <IconContext.Provider value={{size: '1.35em'}}>
                <MdVerticalAlignBottom title='sink'
                  className={styles.pointer}
                  onClick={this.handleMinimizeClick}
                />
              </IconContext.Provider>
              <span>
              <IconContext.Provider value={{size: '1.35em'}}>
                  <MdDelete title='empty'
                    className={styles.pointer}
                    onClick={this.handleEmptyClick}
                  />
                </IconContext.Provider>
              </span>
              <span>
                <IconContext.Provider value={{size: '1.35em'}}>
                  <MdDeleteForever title='delete'
                    className={styles.pointer}
                    onClick={this.handleCloseClick}
                  />
                </IconContext.Provider>
              </span>
            </span>
          </div>
        </div>
        <div style={{'display':'flex', 'justifyContent': 'space-between', 'flexDirection': 'row'}}>
        {collectionHeight < 2 ? null : <div style={{'flex': '60%', 'marginRight': '1%'}}>
          <Select
            styles={customStyles}
            defaultValue={options[this.props.collection.get('viewType')-1]}
            options={options}
            onChange={this.handleViewSelect}
          />
        </div>}
        {this.props.collection.get('viewType') !== 2 ? null : <div style={{'flex': '30%', 'marginLeft': '1%'}}>
          <CreatableSelect
            styles={customStyles}
            isMulti
            placeholder={'destination collection(s)'}
            options={this.destinationOptions}
            onChange={this.handleDestinationSelect}
          />
        </div>}
        </div>
        <hr></hr>
        {this.props.collection.get('viewType') === 1 ? <AlternativeContainer
          altsInCollection={this.props.altsInCollection}
          layoutForCollection={this.props.layoutForCollection}
          selectAlternative={this.props.selectAlternative}
          deselectAlternative={this.props.deselectAlternative}
          hoverAlternative={this.props.hoverAlternative}
          dehoverAlternative={this.props.dehoverAlternative}
          removeAlternativesFromCollection={this.props.removeAlternativesFromCollection}
          toggleAltView={this.props.toggleAltView}
          restoreAlternative={this.props.restoreAlternative}
          selectedAlts={this.props.selectedAlts}      
         /> : null}
        {this.props.collection.get('viewType') === 2 ? <ParallelContainer
          width={this.state.width}
          height={this.state.height}
          selectAlternative={this.props.selectAlternative}
          deselectAlternative={this.props.deselectAlternative}
          deselectAllAlternatives={this.props.deselectAllAlternatives}
          hoverAlternative={this.props.hoverAlternative}
          hoverAlternativeSpecial={this.props.hoverAlternativeSpecial}
          dehoverAlternative={this.props.dehoverAlternative}
          sendMessage={this.props.sendMessage}
          altsInCollection={this.props.altsInCollection}
          collection={this.props.collection}
          alternatives={this.props.alternatives}
          cloneAlternatives={this.props.cloneAlternatives}
          createNewCollection={this.props.createNewCollection}
          setCollectionMode={this.props.setCollectionMode}
          infoRef={this.infoRef}
          restoreAlternative={this.props.restoreAlternative}
          selectedAlts={this.props.selectedAlts}
          lockAspectRatio={this.handleAspectRatio.bind(this)}
          unlockAspectRatio={this.handleUnlockAspectRatio}      
        /> : null}
        {this.props.collection.get('viewType') === 3 ? <ListContainer 
          width={this.state.width}
          height={this.state.height}
          selectAlternative={this.props.selectAlternative}
          deselectAlternative={this.props.deselectAlternative}
          hoverAlternative={this.props.hoverAlternative}
          dehoverAlternative={this.props.dehoverAlternative}
          altsInCollection={this.props.altsInCollection}
          collection={this.props.collection}
          alternatives={this.props.alternatives}
          selectedAlts={this.props.selectedAlts}      

        /> : null}
        <IconContext.Provider value={{size: '1.15em'}}>
          <FaHashtag 
            title='tag'
            className={styles.tagEdit}
            onClick={this.toggleTagging}
          />
        </IconContext.Provider>
        <InfoBar
          altsInCollection={this.props.altsInCollection.toJS()}
          infoRef={this.infoRef}
        />
        {this.state.isTagging ?
          <div style={{'marginLeft': '5%'}}>
            <CreatableSelect
              styles={customStyles}
              isMulti
              onChange={this.handleTaggingSelect}
              defaultValue={this.tagOptions}
              menuPosition={'fixed'}
            /> 
          </div> : null }
          <TagBar 
            tagOptions={this.tagOptions}
          />
      </div>
    );
  }
}

export default Collection;
