import React, { Component } from 'react';
import {connect} from 'react-redux';
import {getUserProjects} from '../actions/fetchActions';
import {logout, fetchUser} from "../actions/userActions";
import Projects from '../components/projects/Projects';
import { 
    selectProject,
    createProject,
    deleteProject,
    editProject,
}  from '../actions/projectActions';
import { 
  isCreatingProject,
  openEditProject,
  showProjectMenu,
  shouldCloseProjectContextMenu,
} from "../actions/updateActions";

/**
 * This component connects the Project component to the state
 */
class ProjectContainer extends Component {
/**
 * The component renders the presentational component Projects
 * @return {ReactComponent}
 */
  
  
 componentDidMount() {
   // we need to the api call to ftech user data
     this.props.fetchUser();
   

 }

    
  render() {
      return(
          <div>
              <Projects
                 {...this.props}
              />
          </div>
      )
  }
}

const mapStateToProps = (state) => ({
  projects: state.projects,
  auth: state.auth,
});

const mapDispatchToProps = (dispatch) => ({
  /**
   * selects a project.
   * @param {object} project
   */
  selectProject: (project) =>{
    dispatch(selectProject(project));
  },

  /**
   * Dispatch action createProject
   * @param {String} name 
   * @param {String} description 
   */
  createProject: (name, description) =>{    
    dispatch(createProject(name, description));
  },

  /**
   * Dispatch action is create a project
   */
  isCreatingProject: () => {
      dispatch(isCreatingProject());
  },

  logout: () => {
    dispatch(logout())
  },

  getUserProjects: () => {
    dispatch(getUserProjects());
  },

  fetchUser: () => {
    dispatch(fetchUser())
  },

  deleteProject: (projectId) => {
    dispatch(deleteProject(projectId));
  },


  openEditProject: (project) => {
    dispatch(openEditProject(project))
  },

  editProject: (editElements) => {
    dispatch(editProject(editElements));

  },

  showProjectMenu: (xPos, yPos, project) => {
    dispatch(showProjectMenu(xPos, yPos, project));
  },

  shouldCloseProjectContextMenu: () => {
    dispatch(shouldCloseProjectContextMenu());
  }




})

export default connect(mapStateToProps, mapDispatchToProps)(ProjectContainer)