import alternativeObjectConstant from '../constants/alternativeObject';
import layoutConstants from '../constants/layout';
import {urls} from '../constants/urls';
import memoize from 'lodash/memoize';
import values from "lodash/values";

/**
 * find alternative object from a list from alternativeID
 * @param {ImmutableList} alternatives
 * @param {string} alternativeID
 * @return {ImmutableMap}
 */
export const findAlternativesFromID = memoize((alternatives, alternativeID) => (
  alternatives.find((alternative) => alternative.get('viewId') === alternativeID)
), (...args) => values(args).join("_"));

/**
 * find refs of an alternative with the given ID (except the ID itself)
 * @param {ImmutableList} alternatives
 * @param {string} alternativeID
 * @return {ImmutableMap}
 */
export const findAlternativeRelativesFromID = (alternatives, alternativeID) => {
  const alternative = alternatives.find((alternative) =>
    alternative.get('viewId') === alternativeID)
  const refId = alternative.get('refId');
  const altRelatives = alternatives.filter((alt) => alt.get('refId') === refId)
    .filter((alt) => alt.get('viewId') !== alternative.get('viewId'))
  return altRelatives;
}

/**
 * remove an item (with id: alternativeID) from the list of alternatives.
 * @param {ImmutableList} alternatives : list of alternatives
 * @param {int} alternativeID : Item to remove
 * @return {ImmutableList} : list of alternatives, with the removed item
 */
export const filterAlternativesNotID = memoize((alternatives, alternativeID) => (
  alternatives.filter((alternative) =>
    alternative.get(alternativeObjectConstant.get('ID')) !== alternativeID
  )
), (...args) => values(args).join("_"));

/**
 * returns the image url of the alternative
 * @param {ImmutableMap} alternative
 * @return {string} :  image url of the alternative
 */
export const getAlternativeImageURL = memoize((alternative) => (
  urls.get('getHost') + alternative.get(alternativeObjectConstant.get('IMAGE'))
));


/**
 * this function gets you the parallel data needed for D3.js
 * @param {ImmutableList} altsInCollection : list of alternatives
 * @return {array} parallelData
 */
export const getParallelDataFromAlternatives = (altsInCollection) => {
  const temp= [];
  const parallelData = [];
  let mergedObject = {};

  altsInCollection.map((alt) => temp.push({'id': alt.viewId, ...alt.attributes}));

  temp.map((obj) => {
    Object.values(obj).map((o) => {
      const eachPoint = {[o.key]: o.value};
      mergedObject = Object.assign(mergedObject, {'id': obj.id}, eachPoint);
    });
    parallelData.push(mergedObject);
    mergedObject = {};
  });

  parallelData.map((obj) => delete obj.undefined);

  return parallelData;
};

/**
 * this function gets you the parallel data needed for D3.js
 * @param {ImmutableMap} altObj : an alternative object
 * @return {array} parallelData
 */
export const getParallelDataFromAlternative = (altObj) => {
  const temp = [];
  let mergedObject = {};

  temp.push({...altObj.params, ...altObj.outputs});
  
  temp.map((obj) => {
    Object.values(obj).map((o) => {
      const eachPoint = {[o.key]: o.value};
      mergedObject = Object.assign(mergedObject, eachPoint);
    });
    return mergedObject;
  });

  return mergedObject;
};

/**
 * filters a list of selected alternatives from the alternatives in collection
 * @param {ImmutableList} altsSelected : list of alternatives selected
 * @param {array} altsInCollection : list of alternatives in collection
 * @return {array} filtered
 */
export const filterSelectedAlternativesInCollection = memoize((altsSelected, altsInCollection) => (
  altsSelected.filter((alt) => altsInCollection.includes(alt.get('viewId'))
  )), (...args) => values(args).join("_"));


/**
 * add properties to default collections list
 * @param {array} alternatives
 * @return {array}
 */
export const addPropertyToAlternatives = (alternatives) => (
  alternatives.map((alternative) => {
    alternative.altView = false;
    alternative.isSelected = false;
    alternative.isHovered = false;
    alternative.isLoading = false;
    alternative.parallelData = [];
    return alternative;
  })
);

/**
 * remove an element (with id = alternatieID) from the altsid list
 * @param {ImmutableList} alternativesID
 * @param {int} alternativeID
 * @return {ImmutableList}
 */
export const fetchAlternativesNotID = memoize((alternativesID, alternativeID) => (
  alternativesID.filter((altID) => altID !== alternativeID)
), (...args) => values(args).join("_"));

/**
 * Generates a layout object for alternatives in collection
 * allots x and y position for every alternative
 * @param {array} altIDs list of alternative ids
 * @param {int} cols: the number of alternatives placed in a row
 * @return {object} defines the x, y, w, h, i for every alternative
 */
export const generateLayoutForAlternatives = (altIDs = [], cols) => {
  let x = 0;
  let y = 0;
  const w = layoutConstants.get('DEFAULT_ALT_WIDTH');
  const h = layoutConstants.get('DEFAULT_ALT_HEIGHT');

  let index = 0;

  return altIDs.map((i) => {
    const layout = {
      x, y, w, h, i,
    };

    index += 1;
    if (index % cols === 0) {
      index = 0;
      y += 1;
      x = 0;
    } else {
      x += 1;
    }

    return layout;
  });
};

/**
 * get alternative ids from a list of alternatives
 * @param {ImmutableList} alternatives
 * @return {ImmutableList}
 */
export const getAlternativeIDs = memoize((alternatives) => (
  alternatives.map((alternative) => (
    alternative.get(alternativeObjectConstant.get('ID'))
  ))
), (...args) => values(args).join("_"));

/**
 * determines which collection the alternative belogs to
 * @param {ImmutableList} collections
 * @param {string} alternativeID
 * @return {string} collectionName
 */
export const determineAlternativeCollection = (collections, alternativeID) => {
  const sourceCollection = collections.filter((collection) => collection.get('altsid').contains(alternativeID))
  return sourceCollection[0].get('collectionName');
}
