import appConstants from '../constants/appConstants';
import Immutable from 'immutable';


const initialState = Immutable.fromJS({
  projects: [],
  projectFetched: false,
  projectLoaded: false,
  isCreating: false,
  showContextMenu: false,
  showEditBox: false,
  editProject: null,
  selectedProject: null,
  contextMenuPosX: 0,
  contextMenuPosY: 0,
  showProjectContextMenu: false,
  contextMenuSelectedProject: null 
});

export default (state = initialState, action={}) => {
    switch (action.type) {

        case appConstants.get('FETCH_PROJECT'):
            return state.set('projects', Immutable.fromJS(action.payload.projects))
                .set('projectLoaded', action.payload.db_loaded)
                .set('projectFetched', true);
        case appConstants.get('SELECT_PROJECT'):
            return state.set('projectLoaded', true).set('selectedProject', action.payload)

        case appConstants.get("CREATING_PROJECT"):
            return state.set("isCreating", !state.get("isCreating"));
        case appConstants.get("DELETE_PROJECT"):
            return state.set('projects', state.get('projects').filter(project => project.get('id') !== action.payload));
        case appConstants.get("PROJECT_EDIT_BOX"):
            return state.set("showEditBox",  !state.get("showEditBox")).set("editProject", action.payload);
        case appConstants.get("UPDATE_PROJECT"):
             const indexOfListToUpdate  = state.get("projects").findIndex(project => {
                 return project.get("id") === action.payload.projectId
             })
            return state.set("showEditBox", !state.get("showEditBox")).setIn(["projects", indexOfListToUpdate, "name"], action.payload.projectName).setIn(["projects", indexOfListToUpdate, "description"], action.payload.projectDescription);
        case appConstants.get("SHOW_PROJECT_CONTEXT_MENU"):
            return state.set("contextMenuPosX", action.payload.xPos).set("contextMenuPosY",  action.payload.yPos).set("showProjectContextMenu", true).set("contextMenuSelectedProject", action.payload.project);
        case appConstants.get("HIDE_PROJECT_CONTEXT_MENU"):
            return state.set("contextMenuPosX", 0).set("contextMenuPosY",  0).set("showProjectContextMenu", false).set("contextMenuSelectedProject", null);
        default:
            return state;
  }
};