/**
 * Action dispatchers for operations on collection objects
 * @namespace parallelActions
 */

import {toast} from 'react-toastify';
import {urls} from '../constants/urls';
import {
  successMessage,
  errorMessage,
} from './notificationActions';
  
const time = new Date().getHours() + ':' + new Date().getMinutes() + ':' + new Date().getSeconds();

/**
 * @function newCartesianProduct
 * @description initiates a new cartesian product process on the server
 * @memberof parallelActions
 * @param {array} alternativeIDs 
 * @param {array} selectedDimensions
 * @returns {redux-thunk} middleware - fetch call to update the database and then the redux-store
 */
export const newCartesianProduct = (alternativeIDs, selectedDimensions, destinations) => (dispatch, getState) => {
  
  return fetch(urls.get('cartesian_product'), {
    method: 'POST',
    headers: {'Content-Type': 'application/json'},
    body: JSON.stringify({
      alt_ids : alternativeIDs,
      keySets : selectedDimensions,
      destinations : destinations
    })
  })
  .then((data) => {
    dispatch(successMessage(toast.success('Cartesian Product has started'), 'Cartesian Product has started ' + time));
  })
  .catch((err) => {
    console.log(err);
    dispatch(errorMessage(toast.error('Cartesian Product has not started'), 'Cartesian Product has not started ' + time));
  });
};

