import React, {Component} from 'react';
import {connect} from 'react-redux';
import {renderIf} from '../util/util';
import Header from '../components/header';
import {
  showSinkList,
  hideSinkList,
  addLayoutObject,
  gatherAllCollections,
  toggleSettings,
  hideHeader,
  dehoverAllAlternative,
  toggleSearch,
} from '../actions/updateActions';
import {
  restoreCollection,
} from '../actions/collectionActions';
import {
  toggleHistory
} from '../actions/notificationActions';

/**
 * This component connects the Header component to the state.
 * It has all the interactions required by the header component
 * The header is displayed when you right click on the design gallery
 */
class HeaderContainer extends Component {
  /**
   * The component should render the presentational component of the header.
   * @return {ReactComponent}
   */
  render() {
    return renderIf(
      this.props.header.get('headerShow'),
      (<Header {...this.props} />),
    );
  }
}

/**
 * state for the header container
 * @param {object} state
 * @return {object}
 */
const mapStateToProps = (state) => ({
  header: state.header,
  layouts: state.layouts,
  collections: state.collections,
});

/**
 * interactions for header container
 * @param {function} dispatch
 * @return {object}
 */
const mapDispatchToProps = (dispatch) => ({

  showSinkList: () => {
    dispatch(showSinkList());
  },

  hideSinkList: () => {
    dispatch(hideSinkList());
  },

  addLayoutObject: (layout) => {
    dispatch(addLayoutObject(layout));
  },

  toggleHistory: () => {
    dispatch(toggleHistory());
  },

  gatherAllCollections: () => {
    dispatch(gatherAllCollections());
  },

  toggleSettings: () => {
    dispatch(toggleSettings());
  },

  dehoverAllAlternative: () => {
    dispatch(dehoverAllAlternative());
  },
  
  hideHeader: () => {
    dispatch(hideHeader());
  },

  toggleSearch: () => {
    dispatch(toggleSearch());
  },

  /**
   * dispaches an action to restore collection with ID
   * @param {string} collectionId: the ID of the collection
   */
  restoreCollection: (collectionId) => {
    dispatch(restoreCollection(collectionId));
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(HeaderContainer);
