import React, { PureComponent } from 'react';
import ReactGridLayout from 'react-grid-layout';
import Websocket from 'react-websocket';
import appConstants from '../../constants/appConstants';
import Collection from './collection';
import {urls} from '../../constants/urls';
import {getLayoutForCollection} from '../../util/layouts';
import SettingsPanel from '../settingsPanel/SettingsPanel';
import SearchPanel from '../searchPanel/SearchPanel';
import HeaderContainer from '../../containers/HeaderContainer';
import NotificationContainer from '../../containers/NotificationContainer';
import layoutConstants from '../../constants/layout';
import MessageBox from './../messageBox/MessageBox';
import styles from './collections.module.scss';
import * as d3 from 'd3';
import {
  findCollectionsFromID,
  findCollectionRelativesFromID,
  getCoordinates,
  calculateWidth,
  getAlternativesInCollection,
  getCollectionsByTag,
  nameExists,
  handleNameCounter
} from '../../util/collections';
import {
  findAlternativeRelativesFromID,
} from '../../util/alternatives';
import throttle from 'lodash/throttle';
import {toast} from 'react-toastify';

const time = new Date().getHours() + ':' + new Date().getMinutes() + ':' + new Date().getSeconds();

/**
 * @class Collections
 * @extends React.PureComponent
 * @description this is the presentational component of the all the collections. it renders all the individual collection components.
 */
class Collections extends PureComponent {
  
  /**
   * @constructor
   * @param {object} props 
   */
  constructor(props) {
    super(props);

    this.state = {
      shouldWarn: false,
      confirmAction: null,
      warningMessage: '',
      matchNumOfColums: 0,
      scrollX: 0,
      editingNames: false,
      editName: '',
      isMultipleInputs: false,
    }

    this.selectedAlts = [];
    this.selectedCols = [];
  }

  /**
   * @description traverse each collection in collections, wrap it in a *<div>*, then assign it a **key/id** and its associated alternatives
   * @param {object} collections - collection components
   * @param {object} layouts - layout object (ReactGridLayout)
   * @param {object} props - additional props to send to collection
   * @returns {ReactPureComponent} <Collection>
   */
  renderCollection = (collections, layouts, props={}) => (
    collections.map((collection) => {
      return (
        <div 
          key={collection.get('viewId')} 
          data-grid={getLayoutForCollection(layouts, collection.get('viewId')).toJS()}>
          <Collection
            key={collection.get('viewId')}
            id={collection.get('refId')}
            layouts={layouts}
            collection={collection}
            altsInCollection={getAlternativesInCollection(collection, this.props.alternatives.get('alternatives'))}
            isDraggable={true}
            isResizable={true}
            layoutForCollection={getLayoutForCollection(layouts, collection.get('viewId'))}
            {...props}
          />
        </div>
      );
    })
  )

  /**
   * updates the collection layouts when triggered
   * @param {object} layout
   */
  onLayoutChange = (layout) => {
    this.props.sendMessage(
      this.refWebSocket, {layout: layout, type: layoutConstants.get("LAYOUT_UPDATE_MESSAGE_TYPE")}
    );
  }

  sendMessage = (data) => {
    this.props.sendMessage(this.refWebSocket, data);
  }

  /**
   * @function handleKeyEvents
   * @description responds to key stroke events with associated copy, clone or move functions
   * @memberof Collections
   * @param {event} e - keyboard event
   */
  handleKeyEvents = (e) => {
    

    if (e.altKey && e.keyCode === 67) {
      console.log('you have pressed ALT+C ');
      this.props.clearAltsClipboard();
      const selectedAlternatives = this.props.alternatives.get('alternatives').filter((alternative) => alternative.get('isSelected')).toJS();
      
      if (selectedAlternatives.length > 0) {
        this.props.alternatives.get('alternatives').filter((alternative) => alternative.get('isSelected'))
          .map((selectedAlternative) => {
            this.props.clipAlternatives(selectedAlternative.get('viewId'), selectedAlternative.get('refId'));
            this.props.successMessage(toast.success('alternative cliped'), 'alternative cliped ' + time);
          });
      } else {
        this.props.warningMessage(toast.warning('Please select an alternative'), 'please select an alternative ' + time);
      }
      
    }

    if (e.altKey && e.keyCode === 88) {
      console.log('you have pressed ALT+X ');
      this.props.clearAltsClipboard();
      const selectedAlternatives = this.props.alternatives.get('alternatives')
        .filter((alternative) => alternative.get('isSelected'));
      
        if (selectedAlternatives.length > 0) {
        this.props.alternatives.get('alternatives').filter((alternative) => alternative.get('isSelected'))
          .map((selectedAlternative) => {
            this.props.clipAlternatives(selectedAlternative.get('viewId'), selectedAlternative.get('refId'));
            this.props.cutAlternativesFromCollection(selectedAlternative.get('viewId'));
          });
      } else {
        this.props.warningMessage(toast.warning('Please select an alternative'), 'please select an alternative ' + time);
      }
      
    }

    if (!e.shiftKey && e.altKey && e.keyCode === 8) {
      console.log('you have pressed ALT+BACKSPACE/DELETE');
      this.props.clearAltsClipboard();

      const selectedAlternatives  = this.props.alternatives.get('alternatives')
        .filter((alternative) => alternative.get('isSelected'));
      
      if (selectedAlternatives.size > 0) {
        this.props.removeAlternativesFromCollection(selectedAlternatives);
      } else {
        this.props.warningMessage(toast.warning('Please select an alternative'), 'please select an alternative ' + time);
      }
    }

    if (e.shiftKey && e.altKey && e.keyCode === 8) {
      console.log('you have pressed SHIFT+ALT+BACKSPACE/DELETE');
      const selectedAlternatives = this.props.alternatives.get('alternatives')
        .filter((alternative) => alternative.get('isSelected'));

      if (selectedAlternatives.size > 0) {
        this.setState({
          shouldWarn: true,
          warningMessage: 'Do you want to delete all selected alternative views and their underlying alternative?',
          confirmAction: this.handleAlternativeDelete
        });
      } else {
        this.props.warningMessage(toast.warning('Please select an alternative'), 'please select an alternative ' + time);
      }
    }

    if (e.altKey && e.keyCode === 86) {
      console.log('you have pressed ALT+V');
      const alts = this.props.alternatives.get('clippedAlternatives').map((alt) => alt.get('viewId')).toJS();
      const cols = this.props.collections.get('collections').filter((collection) => collection.get('isSelected')).map((col) => col.get('viewId')).toJS();
      this.props.refAlternatives(alts, cols);
    }

    if (e.altKey && e.keyCode === 66) {
      console.log('you have pressed ALT+B');
      const alts = this.props.alternatives.get('clippedAlternatives').map((alt) => alt.get('viewId')).toJS();
      const cols = this.props.collections.get('collections').filter((collection) => collection.get('isSelected')).map((col) => col.get('viewId')).toJS();
      this.props.cloneAlternatives(alts, cols);
    }

    if (e.ctrlKey && e.keyCode === 67) {
      console.log('you have pressed CTRL+C');
      this.props.clearColsClipboard();
      const selectedCollections = this.props.collections.get('collections').filter((collection) => collection.get('isSelected')).toJS();
      
      if (selectedCollections.length > 0) {
        this.props.collections.get('collections').filter((collection) => collection.get('isSelected'))
          .map((selectedCollection) => {
            try {
              const alternatives = findCollectionsFromID(this.props.collections.get('collections'), selectedCollection.get('viewId')).get('altsid');
              const layout = getLayoutForCollection(this.props.layouts, selectedCollection.get('viewId'));
              this.props.clipCollection(selectedCollection.get('viewId'), selectedCollection.get('refId'), alternatives, layout);
              this.props.successMessage(toast.success('collection copied'), 'collection copied' + time);
            } catch (e) {
              console.error(e.message);
            }
          }); 
      } else {
        this.props.warningMessage(toast.warning('Please select a collection'), 'please select a collection ' + time);
      }
      
    
    }

    if (e.ctrlKey && e.keyCode === 88) {
      console.log('you have pressed CTRL+X');

      this.props.clearColsClipboard();
      const selectedCollections = this.props.collections.get('collections').filter((collection) => collection.get('isSelected'));
      
      if (selectedCollections.size > 0) {
        this.props.collections.get('collections').filter((collection) => collection.get('isSelected'))
          .map((selectedCollection) => {
            try {
              const alternatives = findCollectionsFromID(this.props.collections.get('collections'), selectedCollection.get('viewId')).get('altsid');

              alternatives.toJS().map((alt) => {
                return this.props.deselectAlternative(alt.viewId);
              });

              const layout = getLayoutForCollection(this.props.layouts, selectedCollection.get('viewId'));
              this.props.clipCollection(selectedCollection.get('viewId'), selectedCollection.get('refId'), alternatives, layout);
              this.props.deselectCollection(selectedCollection.get('viewId'));
              this.props.deleteCollections(selectedCollection.get('viewId'));
            } catch (e) {
              console.error(e.message);
            }
          });
      } else {
        this.props.warningMessage(toast.warning('Please select a collection'), 'please select a collection ' + time);
      }
      
    }

    if(!e.shiftKey && e.ctrlKey && e.keyCode === 8) {
      console.log('you have pressed CTRL+BACKSPACE/DELETE');
      this.props.clearColsClipboard();

      const selectedCollections = this.props.collections.get('collections')
        .filter((collection) => collection.get('isSelected')).map((collection) => collection.get('viewId'));
      
      if (selectedCollections.size > 0 ) {
        selectedCollections.map((collection) => {
          this.props.deleteCollections(collection);
        });
      } else {
        this.props.warningMessage(toast.warning('Please select a collection'), 'please select a collection ' + time);
      }
    }

    if (e.shiftKey && e.ctrlKey && e.keyCode === 8) { 
      console.log('you have pressed SHIFT+CTRL+BACKSPACE/DELETE');
      const selectedCollections = this.props.collections.get('collections')
        .filter((collection) => collection.get('isSelected')).map((collection) => collection.get('viewId'));

      if (selectedCollections.size > 0) {
        this.setState({
          shouldWarn: true,
          warningMessage: 'Do you want to delete all selected collection views and their underlying collection?',
          confirmAction: this.handleCollectionDelete,
        });
      } else {
        this.props.warningMessage(toast.warning('Please select a collection'), 'please select a collection ' + time);
      }
      
    
    }

    if (e.ctrlKey && e.keyCode === 86) {
      console.log('you have pressed CTRL+V');
      
      if (this.props.collections.get('clippedCollections').size > 0 ) {
        const copyCollections = this.props.collections.get('clippedCollections').map((collection) => {
          return { layout: collection.get('layout').toJS(), viewId: collection.get('viewId') };
        });
        this.props.copyWithRefCollection(copyCollections.toJS());
      } else {
        this.props.warningMessage(toast.warning('Please copy a collection first'), 'please copy a collection first ' + time);
      }
    }

    if (!e.shiftKey && e.ctrlKey && e.keyCode === 66) {
      console.log('you have pressed CTRL+B');

      if (this.props.collections.get('clippedCollections').size > 0) {
        const cloneCollections = this.props.collections.get('clippedCollections').map((collection) => {
          return { layout: collection.get('layout').toJS(), viewId: collection.get('viewId') };
        });
        this.props.cloneCollection(cloneCollections.toJS());
      } else {
        this.props.warningMessage(toast.warning('Please copy a collection first'), 'please copy a collection first ' + time);
      }
    }

    if (e.shiftKey && e.ctrlKey && e.keyCode === 66) {
      console.log('you have pressed SHIFT+CTRL+B');

      if (this.props.collections.get('clippedCollections').size > 0) {
        const cloneCollections = this.props.collections.get('clippedCollections').map((collection) => {
          return { layout: collection.get('layout').toJS(), viewId: collection.get('viewId') };
        });

        this.props.deepCloneCollection(cloneCollections.toJS());
      } else {
        this.props.warningMessage(toast.warning('Please copy a collection first'), 'please copy a collection first ' + time);
      }
      
    }

    if (e.shiftKey && e.ctrlKey && e.keyCode === 78) {
      console.log('you have pressed SHIFT+CTRL+N');

      if (this.props.alternatives.get('clippedAlternatives').size > 0) {
        const alts = this.props.alternatives.get('clippedAlternatives').map((alt) => alt.get('viewId')).toJS();

        this.props.cloneAltInNewCollection(alts);
      } else {
        this.props.warningMessage(toast.warning('Please copy an alternative first'), 'please copy an alternative first ' + time);
      }
      
    }

  }

  handleCollectionDelete = (e) => {
      this.props.clearColsClipboard();

      const selectedCollections = this.props.collections.get('collections')
         .filter((collection) => collection.get('isSelected')).map((collection) => collection.get('viewId'));

      const colRelList = [];

      selectedCollections.map((selectedCol) => 
         findCollectionRelativesFromID(this.props.collections.get('collections'), selectedCol)
            .map((colRel) => colRelList.push(colRel.get('viewId'))));

      colRelList.concat(selectedCollections.toJS()).map((col) => {
        this.props.deselectCollection(col);
        this.props.deleteCollections(col);
      })

      this.setState({
        shouldWarn: false,
        warningMessage: '',
        confirmAction: null,
      })
  }

  handleAlternativeDelete = (e) => {
    this.props.clearAltsClipboard();

    const selectedAlternatives  = this.props.alternatives.get('alternatives')
      .filter((alternative) => alternative.get('isSelected'))

    const altRelList = [];
      
    selectedAlternatives.map((alt) => altRelList.push(alt));

    selectedAlternatives.map((selectedAlt) => 
        findAlternativeRelativesFromID(this.props.alternatives.get('alternatives'), selectedAlt.get('viewId'))
          .map((altRel) => altRelList.push(altRel)));

    this.props.removeAlternativesFromCollection(altRelList);

    this.setState({
      shouldWarn: false,
      warningMessage: '',
      confirmAction: null,
    })
  }

  /**
   * @param {object} ev: the event object
   */
  handleDoubleClick = (ev) => {
    console.log(this.props.settings.get('showSettings'))
    if (!this.props.header.get('headerShow') && !this.props.settings.get('showSettings')) {
      const newPoint = getCoordinates(
        ev.clientX - 12, // some fine tuning
        ev.clientY - 48,
      );

      this.props.createNewCollection(newPoint.x, newPoint.y);
    }
  }

  handleContextMenu = (event) => {
    event.preventDefault();
    this.props.showHeader(event.pageX, event.pageY);
  }


  getRefId = (filterdCollections) => {
      for(let collection of filterdCollections)
      {
        return collection.get('refId');
      }
  }

  hideHeader = () => {
    let filterdCollections = this.props.collections.get('collections').filter((col) => col.get('isEditingName'));
    
    if (filterdCollections.size > 0) {
      const refId = this.getRefId(filterdCollections);
      this.props.isEditingCollectionsName(refId);
      this.props.removeFocus();
    }

    if (this.props.header.get('headerShow')) {
      this.props.hideHeader();
      this.props.hideSinkList();
    }
  }

  /**
   * @function loadOptions
   * @description loads the destination options that *should* be available to the collection
   * @memberof Collection
   * @returns {void}
   */
  loadOptions = (viewId) => {
    return this.props.collections.get('collections')
      .filter((collection) => collection.get('viewId') !== viewId)
        .map((collection) => {
          return {'label': collection.get('collectionName'), collectionName:collection.get('collectionName')+"-"+collection.get('collectionNameView') , 'value': collection.get('viewId'), '__isNew__': false};
        });
  }

  cloneAlternativesFromTaggedCollections = (tagName, collectionId) => {
        const collectionsToCopy = getCollectionsByTag(this.props.collections.get('collections'), tagName);

        collectionsToCopy.map(collection => {
          const col = findCollectionsFromID(this.props.collections.get('collections'), collection.get('viewId'));
          const alts = col.get('altsid').toJS();
          this.props.cloneAlternatives(alts, [collectionId]);  
        });
  }

  hoverAlternative = (viewId, refId) => {
    
    const isSelected = this.props.alternatives.get('alternatives')
    .find(alt => alt.get('viewId') === viewId).get('isSelected');
    
    if (isSelected) {
      d3.selectAll(".alt-ref-" + refId)
        .style('border', '1.5px')
        .style('border-style', 'solid')
        .style('border-color', styles.colorHoveredSelected)
        .style('border-radius', '7px')
        .style('-webkit-box-shadow', '0px 0px 5px ' + styles.colorHoveredSelected);
  
      d3.selectAll(".pc-ref-" + refId)
        .style('stroke', styles.colorHoveredSelected)
        .style('opacity', 1);

      d3.selectAll(".tableList-ref-" + refId)
        .style('background-color', styles.colorHoveredSelected)
      
      } else if (!isSelected && this.selectedAlts.includes(refId)) {
        
      d3.selectAll(".alt-ref-" + refId)
        .style('border', '1.5px')
        .style('border-style', 'solid')
        .style('border-color', styles.colorHoveredRelated)
        .style('border-radius', '7px')
        .style('-webkit-box-shadow', '0px 0px 5px ' + styles.colorHoveredRelated);
  
      d3.selectAll(".pc-ref-" + refId)
        .style('stroke', styles.colorHoveredRelated)
        .style('opacity', 1);

      d3.selectAll(".tableList-ref-" + refId)
        .style('background-color', styles.colorHoveredRelated)

      } else {
        
      d3.selectAll(".alt-ref-" + refId)
        .style('border', '1.5px')
        .style('border-style', 'solid')
        .style('border-color', styles.colorHovered)
        .style('border-radius', '7px')
        .style('-webkit-box-shadow', '0px 0px 5px ' + styles.colorHovered);
  
      d3.selectAll(".pc-ref-" + refId)
        .style('stroke', styles.colorHovered)
        .style('opacity', 1);

      d3.selectAll(".tableList-ref-" + refId)
        .style('background-color', styles.colorHovered)   
      }
  }

  dehoverAlternative = (viewId, refId) => {

    const isSelected = this.props.alternatives.get('alternatives')
    .find(alt => alt.get('viewId') === viewId).get('isSelected');
    
    if (isSelected) {
      d3.selectAll(".alt-ref-" + refId)
        .style('border', '1.5px')
        .style('border-style', 'solid')
        .style('border-color', styles.colorSelectedRelated)
        .style('border-radius', '7px')
        .style('-webkit-box-shadow', '0px 0px 5px ' + styles.colorSelectedRelated);

      d3.selectAll(".pc-ref-" + refId)
        .style('stroke', styles.colorSelectedRelated)
        .style('opacity', 0.7);

      d3.selectAll(".tableList-ref-" + refId)
        .style('background-color', styles.colorSelectedRelated);

        this.props.alternatives.get('alternatives').filter(alt => alt.get('isSelected'))
        .map(alt => {
          d3.select(".alt-view-" + alt.get('viewId'))   
          .style('border', '1.5px')
          .style('border-style', 'solid')
          .style('border-color', styles.colorSelected)
          .style('border-radius', '7px')
          .style('-webkit-box-shadow', '0px 0px 5px ' + styles.colorSelected);

          d3.select(".pc-view-" + alt.get('viewId'))
          .style('stroke', styles.colorSelected)
          .style('opacity', 1);

          d3.select(".tableList-view-" + alt.get('viewId'))   
            .style('background-color', styles.colorSelected);
        });
    } else if (!isSelected && this.selectedAlts.includes(refId)) {
      d3.selectAll(".alt-ref-" + refId)
        .style('border', '1.5px')
        .style('border-style', 'solid')
        .style('border-color', styles.colorSelectedRelated)
        .style('border-radius', '7px')
        .style('-webkit-box-shadow', '0px 0px 5px ' + styles.colorSelectedRelated);

      d3.selectAll(".pc-ref-" + refId)
        .style('stroke', styles.colorSelectedRelated)
        .style('opacity', 0.7);

      d3.selectAll(".tableList-ref-" + refId)
        .style('background-color', styles.colorSelectedRelated);
    
      this.props.alternatives.get('alternatives').filter(alt => alt.get('isSelected'))
        .map(alt => {
          d3.select(".alt-view-" + alt.get('viewId'))   
          .style('border', '1.5px')
          .style('border-style', 'solid')
          .style('border-color', styles.colorSelected)
          .style('border-radius', '7px')
          .style('-webkit-box-shadow', '0px 0px 5px ' + styles.colorSelected);

          d3.select(".pc-view-" + alt.get('viewId'))
            .style('stroke', styles.colorSelected)
            .style('opacity', 1);

          d3.select(".tableList-view-" + alt.get('viewId'))   
            .style('background-color', styles.colorSelected);
        });
    } else if (!isSelected && !this.selectedAlts.includes(refId) ) {
        d3.selectAll(".alt-ref-" + refId)
          .style('border', null)
          .style('border-style', null)
          .style('border-color', null)
          .style('border-radius', null)
          .style('-webkit-box-shadow', null);
    
        d3.selectAll(".pc-ref-" + refId)
          .style('stroke', styles.colorUnselected)
          .style('opacity', 0.3);
    
        d3.selectAll(".tableList-ref-" + refId)
          .style('background-color', '#ffffff');
      }
  }

  /**
   * handleSearch finds collection by name
   * @param {String} searchText
   */
  handleSearch = (searchText) => {
    let trimmedText = searchText.replace(/ /g, '');
    const arrayOfStrings = trimmedText.split(",");
    let collection = null;
    let foundAll = true;
    let namesMissMatch = [];
    
    for(let i = 0; i < arrayOfStrings.length; i++) {
      collection = this.props.collections.get('collections')
        .find(col => col.get("collectionName").toLowerCase() === arrayOfStrings[i].toLowerCase());        
        
        if (collection) {
          this.props.selectCollection(collection.get("viewId"));
        } else {
          foundAll = false;
          namesMissMatch.push(arrayOfStrings[i]);
          this.props.searchedCollectionFailed();
        }
    }

    if (!foundAll) {
      this.props.searchedCollectionFailed(namesMissMatch)
    } else {
      this.props.searchedCollectionSuccess();
      this.props.toggleSearch();
    }
  }


  updateLayoutForCollection = (layout) => {
    let layoutId = layout.get('i');
    this.props.updateLayoutForCollection(layoutId);
  }


  changeMultipleName = (object) => {

    let collectionsWithRef =  this.props.collections.get('collections').filter(col => col.get("refId") === object.refId);
    if (collectionsWithRef.size > 1) {
      for (let collection of collectionsWithRef) {
        if (collection.get("isEditingName")) {
          let selectedElement = document.getElementById(object.viewId);
          
          if (collection.get('viewId') !== object.viewId) {
            let refElement = document.getElementById(collection.get('viewId'));
  
            if(refElement !== null) {
                refElement.value = selectedElement.value
            }
          }
        }
      }
      
      if(!this.state.isMultipleInputs) {
        this.setState({
          isMultipleInputs: true,
        });
      }
    } else {
      // console.log("only one")
    }
  }

  checkNameExists = (name, unquieCollections)  => {
    let counter = 0;
    let compareNumber =  0;

    for (let collection of unquieCollections) {
      let collectionSubname = collection.get("collectionName").substring(0, name.length + 1);
      if (collection.get("collectionName") === name) {
        counter++;
      } else if (collectionSubname === name + ":")
      {      
        counter++;
        let numberAsString =  collection.get("collectionName").split(":")
        let integer = parseInt(numberAsString[1], 10);

        if (compareNumber <= integer) {
          compareNumber = integer;
        }
      }
    }
    if(compareNumber > counter) {
      return compareNumber + 1;
    }
    return counter;
  }



  updateMultipleNames = (object) => {
    let refIdsCollection = this.props.collections.get("collections").filter((col) => col.get('refId') === object.collection.get('refId'))
    if (refIdsCollection !== null && refIdsCollection.size > 1) {
        for (let collection of refIdsCollection) {
          try {
              let newCollection = collection.toJS();
              newCollection.collectionName = object.name + "-" +  newCollection.collectionNameView;
              this.props.updateCollection(newCollection);
            } catch(err) {
              console.log(err)
            } finally {
              this.props.editNameForCollection(collection.get('viewId'), object.name);
            } 
        }
        this.setState({
          isMultipleInputs: false,
        });
    } else {
      console.log("has no ref")
    }
  }

  namesExists = (name, collection) => {
     return nameExists(name, this.props.collections.get("collections"), collection)
  }

  showWarningMessage = () => {
    this.props.warningMessage(toast.warning('Name already exists'), 'Name already exists' + time);
  }

  handleNameExists = (collection, name) => {
    this.props.warningMessage(toast.warning(`Collection ${name} already exists`), 'Name already exists' + time);
    let nameCounter =  handleNameCounter(this.props.collections.get("collections"), name, collection);
    return nameCounter;
  }

  nameHasTheSameValue = (refId) => {
    this.props.warningMessage(toast.warning(`Collection name did not change`), 'collection name did not change' + time);
    this.props.isEditingCollectionsName(refId);
  }

 
  updateAspectRatio = (object) => {
  
    let copyArray = [...this.props.layouts.get('gridLayout').toJS()]
    let newArray = copyArray.map((layout) => {
      if(layout.i === object.viewId)
      {
        layout.w = object.width;
        layout.h = object.height;
        layout.isResizable = object.isResizable
        return layout
      }
      return layout

    })

    this.props.updateMaxColumns(newArray)
  }

  updateUnlockAspectRatio = (object) => {
    let copyArray = [...this.props.layouts.get('gridLayout').toJS()];
    let newArray = copyArray.map((layout) => {
      if( layout.i === object.viewId) {
        layout.isResizable = object.isResizable;
        return layout;
      }
      return layout

    })

    this.props.updateMaxColumns(newArray)
  }

  handleDeleteCancel = () => {
    this.setState({
        shouldWarn: false,
        warningMessage: "",
        confirmAction: null
    })
}

  
  /**
  * @returns {ReactPureComponent}
  */
  render() {
      const width = calculateWidth(
        this.props.layouts.get('maxColumns'),
        layoutConstants.get('MARGIN'),
        layoutConstants.get('CELL_DIM'),
        layoutConstants.get('BUFFER')
      );

      const collectionsDivStyle = {
        width:  window.innerWidth+ 'px',
        minWidth: '100%',
        height: window.innerHeight + 'px',
        minHeight: '100%',
      }

      this.selectedAlts = [...new Set(this.props.alternatives.get('alternatives').filter(alt => alt.get('isSelected')).map(alt => alt.get('refId')).toJS())];
      this.selectedCols = [...new Set(this.props.collections.get('collections').filter(col => col.get('isSelected')).map(col => col.get('refId')).toJS())];
    return (
      <div
        ref={this.mainRef}
        onKeyDown={this.handleKeyEvents}
        onDoubleClick={this.handleDoubleClick}
        tabIndex="0"
        style={collectionsDivStyle}
        onContextMenu={this.handleContextMenu}
        onClick={this.hideHeader}
        className={styles["container"]}
      >

        <HeaderContainer  
          deselectAllCollections={this.props.deselectAllCollections}
          deselectAllAlternatives={this.props.deselectAllAlternatives}
        />
        <NotificationContainer />

        <ReactGridLayout
        className={styles["gridLayout"]}
          cols = {this.props.layouts.get('maxColumns')}
          rowHeight={60}
          layout={this.props.layouts.get('gridLayout').toJS()}
          width={width}
          isDraggable
          isResizable
          onLayoutChange={this.onLayoutChange}
          onResizeStop = {
            throttle(
              this.props.updateMaxColumns.bind(
                this.props.layouts.get('gridLayout')
              ))
          }
          onDragStop = {
            throttle(this.props.updateMaxColumns.bind(
              this.props.layouts.get('gridLayout')
              ))
          }
          compactType={this.props.settings.get('collectionCompactionType') !== 'none' ? this.props.settings.get('collectionCompactionType') : null}
          preventCollision={this.props.settings.get('preventCollision')}
          draggableHandle=".drag-handle"
        >
          {this.renderCollection(
            this.props.collections.get('collections').filter((collection) => (collection.get('isSinked') === false)),
            this.props.layouts,
            {
              hoverAlternative: this.hoverAlternative,
              dehoverAlternative: this.dehoverAlternative,
              sendMessage: this.sendMessage,
              loadOptions: this.loadOptions,
              isMultipleInputs: this.state.isMultipleInputs,
              cloneAlternativesFromTaggedCollections: this.cloneAlternativesFromTaggedCollections,
              updateLayoutForCollection: this.updateLayoutForCollection,
              updateAspectRatio: this.updateAspectRatio.bind(this),
              updateUnlockAspectRatio: this.updateUnlockAspectRatio.bind(this),
              updateMultipleNames: this.updateMultipleNames.bind(this),
              createNewCollection: this.props.createNewCollection,
              deleteCollections: this.props.deleteCollections,
              selectCollection: this.props.selectCollection,
              deselectCollection: this.props.deselectCollection,
              selectAlternative: this.props.selectAlternative,
              deselectAlternative: this.props.deselectAlternative,
              deselectAllAlternatives: this.props.deselectAllAlternatives,
              hoverAlternativeSpecial: this.props.hoverAlternativeSpecial,
              cloneAlternatives: this.props.cloneAlternatives,
              toggleSink: this.props.sinkCollection,
              toggleViewMode: this.props.toggleViewMode,
              toggleAltView: this.props.toggleAltView,
              removeAlternativesFromCollection: this.props.removeAlternativesFromCollection,
              editNameForCollection: this.props.editNameForCollection,
              isEditingCollectionsName: this.props.isEditingCollectionsName,
              nameExists: this.namesExists.bind(this),
              handleNameExists: this.handleNameExists.bind(this), 
              restoreAlternative: this.props.restoreAlternative,
              updateCollection: this.props.updateCollection,
              saveLayoutObject: this.props.saveLayoutObject,
              setCollectionDestination: this.props.setCollectionDestination,
              setCollectionMode: this.props.setCollectionMode,
              updateCollectionTags: this.props.updateCollectionTags,
              alternatives: this.props.alternatives,
              layouts: this.props.layouts,
              selectedAlts: this.selectedAlts,
              selectedCols: this.selectedCols,
              changeMultipleName: this.changeMultipleName.bind(this),
              nameHasTheSameValue: this.nameHasTheSameValue.bind(this),
              removeFocus: this.props.removeFocus,
              shouldRemoveFocus: this.props.collections.get('shouldRemoveFocus'),
            }
          )}
        </ReactGridLayout>
        <Websocket
          url={urls.get('websocketURL')+"?projectId="+ sessionStorage.getItem(appConstants.get('PROJECT_ID'))+ "&userId="+ localStorage.getItem(appConstants.get('USER_ID'))}
          onMessage={this.props.onMessageRecieved}
          onOpen={this.props.openWebSocketConnection}
          ref={Websocket => this.refWebSocket = Websocket}
        />
        {this.props.settings.get('showSettings') ? 
          <SettingsPanel 
            settings = {this.props.settings}
            toggleSettings = {this.props.toggleSettings}
            setCollisionPrevention ={this.props.setCollisionPrevention}
            setAlternativeSize = {this.props.setAlternativeSize}
            setCompactionType = {this.props.setCompactionType}
            projects={this.props.projects.get('projects')} 
            message="Please select a project: " 
            selectProject={this.props.selectProject}
            createProject={this.props.createProject}
            user={this.props.user.get("user")}  
            selectedProject={this.props.projects.get("selectedProject")}
            /> 
        : null}
        {this.props.search.get('showSearch') ?
          <SearchPanel
            search={this.props.search}
            toggleSearch={this.props.toggleSearch}
            handleSearch={this.handleSearch}
            searchedCollectionFailed={this.props.searchedCollectionFailed}
            searchedCollectionSuccess={this.props.searchedCollectionSuccess}
            emptyMissMatchArray={this.props.emptyMissMatchArray}
          />
          : null
        }
       
        {this.state.shouldWarn ? <MessageBox handleDeleteCancel={this.handleDeleteCancel} confirmAction={this.state.confirmAction} message={this.state.warningMessage}/> : null} 
      </div>
    );
  }
}

export default Collections;
