import appConstants from '../constants/appConstants';

/**
 * sends the success message to redux store
 * @param {object} payload
 * @returns {object} : action object
 */
export const successMessage = (id, message) => ({
  type: appConstants.get('HANDLE_SUCCESS'),
  payload: {
    id,
    message,
  },
});

/**
 * sends the warning message to redux store
 * @param {object} payload
 * @returns {object} : action object
 */
export const warningMessage = (id, message) => ({
  type: appConstants.get('HANDLE_WARNING'),
  payload: {
    id,
    message,
  },
});

/**
 * sends the error message to redux store
 * @param {object} payload
 * @returns {object} : action object
 */
export const errorMessage = (id, message) => {
  return ({
    type: appConstants.get('HANDLE_ERROR'),
    payload: {
      id,
      message,
    }
  })
}


/**
 * toggles message history pane view
 * @param {object} payload
 * @returns {object} : action object
 */
export const toggleHistory = (payload) => {
  return ({
    type: appConstants.get('TOGGLE_HISTORY'),
    payload,
  });
};
