import React, { Component } from 'react';
import { connect } from 'react-redux';
import Alternatives from '../components/alternatives/Alternatives';
import {renderIf} from '../util/util';

class AlternativeContainer extends Component {
    render() {
        return (
            renderIf(
                this.props.alternativeImageSize,
                <Alternatives {...this.props} />
            )
        );
    }
}

const mapStateToProps = (state) => ({
    alternativeImageSize: state.settings.get('alternativeImageSize'),
  })


export default connect(
    mapStateToProps,
)(AlternativeContainer);