/**
 * remove an element (with id = queryId) from the queries list
 * @param {ImmutableList} queries
 * @param {string} queryId
 * @return {ImmutableList}
 */
export const fetchQueriesNotID = (queries, queryId) => (
    queries.filter((query) => query.get('id') !== queryId)
  );

/**
 * remove an element (with id = brushId) from the brushes list
 * @param {ImmutableList} brushes
 * @param {string} brushId
 * @return {ImmutableList}
 */
export const fetchBrushesNotID = (brushes, brushId) => (
  brushes.filter((brush) => brush.get('id') !== brushId)
);