import React, {PureComponent} from 'react';
import appStyles from './App.module.scss';
import CollectionContainer from '../../containers/CollectionContainer';
import appConstants from '../../constants/appConstants';
import '../../../node_modules/react-grid-layout/css/styles.css';
import '../../../node_modules/react-resizable/css/styles.css';

/**
 * main app entry point
 */
export default class App extends PureComponent {

  /**
   * fetch layout when component mounts
   * this triggers a chain reaction and fetches
   * collections and alternatives as well
   */

  
  componentDidMount() {
    const userLoggedIn = localStorage.getItem(appConstants.get("USER_ID"));
    const projectID = sessionStorage.getItem(appConstants.get("PROJECT_ID"));
    const projectFetched = this.props.projects.get('projectFetched');

    if (userLoggedIn && projectID && !projectFetched ) {
      this.props.fetchProject();
    }   
  }


  /**
   * The component should render collections container, which
   * contains everything else in the app
   * @return {ReactComponent}
   */
  render() {
    return (
      <div className={appStyles.App}>

        <div className={appStyles.canvas}>
          <div className={appStyles.container}>

            <CollectionContainer />
          </div>
        </div>
      </div>
    );
  }
}