import appConstants from '../constants/appConstants';
import Immutable from 'immutable';

const initialState = Immutable.fromJS({
  showSettings: false,
  alternativeImageSize: 180,
  collectionCompactionType: 'none',
  preventCollision: true,
});

export default (state = initialState, action={}) => {
  switch (action.type) {
    case appConstants.get("FETCHING_PROJECT"):
      return state
              .set('showSettings', false)
    case appConstants.get('FETCH_SETTINGS'):
      return state
        .set('alternativeImageSize', Immutable.fromJS(action.payload.alternativeImageSize))
        .set('collectionCompactionType', Immutable.fromJS(action.payload.collectionCompactionType))
        .set('preventCollision', Immutable.fromJS(action.payload.preventCollision));

    case appConstants.get('TOGGLE_SETTINGS'): 
      return state.set('showSettings', !state.get('showSettings'));

    case appConstants.get('SET_ALTERNATIVE_SIZE'):
        return state.set('alternativeImageSize', action.payload);

    case appConstants.get('SET_COMPACTION_TYPE'):
        return state.set('collectionCompactionType', action.payload);

    case appConstants.get('SET_COLLISION_PREVENTION'):
        return state.set('preventCollision', action.payload);

  default:
    return state;
  }
};