import memoize from 'lodash/memoize';
import values from "lodash/values";

/**
 * layout for the collection id is determined
 * @param {ImmutableMap} layouts
 * @param {string} collectionId
 * @return {ImmutableMap}
 */
export const getLayoutForCollection = memoize((layouts, collectionId) => (
  layouts.get('gridLayout').find((layout) => layout.get('i') === collectionId)
), (...args) => values(args).join("_"));

/**
 * layout for the collection id is determined
 * @param {ImmutableMap} layouts
 * @param {string} collectionId
 * @return {ImmutableMap}
 */
export const getLayoutForCollectionFromClipboard = memoize((layouts, collectionId) => (
  layouts.get('layoutClipboard').find((layout) => layout.get('i') === collectionId)
), (...args) => values(args).join("_"));

/**
 * removes the layout of a collection from the list
 * @param {ImmutableList} layouts
 * @param {string} collectionId
 * @return {ImmutableList}
 */
export const deleteLayoutForCollection = memoize((layouts, collectionId) => (
  layouts.filter((layout) => (
    layout.get('i') !== collectionId
  ))
), (...args) => values(args).join("_"));

/**
 * compacts the layout list
 * @param {ImmutableList} layouts
 * @return {ImmutableList}
 */
export const compactCollectionLayouts = (gridLayout) => {
  let x = 0;
  let y = 0;
  let layouts = gridLayout.toJS();

  for(var j=0; j<layouts.length; j++) {
    layouts[j].w = 3;
    layouts[j].h = 5;

    if (x < 8) {
      layouts[j].x = x;
      layouts[j].y = y;
      x = x + layouts[j].w;
    } else {
      x = 0;
      layouts[j].x = x;
      layouts[j].y = y;
      x = x + layouts[j].w;
      y = y + layouts[j].h;
    }
  }

  return layouts;
}

export const updateLayoutAddMaxHeightAndWidth = (gridLayout, layoutId) => {
  let layouts = gridLayout.toJS();
  for (let i = 0; layouts.length > i; i++) {
    if (layouts[i].i === layoutId) {
      layouts[i].maxH = 14
      layouts[i].maxW = 6 
    }
  }
  return gridLayout;
}
