import React, {Component} from 'react';
import {connect} from 'react-redux';
import App from '../components/App/App';
import {
  fetchAlternatives,
  fetchCollections,
  fetchLayout,
  fetchProject
} from '../actions/fetchActions';

/**
 * This component connects the App component to the state.
 * TODO: It is a temporary component and needs to be deleted after refactoring.
 */
class AppContainer extends Component {
  /**
   * The component should render the presentational component of App.
   * @return {ReactComponent}
   */
  render() {
    return (
      <App {...this.props} styles={null} />
    );
  }
}

const mapStateToProps = (state) => state;

const mapDispatchToProps = (dispatch) => ({
  fetchAlternatives: () => {
    dispatch(fetchAlternatives());
  },

  fetchCollections: () => {
    dispatch(fetchCollections());
  },

  fetchLayout: () => {
    dispatch(fetchLayout());
  },

  fetchProject: () => {
    dispatch(fetchProject());
  }
});

export default connect(mapStateToProps, mapDispatchToProps)(AppContainer);
