/**
 * Action dispatchers for operations on collection objects
 * @namespace collectionActions
 */

import {urls} from '../constants/urls';
import {
  deleteCollection,
  createCollection,
  addLayoutObject,
  deselectCollection,
  deselectAlternative,
  toggleSink,
  addAlternative,
  editNameForCollection,
  addAlternativeToCollection,
} from './updateActions';
import {
  cloneAlternativesToCollection,
} from './alternativeActions'
import {
  successMessage,
  warningMessage,
  errorMessage,
} from './notificationActions';
import {
  shapeAlternative,
  shapeCollection,
} from '../util/util';
import {
  findCollectionsFromID,
  removeViewName
} from '../util/collections';
import {
  findAlternativesFromID,
} from '../util/alternatives';
import Immutable from 'immutable';
import {toast} from 'react-toastify';
import {batch} from 'react-redux';

const time = new Date().getHours() + ':' + new Date().getMinutes() + ':' + new Date().getSeconds();

/**
 * @function deleteCollections
 * @description deletes a collection with the given viewID
 * @memberof collectionActions
 * @param {string} collectionId - the viewID of collection
 * @returns {redux-thunk} middleware - fetch call to update the database and then the redux-store
 */
export const deleteCollections = (collectionId) => (dispatch, getState) => {
  return fetch(urls.get('deleteCollection'), {
    method: 'delete',
    headers: {'Content-Type': 'application/json'},
    body: JSON.stringify([collectionId]),
  }).then(() => {
    const collections = getState().collections.get('collections');
    const alternatives = getState().alternatives.get('alternatives');
    const collection = findCollectionsFromID(collections, collectionId);

    collection.get('altsid').map((alt) => {
      const alternative = findAlternativesFromID(alternatives, alt);
      dispatch(deselectAlternative(alternative.get('viewId')));
    });

    batch(() => {
      dispatch(deselectCollection(collectionId));
      dispatch(deleteCollection(collectionId));
      dispatch(successMessage(toast.success('Collection deleted'), 'collection deleted ' + time));
    });
  }).catch((err) => {
    console.log(err);
    dispatch(errorMessage(toast.error('Collection is not deleted'), 'collection not deleted ' + time));
  });
};

/**
 * @function updateCollection
 * @description updates a (whole) collection
 * @memberof collectionActions
 * @param {object} collection - the collection object
 * @returns {redux-thunk} middleware - fetch call to update the database and then the redux-store
 */
export const updateCollection = (collection) => (dispatch) => {
  
  return fetch(urls.get('collections'), {
    method: 'put',
    headers: {'Content-Type': 'application/json'},
    body: JSON.stringify([collection]),
  }).then((data) => {
    if (data.status === 200) {
      dispatch(successMessage(toast.success('Collection updated'), 'collection updated ' + time));
    } else {
      dispatch(warningMessage(toast.warning('Something went wrong'), 'something went wrong ' + time));
    }
  }).catch((err) => {
    dispatch(errorMessage(toast.error('Collection is not updated'), 'collection not updated ' + time));
  });
};

/**
 * @function createNewCollection
 * @description dispatches an action to create a new collection
 * @memberof collectionActions
 * @param {number} x - x coordinate
 * @param {number} y - y coordinate
 * @param {number} w - collection width
 * @param {number} h - collection height
 * @param {array} altsid - array of alternative viewIDs that belong to the collection
 * @param {string} viewId - viewID of the collection
 * @param {string} refId - refID of the collection
 * @param {boolean} isSelected - flag for whether the collection is selected
 * @param {array} tags - tags that the collection has
 * @param {number} viewType - current view type of the collection
 * @param {array} dimOrder - order of dimensions of the alternatives in the collection
 * @param {array} inputs - input dimensions
 * @param {array} outputs - output dimensions
 * @param {array} destinations - destinations for the query operations on this collection
 * @param {object} currentMode - current mode (if any) in the parallel coordinates view
 * @returns {redux-thunk} middleware - fetch call to update the database and then the redux-store
 */ 
export const createNewCollection = (
  x = 0, y = 0, w = 3, h = 5, altsid = [], collectionName = 'col',
  viewId = null, refId = null, isSelected = false, isSinked = false,
  tags = [], viewType = 1, dimOrder, inputs = [], outputs = [], destinations = [], currentMode = {}, isEditingName = false,  collectionNameView = "View(1)") => (dispatch, getState) => {
  
  return fetch(urls.get('collections'), {
    method: 'POST',
    headers: {'Content-Type': 'application/json'},
    body: JSON.stringify({}),
  }).then(response => response.json())
    .then((data) => {

      viewId = data[0].viewId;
      refId = data[0].refId;
      viewType = data[0].viewType;
      tags = data[0].tags;
      dimOrder = getState().scales.get('keySets');
      collectionNameView = data[0].collectionNameView;
    
      collectionName     =  collectionName && collectionName !== 'col' ? collectionName + "-" + collectionNameView : data[0].collectionName + "-" + collectionNameView;

      const layoutObject = Immutable.fromJS(
        {x: x, y: y, w: w, h: h, i: viewId, minW: 3, minH: 5, moved: false, static: false}
      );

      batch(() => {
        dispatch(addLayoutObject(layoutObject));
        dispatch(createCollection(data[0].altsid, collectionName, 
          refId, tags, viewId, isSelected, isSinked,
          viewType, dimOrder, inputs, outputs, destinations, currentMode, isEditingName, collectionNameView));
      });

      return viewId;

    }).then(viewId => {

      if (altsid !== null && altsid !== undefined && altsid.length > 0) {
        dispatch(cloneAlternativesToCollection(altsid, [viewId]));
      }

      if (collectionName !== null && collectionName !== undefined && collectionName.length > 0) {
        batch(() => {
          let nameWithoutView = collectionName
           nameWithoutView    = removeViewName(nameWithoutView)
          dispatch(updateCollection(findCollectionsFromID(getState().collections.get('collections'), viewId).toJS()));
          dispatch(editNameForCollection(viewId,  nameWithoutView))
        });
      }

    }).catch((err) => {
      dispatch(errorMessage(toast.error('Collection is not created'), 'collection is not created' + time));
    });
};



/**
 * @function copyWithRefCollection
 * @description copies an collection as a ref i.e. the new collection as a mirror image of the copied collection
 * @memberof collectionActions
 * @param {array} viewIds - an array of collection viewIDs
 * @param {boolean} isSelected - flag for whether the collection is selected
 * @param {boolean} isSinked - flag for whether the collection is sinked
 * @param {array} tags - tags that the collection has
 * @param {number} viewType - current view type of the collection
 * @returns {redux-thunk} middleware - fetch call to update the database and then the redux-store
 */
export const copyWithRefCollection = (viewIds = null, isSelected = false, isSinked = false, tags = [], viewType = 1, destinations = [], currentMode = {},  isEditingName = false) => (dispatch, getState) => {
  const toastyVar = toast.info("Copying Collection With Ref", {
    toastId: "CopyWithRef",
    autoClose: false,
    closeOnClick: false,
    pauseOnHover: false,
    rtl: true,
    position: 'bottom-center',
    progress: 0.8,
  });
  return fetch(urls.get('collectionCopyWithRef'), {
    method: 'POST',
    headers: {'Content-Type': 'application/json'},
    body: JSON.stringify(viewIds),
  }).then(response => {
    return response.json()
  })
    .then((data) => {

      toast.update(toastyVar, {progress: 0.5});

      const inputs = getState().alternatives.get('alternatives').size > 0 ? getState().alternatives.get('alternatives').toJS()[0]['params'] : {};
      const outputs = getState().alternatives.get('alternatives').size > 0 ? getState().alternatives.get('alternatives').toJS()[0]['outputs'] : {};

      for (let i = 0; i < data.alternatives.length; i++) {
        dispatch(addAlternative(shapeAlternative(data.alternatives[i], inputs, outputs)));
      }
    
      const newCollections = data.collections.map(col => shapeCollection(col, Object.keys(inputs), Object.keys(outputs)));
      for (let i = 0; i < newCollections.length; i++) {    
        const newCollection = newCollections[i];
        let collectionName = newCollection.collectionName;

        let viewId = newCollection.viewId;
        let refId = newCollection.refId; 

        let collectionNameView = newCollection.collectionNameView;
        viewType = newCollection.viewType;
        tags = newCollection.tags;
        let altsid = newCollection.altsid;
        let inputs = newCollection.inputs;
        let outputs = newCollection.outputs;
        let dimOrder = findCollectionsFromID(getState().collections.get('collections'), viewIds[i]['viewId']).get('dimOrder');

        let layout = data.collections[i].layout;
        
        const layoutObject = Immutable.fromJS(
          {x: layout.x + layout.w, y: layout.y, w:layout.w, h: layout.h, i: viewId, minW: 3, minH: 5, moved: false, static: false}
        );

        batch(() => {
          toast.update(toastyVar, {progress: 0.2});

          dispatch(addLayoutObject(layoutObject)); 
          dispatch(createCollection(altsid, collectionName, refId, tags, viewId, isSelected, isSinked, viewType, dimOrder, inputs, outputs, destinations = [], currentMode = {}, isEditingName, collectionNameView));
          dispatch(addLayoutObject(layoutObject)); 

          if (toast.isActive(toastyVar)) {
            toast.update(toastyVar, {
              progress: 0,
              closeOnClick: true,
              autoClose: 1,
            });
          }
          dispatch(successMessage(toast.success('Collection copied'), 'collection copied' + time));
        });
      }
    }).catch((err) => {
      toast.update(toastyVar, {progress: 0, closeOnClick: true, autoClose: 1});
      dispatch(errorMessage(toast.error('collection is not copied'), 'collection is not copied' + time));
    });
};

/**
 * @function cloneAltInNewCollection
 * @description copies an collection as a ref i.e. the new collection as a mirror image of the copied collection
 * @memberof collectionActions
 * @param {number} x - x coordinate
 * @param {number} y - y coordinate
 * @param {number} w - collection width
 * @param {number} h - collection height
 * @param {array} altsid - array of alternative viewIDs that belong to the collection
 * @param {string} viewId - viewID of the collection
 * @param {string} refId - refID of the collection
 * @param {boolean} isSelected - flag for whether the collection is selected
 * @param {array} tags - tags that the collection has
 * @param {number} viewType - current view type of the collection
 * @param {array} dimOrder - order of dimensions of the alternatives in the collection
 * @param {array} inputs - input dimensions
 * @param {array} outputs - output dimensions
 * @param {array} destinations - destinations for the query operations on this collection
 * @param {object} currentMode - current mode (if any) in the parallel coordinates view
 * @returns {redux-thunk} middleware - fetch call to update the database and then the redux-store
 */ 
export const cloneAltInNewCollection = (
  altsToBeCloned = [], x = 0, y = 0, w = 3, h = 5, altsid = [], collectionName = 'col',
  viewId = null, refId = null, isSelected = false, isSinked = false,
  tags = [], viewType = 1, dimOrder, inputs = [], outputs = [], destinations = [], currentMode = {}, isEditingName = false,  collectionNameView = "View(1)") => (dispatch, getState) => {

  if (altsToBeCloned.length > 0) {
    const toastyVar = toast.info("Clone Alternative In a New Collection", {
    toastId: "cloneAltInNewCollection",
    autoClose: false,
    closeOnClick: false,
    pauseOnHover: false,
    rtl: true,
    position: 'bottom-center',
    progress: 0.8,
  });

  return fetch(urls.get('cloneAltInNewCollection'), {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        altsid:altsToBeCloned
      }),
    }).then(response => response.json())
    .then((data) => {
      var collections = data.collections;
      viewId = collections[0].viewId;
      refId = collections[0].refId;
      viewType = collections[0].viewType;
      tags = collections[0].tags;
      dimOrder = getState().scales.get('keySets');
      collectionNameView = collections[0].collectionNameView;
    
      collectionName =  collectionName && collectionName !== 'col' ? collectionName + "-" + collectionNameView : collections[0].collectionName + "-" + collectionNameView;

      const layoutObject = Immutable.fromJS(
        {x: x, y: y, w: w, h: h, i: viewId, minW: 3, minH: 5, moved: false, static: false}
      );

      batch(() => {
        dispatch(addLayoutObject(layoutObject));
        dispatch(createCollection(collections[0].altsid, collectionName, 
          refId, tags, viewId, isSelected, isSinked,
          viewType, dimOrder, inputs, outputs, destinations, currentMode, isEditingName, collectionNameView));
      });

      return data;
    }).then((data) => {
      toast.update(toastyVar, {progress: 0.5});
      let alternatives = data.alternatives;

      alternatives.forEach((datum) => {

        const inputs = getState().alternatives.get('alternatives').toJS()[0]['params'];
        const outputs = getState().alternatives.get('alternatives').toJS()[0]['outputs'];

        const newCollection = shapeCollection(datum.collection, Object.keys(inputs), Object.keys(outputs));

        batch(()=>{
          datum.alts.map((alternative) => {
            dispatch(addAlternative(shapeAlternative(alternative, inputs, outputs)))
          });
        })
        
        datum.alts.map((alt, index) => {
          batch(() => {
            // TODO: fix collection inputs/outputs/dimOrder
            dispatch(addAlternativeToCollection(datum.collectionId, alt.viewId, newCollection.inputs, newCollection.outputs, newCollection.dimOrder));
          });
        });
      });

      return data;   
    }).then((data)=>{
      if (collectionName !== null && collectionName !== undefined && collectionName.length > 0) {
        batch(() => {
          let nameWithoutView = collectionName
           nameWithoutView    = removeViewName(nameWithoutView)
          dispatch(updateCollection(findCollectionsFromID(getState().collections.get('collections'), viewId).toJS()));
          dispatch(editNameForCollection(viewId,  nameWithoutView))
        });
      }

      return data.refs;
    }).then((data) => {
      const inputs = getState().alternatives.get('alternatives').size > 0 ? getState().alternatives.get('alternatives').toJS()[0]['params'] : {};
      const outputs = getState().alternatives.get('alternatives').size > 0 ? getState().alternatives.get('alternatives').toJS()[0]['outputs'] : {};

      for (let i = 0; i < data.alternatives.length; i++) {
        dispatch(addAlternative(shapeAlternative(data.alternatives[i], inputs, outputs)));
      }
    
      const newCollections = data.collections.map(col => shapeCollection(col, Object.keys(inputs), Object.keys(outputs)));
      batch(() => {
        for (let i = 0; i < newCollections.length; i++) {    
          const newCollection = newCollections[i];
          let collectionName = newCollection.collectionName;

          let viewId = newCollection.viewId;
          let refId = newCollection.refId; 

          let collectionNameView = newCollection.collectionNameView;
          viewType = newCollection.viewType;
          tags = newCollection.tags;
          let altsid = newCollection.altsid;
          let inputs = newCollection.inputs;
          let outputs = newCollection.outputs;

          let layout = data.collections[i].layout;
          
          const layoutObject = Immutable.fromJS(
            {x: x, y: y, w: w, h: h, i: viewId, minW: 3, minH: 5, moved: false, static: false}
          );

          dispatch(addLayoutObject(layoutObject)); 
          dispatch(createCollection(altsid, collectionName, refId, tags, viewId, isSelected, isSinked, viewType, dimOrder, inputs, outputs, destinations = [], currentMode = {}, isEditingName, collectionNameView));
          dispatch(addLayoutObject(layoutObject)); 
        } 

        toast.update(toastyVar, {
          progress: 0,
          closeOnClick: true,
          autoClose: 1,
        })
        dispatch(successMessage(toast.success('Alternative(s) cloned in the new collection'), 'alternative(s) cloned in the new collection ' + time));
      });     
    }).catch((err) => {
      console.log(err)
      toast.update(toastyVar, {
        progress: 0,
        closeOnClick: true,
        autoClose: 1,
      })
      dispatch(errorMessage(toast.success('Alternative(s) not cloned in the new collection'), 'alternative(s) not cloned in the new collection ' + time));
    })
  } else {
    dispatch(warningMessage(toast.warning('Please select an alternative'), 'please select an alternative' + time));
  }
};

/**
 * @function cloneCollection
 * @description creates a new collection as a clone i.e. with a new refId
 * @memberof collectionActions
 * @param {array} viewIds - an array of collection viewIDs
 * @param {boolean} isSelected - flag for whether the collection is selected
 * @param {boolean} isSinked - flag for whether the collection is sinked
 * @param {array} tags - tags that the collection has
 * @param {number} viewType - current view type of the collection
 * @returns {redux-thunk} middleware - fetch call to update the database and then the redux-store
 */
export const cloneCollection = (viewIds=null, isSelected = false, isSinked = false, tags = [], viewType = 1, destinations = [], currentMode = {}, isEditingName = false)  => (dispatch, getState) => {

  const toastyVar =  toast.info("Cloning Collection", {
    toastId: "cloningCollection",
    autoClose: false,
    closeOnClick: false,
    pauseOnHover: false,
    progress: 0.8,
    position: 'bottom-center',
    rtl: true,
  });

  return fetch(urls.get('collectionClone'), {
    method: 'POST',
    headers: {'Content-Type': 'application/json'},
    body: JSON.stringify(viewIds),
  }).then(response => response.json())
    .then((data) => {
      toast.update(toastyVar, {
        progress: 0.5
      })
      const inputs = getState().alternatives.get('alternatives').size > 0 ? getState().alternatives.get('alternatives').toJS()[0]['params'] : {};
      const outputs = getState().alternatives.get('alternatives').size > 0 ? getState().alternatives.get('alternatives').toJS()[0]['outputs'] : {};
      
      for (var i = 0; i < data.alternatives.length; i++) {
        dispatch(addAlternative(shapeAlternative(data.alternatives[i], inputs, outputs)));
      }

      toast.update(toastyVar, {progress: 0.2});
    
      const newCollections = data.collections.map(col => shapeCollection(col, Object.keys(inputs), Object.keys(outputs)));

      for (let i = 0; i < newCollections.length; i++) {      
        const newCollection    = newCollections[i];
        let collectionName     = newCollection.collectionName;
        let collectionNameView = newCollection.collectionNameView
        let viewId = newCollection.viewId;
        let refId = newCollection.refId; 
        viewType = newCollection.viewType;
        tags = newCollection.tags;
        let altsid = newCollection.altsid;
       
        let inputs = newCollection.inputs;
        let outputs = newCollection.outputs;
        let dimOrder = findCollectionsFromID(
          getState().collections.get('collections'), viewIds[i]['viewId']).get('dimOrder')

          let layout = data.collections[i].layout;
        const layoutObject = Immutable.fromJS(
          {x: layout.x + layout.w, y: layout.y, w:layout.w, h: layout.h, i: viewId, minW: 3, minH: 5, moved: false, static: false}
        );
        
        batch(() => {
          dispatch(addLayoutObject(layoutObject));
          dispatch(createCollection(altsid, collectionName, refId, tags, viewId, isSelected, isSinked, viewType, dimOrder, inputs, outputs, destinations = [], currentMode = {}, isEditingName, collectionNameView));
          toast.update(toastyVar, {
            progress: 0,
            closeOnClick: true,
            autoClose: 1,
          })
          dispatch(successMessage(toast.success('Collection cloned'), 'collection cloned' + time));
        });
      }
    }).catch((err) => {
      toast.update(toastyVar, {
        progress: 0,
        closeOnClick: true,
        autoClose: 1,
      })
      dispatch(errorMessage(toast.error('Collection is not cloned'), 'collection is not cloned' + time));
    });
};

