/**
 * Action dispatchers for operations on collection objects
 * @namespace fetchActions
 */

import {urls} from '../constants/urls';
import {
  projectLoaded,
  fetchingProject,
  projectFetch,
  updateDeletedProject,
  updateProjectDetails,
} from './updateActions';
import {
  successMessage,
  errorMessage,
} from './notificationActions';
import {fetchLayout} from './fetchActions'
import {toast} from 'react-toastify';
import {batch} from 'react-redux';
import {
  isCreatingProject
} from "../actions/updateActions";

import {
  doesProjectExists
} from '../util/projects';
import appConstants from '../constants/appConstants';


const time = new Date().getHours() + ':' + new Date().getMinutes() + ':' + new Date().getSeconds();

/**
 * @function selectProject
 * @description select a project
 * @memberof projectActions
 * @returns {Promise}
 */
export const selectProject = (project) => (dispatch) => {
  dispatch(fetchingProject());  
  fetch(urls.get('project_select'), {
    method: 'POST',
    headers: {'Content-Type': 'application/json'},
    body: JSON.stringify({
      id: project.id
    })
  }).then((response)=> response.json())
  .then((response) => {
    if (response.success) {
      if (response.currentProject && response.currentProject.id) {
        sessionStorage.setItem(appConstants.get("PROJECT_ID"),response.currentProject.id);
      } 
      batch(() => {
        dispatch(projectLoaded(project));
        dispatch(fetchLayout()); 
      });   
    }      
  }).catch((err) => {
    dispatch(errorMessage(toast.error('Project not selected'), 'project not fetched ' + time));
  })
};

/**
 * @function createProject
 * @description creates a project
 * @memberof projectActions
 * @returns {Promise}
 */
export const createProject = (name,description) => (dispatch, getState) => {
  if(!doesProjectExists(getState().projects.get("projects"), name)){
    dispatch(fetchingProject());
    fetch(urls.get('project_create'), {
      method: 'POST',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify({
        name: name,
        description: description
      })
    }).then((response)=> response.json())
    .then((response) => {
      if (response.db_loaded) {
        if (response.currentProject && response.currentProject.id) {
          sessionStorage.setItem(appConstants.get("PROJECT_ID"), response.currentProject.id);
        }      
        batch(() => {
          dispatch(isCreatingProject());
          // dispatch(projectLoaded());
          dispatch(projectFetch(response));
          dispatch(fetchLayout()); 
          dispatch(successMessage(toast.success("Created project successfully"), "Created project successfully " + time));
        });   
      }      
    }).catch((err) => {
      dispatch(errorMessage(toast.error('Could not create project'), 'Could not create project ' + time));
    })
  }else{
    // dispatch(isCreatingProject());
    dispatch(errorMessage(toast.error('Project already exists'), 'Project already exists ' + time));
  }
 
};

/**
 * @function deleteProject
 * @description deleted project
 * @param {String} projectId
 * @returns {Promise}
 */
export const deleteProject = (projectId) => dispatch => {
  return fetch(urls.get("delete_project"), {
    method: "delete",
    headers: {'Content-Type': 'application/json'},
    body: JSON.stringify([projectId]),
  }) .then((response) => response.json())
  .then((response) => {
    if(response){
      // const projects = getState().collections.get('collections');
      batch(() => {
        dispatch(updateDeletedProject(projectId));
        dispatch(successMessage(toast.success("Deleted project successfully"), "Deleted project successfully " + time));
      }) 
    }
  }).catch((err) => {
    dispatch(errorMessage(toast.error('Could not delete project'), 'Could not delete project ' + time));
  })

}

/**
 * @function editProject
 * @description edits project
 * @param {Object} editElements 
 * @returns {Promise}
 */
export const editProject = (editElements) => (dispatch, getState) => {
  if(!doesProjectExists(getState().projects.get("projects"), editElements.projectName)){
    return fetch(urls.get('update_project'), {
      method: "put",
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify([editElements])
    }).then((response) => response.json())
    .then((response) => {
      if(response){
        batch(() => {
          dispatch(updateProjectDetails(editElements));
          dispatch(successMessage(toast.success("Updated project successfully"), "Updated project successfully " + time));
        })
      }
    }).catch((err)=> {
      dispatch(errorMessage(toast.error('Could not update project'), 'Could not update project ' + time));
    })
  }else{
    dispatch(errorMessage(toast.error('Project already exists'), 'Project already exists ' + time));
  }
}




