import React from 'react';
import {BrowserRouter, Route, Switch} from 'react-router-dom';
import {connect} from 'react-redux';
import AppContainer from '../containers/AppContainer';
import ProjectContainer from '../containers/ProjectContainer';
import LandingPageContainer from '../containers/LandingPageContainer';
import AuthContainer from '../containers/AuthContainer';
import appConstants from '../constants/appConstants';


/**
 * This component contains all the routing details.
 */
class AppRouter extends React.PureComponent {

  constructor(props) {
    super(props)

    this.state = {
      isLoggedIn: false,
    }
  }

  componentDidMount(){
    const userId = localStorage.getItem(appConstants.get('USER_ID'));

    // TODO: add more checks here
    if (userId && userId !== null) {

      this.setState({
        isLoggedIn: true,
      })
    }    
  }
  /**
   * @return {ReactComponent}
   */
  render() {
    // console.log(this.state.isLoggedIn, ' is loggedIn')
    // this.props.history.push for redirecting to another page
    // this.props.history.push('/twitter');

    let routes = (
      <Switch>
        <Route path="/projects" component={ProjectContainer} />
        <Route path="/project" component={AppContainer} />
        <Route path="/signup" component={AuthContainer} />
        <Route path="/login" component={AuthContainer} />
        <Route exact path="/" component={LandingPageContainer} />
      </Switch>
    )

    if (this.props.auth.get("userFetched")) {
      routes = (
        <Switch>
          <Route path="/projects" component={ProjectContainer} />
          <Route path="/project" component={AppContainer} />
          <Route exact path="/" component={LandingPageContainer} />
      </Switch>)
    }

    return (
      <BrowserRouter>
        {routes}
      </BrowserRouter>
    );
  }
}

const mapStateToProps = (state) => state;


export default connect(mapStateToProps, null)(AppRouter);
