import appConstants from '../constants/appConstants';
import layoutConstants from '../constants/layout';
import Immutable from 'immutable';
import {
  deleteLayoutForCollection,
  compactCollectionLayouts
} from '../util/layouts';

const initialState = Immutable.fromJS({
  gridLayout: [],
  layoutClipboard: [],
  layoutsFetched: false,
  maxColumns: layoutConstants.get('DEFAULT_MAX_COLUMNS'),
  
});

export default (state = initialState, action={}) => {
  switch (action.type) {
  case appConstants.get("FETCHING_PROJECT"):
    return state
        .set("layoutsFetched", false)
        .set('gridLayout', Immutable.fromJS([]))
        .set('layoutClipboard', Immutable.fromJS([]))
        .set('maxColumns', Immutable.fromJS(layoutConstants.get('DEFAULT_MAX_COLUMNS')))
  case appConstants.get('FETCH_LAYOUT'):
    return state.set('gridLayout', Immutable.fromJS(action.payload))
      .set('layoutsFetched', true);

  case appConstants.get('UPDATE_MAX_COLUMNS'):
    return state.set('gridLayout', Immutable.fromJS(action.layout))
      .set('maxColumns', action.width)

  case appConstants.get('TOGGLE_DRAGGING'):
    return state.set('dragging', !state.get('dragging'));

  case appConstants.get('DELETE_COLLECTION'):
    return state.set('gridLayout', deleteLayoutForCollection(state.get('gridLayout'), action.payload));

  case appConstants.get('INSERT_LAYOUT'):
    return state.update('gridLayout', (gridLayout) =>
      gridLayout.push(Immutable.fromJS(action.payload)));

  case appConstants.get('SAVE_LAYOUT'):
    return state.update('layoutClipboard', (layoutClipboard) =>
      layoutClipboard.push(Immutable.fromJS(action.payload))
    );

  case appConstants.get('GATHER_ALL_COLLECTIONS'):
    return state.set('gridLayout', Immutable.fromJS(compactCollectionLayouts(state.get('gridLayout'))));
  case appConstants.get("UPDATE_LAYOUTS"):
      // updateLayoutAddMaxHightAndWIdth(state.get('gridLayout'), action.payload)
      return state;
  default:
    return state;
  }
};
