import appConstants from '../constants/appConstants';
import Immutable from 'immutable';

const initialState = Immutable.fromJS({
  keySets: [],
  valueBands: [],
});

export default (state = initialState, action={}) => {

  switch (action.type) {
  case appConstants.get("FETCHING_PROJECT"):
    return state
              .set('keySets', Immutable.fromJS([]))
              .set('valueBands',  Immutable.fromJS([]))
  case appConstants.get('FETCH_KEY_SETS'):
    return state.set('keySets', Immutable.fromJS(action.payload));

  case appConstants.get('FETCH_VALUE_BANDS'):
    return state.set('valueBands', Immutable.fromJS(action.payload));

  case appConstants.get('SET_VALUE_BANDS'):
    return state.setIn(['valueBands', action.payload.dim, action.payload.end], Immutable.fromJS(action.payload.val));

  default:
    return state;
  }
};
