import {
  findAlternativesFromID,
  getParallelDataFromAlternative,
} from './alternatives';

import {
  sliceViewFromName,
} from './collections'

/**
 * used to define default functions in PropTypes
 * Just an empty function
 */
export const emptyFunction = () => {};

/**
 * Retuns what component to render based on the condition
 * @param {boolean} condition: The condition to evaluate
 * @param {ReactPureComponent} Component1: return if condition is true.
 * @param {ReactPureComponent} Component2=null: return if condition is false.
 * @return {ReactPureComponent}
 */
export const renderIf = (condition, Component1, Component2 = null) => (
  condition ? Component1 : Component2
);

/**
 * creates a new id of length at random
 * @param {int} length
 * @return {string} : the Id of a new collection
 */
export const makeId = (length=5) => {
  let text = '';
  const ids = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
  for (let i = 0; i < length; i++) {
    text += ids.charAt(Math.floor(Math.random() * ids.length));
  }
  return text;
};

/**
 * remove a certain field from an array of data objects
 * @param {array} data : array of data objects
 * @param {string} field : name of the field to remove
 * @return {array} new array of data objects
 */
export const removeFieldFromData = (data, field) => {
  const newData = [];

  data.map((d) => {
    const {[field]: foo, ...rest} = d;
    newData.push(rest);
  });

  return newData;
};


/**
 * remove a certain field from an array of data objects
 * @param {object} datum : data object
 * @param {string} field : name of the field to remove
 * @return {array} new array of data objects
 */
export const removeFieldFromDatum = (datum, field) => {
  const {[field]: foo, ...rest} = datum;
  return rest;
};

/**
 * add a certain field to an array of data objects
 * @param {array} data : array of data objects
 * @param {string} field : name of the field to remove
 * @param {object} source : source object to get field value
 * @return {array} new array of data objects
 */
export const addFieldToData = (data, field, source) => {
  const newData = [];
  let parallelData;
  let alternative;

  data.map((d) => {
    alternative = findAlternativesFromID(source, d.id).toJS();
    parallelData = getParallelDataFromAlternative(alternative);
    d[field] = parallelData[0][field];
    newData.push(d);
  });

  return newData;
};

/**
 * add a certain field to an array of data objects
 * @param {object} datum : data object
 * @param {string} field : name of the field to remove
 * @param {object} source : source object to get field value
 * @return {array} new array of data objects
 */
export const addFieldToDatum = (datum, field, source) => {
  datum[field] = source[field];
  return datum;
};

export const objectDifference = (object1, object2) => {

  let keys1 = Object.keys(object1);
  let keys2 = Object.keys(object2);
  const keyDifference = keys1.filter(x => !keys2.includes(x));
  const difference = {};

  Object.entries(object1).map(entry => {
    if (keyDifference.includes(entry[0])) {
      difference[entry[0]] = entry[1];
    }
  });

  return difference;
}

export const shapeAlternative = (alt, inputs, outputs) => {

  return {
  viewId: alt.viewId,
  refId: alt.refId,
  collectionName: sliceViewFromName(alt.collectionName),
  collectionRefId: alt.collectionRefId,
  collectionViewId: alt.collectionViewId,
    params: alt.params ? alt.params : inputs,
  outputs: alt.outputs ? alt.outputs: outputs,
    parallelData: alt.params || alt.outputs ? Object.assign(alt.params, alt.outputs) : Object.assign(inputs, outputs),
  gltf: alt.gltf,
  image: alt.image,
    altView: false,
    isSelected: false,
    isHovered: false,
    isLoading: false,
  }
}
                                                                                                                                                      
export const shapeCollection = (col, inputs, outputs) => {
  return {
    viewId: col.viewId,
    refId: col.refId,
    collectionName: sliceViewFromName(col.collectionName),
    collectionNameView: col.collectionNameView,
    altsid: col.altsid,
    isSinked: col.isSinked,
    tags: col.tags,
    viewType: col.viewType,
    dimOrder: col.keySets,
    inputs: inputs,
    outputs: outputs,
    isSelected: false,
    isEditingName: false,
    destinations: [],
    currentMode: {
      isAltering: false,
      isEditing: false,
      isLiveEditing: false,
      isCloning: false,
      isToggling: false,
      isSampling: false,
      isSketching: false,
      isBrushing: false,
      isCPing: false,
      isPSing: false,
      isSAing: false,
    }
  }
}


