import React, {Component} from 'react';
import {connect} from 'react-redux';
import {renderIf} from '../util/util';
import NotBar from '../components/notBar';
import {
  successMessage,
  warningMessage,
  errorMessage,
  toggleHistory,
} from '../actions/notificationActions';


class NotificationContainer extends Component {
    render() {
        return renderIf(
            true,
            (<NotBar {...this.props} />),
          );
    }
}

/**
 * state for the header container
 * @param {object} state
 * @return {object}
 */
const mapStateToProps = (state) => ({
  notifications: state.notifications
});

/**
 * interactions for header container
 * @param {function} dispatch
 * @return {object}
 */
const mapDispatchToProps = (dispatch) => ({
  successMessage: (id, message) => {
    dispatch(successMessage(id, message));
  },

  warningMessage: (id, message) => {
    dispatch(warningMessage(id, message));
  },

  errorMessage: (id, message) => {
    dispatch(errorMessage(id, message));
  },

  toggleHistory: () => {
    dispatch(toggleHistory());
  },
});

export default connect(
  mapStateToProps, mapDispatchToProps
)(NotificationContainer);
