import React from 'react';
import styles from './tagBar.module.scss';

const TagBar = props => {
    const tags = props.tagOptions.map((tag, i) => { 
        return <span className={styles["tagBar-span"]} key={"tagBar-key" + i}>#{tag.label}</span>
    });

    return (
        <div className={styles["tagBar"]}>
            {tags}
        </div>
    );
};

export default TagBar;