import React, {PureComponent} from 'react';
import * as THREE from 'three';
import * as GLTFLoader from 'three-gltf-loader';
import {urls} from '../../constants/urls';

const OrbitControls = require('three-orbit-controls')(THREE);

/**
 * @class Scene
 * @extends React.Component
 * @description scene component renders a 3d view of the alternative using three.js library
 */
class Scene extends PureComponent {

  componentDidMount() {
    this.scene = new THREE.Scene();
    this.scene.background = new THREE.Color(0xededed);

    this.camera = new THREE.PerspectiveCamera(80, 1, 0.001, 1000);
    this.scene.add(this.camera);

    this.renderer = new THREE.WebGLRenderer({antialias: true});
    this.controls = new OrbitControls(this.camera, this.renderer.domElement);

    this.renderer.setSize(this.props.width, this.props.height);
    this.renderer.toneMapping = THREE.ReinhardToneMapping;

    this.mount.appendChild(this.renderer.domElement);
    this.renderer.domElement.setAttribute('style', this.props.customStyle);
    this.initializeOrbits();

    this.loader = new GLTFLoader();
    this.loader.load(urls.get('getHost') + this.props.gltf, (gltf) => {
      this.scene.add(gltf.scene);
      this.scene.add(new THREE.AmbientLight( 0xd60000));
      this.scene.traverse((child) => {
        if (child instanceof THREE.Mesh) {
          child.geometry.center();
          child.material = new THREE.MeshPhongMaterial({
            color: 0x4f0303,
            emissive: 0x000000,
            shading: THREE.FlatShading,
            polygonOffset: true,
            polygonOffsetFactor: 1, // positive value pushes polygon further away
            polygonOffsetUnits: 1,
          });
        }
        this.initializeCamera();
      });
    });

    this.animate();

    window.onresize = () => {
      const width = window.innerWidth;
      const height = window.innerHeight;
      this.camera.aspect = width / height;
      this.camera.updateProjectionMatrix();
      this.renderer.setSize(width, height);
    };
  }

  componentWillUnmount() {
    cancelAnimationFrame(this.frameId);
    this.mount.removeChild(this.renderer.domElement);
  }

  initializeOrbits = () => {
    this.controls.rotateSpeed = 1.0;
    this.controls.zoomSpeed = 1.2;
    this.controls.panSpeed = 0.8;
  }

  initializeCamera = () => {
    const object = this.scene.children[1].children[0];
    const offset = 20;
    const boundingBox = new THREE.Box3();

    // set bounding box of object
    // this will be used to setup controls and camera
    boundingBox.setFromObject( object ); 

    // const center = boundingBox.getCenter();
    const size = boundingBox.getSize();

    // get the max side of the bounding box (fits to width OR height as needed )
    const maxDim = Math.max( size.x, size.y, size.z );
    const fov = this.camera.fov * ( Math.PI / 180 );

    let cameraZ = Math.abs( maxDim / 4 * Math.tan( fov * 2 ) );
    cameraZ *= offset; // zoom out a little so that objects don't fill the screen
    this.camera.position.z = cameraZ;

    const minZ = boundingBox.min.z;
    const cameraToFarEdge = ( minZ < 0 ) ? -minZ + cameraZ : cameraZ - minZ;

    this.camera.far = cameraToFarEdge * 3;
    this.camera.updateProjectionMatrix();
  }

  animate = () => {
    this.frameId = window.requestAnimationFrame(this.animate);
    this.renderer.setPixelRatio( window.devicePixelRatio );
    this.renderer.gammaOutput = true;
    this.renderer.gammaFactor = 2.2;

    this.renderer.shadowMap.enabled = true;
    this.renderer.shadowMap.type = THREE.PCFSoftShadowMap;

    this.renderer.render(this.scene, this.camera);
  }

  handleControls = (e) => {
    e.stopPropagation();
  }
  
  render() {
    return (
      <div
        onMouseDownCapture={this.handleControls}
        className = "boardCanvas"
        ref={(mount) => {
          this.mount = mount;
        }}
      />
    );
  }
}

export default Scene;
