/**
 * Action dispatchers for operations on collection objects
 * @namespace fetchActions
 */

import {urls} from '../constants/urls';
import {
  alternativesFetch,
  collectionsFetch,
  layoutFetch,
  projectFetch,
  addLayoutObject,
  keySetsFetch,
  valueBandsFetch,
  settingsFetch,
} from './updateActions';
import {
  successMessage,
  errorMessage,
} from './notificationActions';
import {
  filterCollectionsFromLayouts,
  correctAltIDsInCollections,
  addCollectionNameView,
  shouldAddNameView,
} from '../util/collections';
import {
  shapeAlternative,
  shapeCollection
} from '../util/util';
import {
  getAlternativeIDs,
} from '../util/alternatives';
import Immutable from 'immutable';
import {toast} from 'react-toastify';
import {batch} from 'react-redux';
import { updateCollection } from './collectionActions';

const time = new Date().getHours() + ':' + new Date().getMinutes() + ':' + new Date().getSeconds();


/***
 * @function getUserProjects
 * @description fetches users project from database
 * @memberof fetchActions
 * @returns {Promise}
 */
export const getUserProjects = () => (dispatch) => {
  fetch(urls.get('project_fetch'))
    .then((response)=> response.json())
    .then((response) => {
      if (response.db_loaded) {
        batch(() => {
          dispatch(projectFetch(response));           
        });   
      }else{
        dispatch(projectFetch(response));
      }      

    }).catch((err) => {
      dispatch(errorMessage(toast.error('Layouts not fetched'), 'layouts not fetched ' + time));
    })
  };



/**
 * @function fetchLayout
 * @description fetch layout information from the database and update the store with the results
 * @memberof fetchActions
 * @returns {Promise}
 */
export const fetchProject = () => (dispatch) => {
  fetch(urls.get('project_fetch'))
    .then((response)=> response.json())
    .then((response) => {
      if (response.db_loaded) {
        
        batch(() => {
          dispatch(projectFetch(response)); 
          dispatch(fetchLayout()); 
        });   
      }else{
        dispatch(projectFetch(response));
      }      
      
    }).catch((err) => {
      dispatch(errorMessage(toast.error('Projects not fetched'), 'Projects not fetched ' + time));
    })
  };

/**
 * @function fetchLayout
 * @description fetch layout information from the database and update the store with the results
 * @memberof fetchActions
 * @returns {Promise}
 */
export const fetchLayout = () => (dispatch) => (
  fetch(urls.get('layout_fetch'))
    .then((response)=> response.json())
    .then((response) => {
        const layouts = response[0]['layout'];
        batch(() => {
          dispatch(layoutFetch(layouts));
          dispatch(fetchAlternatives());
          dispatch(successMessage(toast.success('Layouts fetched'), 'layouts fetched ' + time)); 
        });               
    }).catch((err) => {
      dispatch(errorMessage(toast.error('Layouts not fetched'), 'layouts not fetched ' + time));
    })
);

export const fetchSettings = () => (dispatch) => (
  fetch(urls.get('settings_fetch'))
    .then(response => response.json())
    .then((response) => {
      const settings = {
        alternativeImageSize: response.length !== 0 && response[0].hasOwnProperty('alternativeImageSize') ? response[0].alternativeImageSize : 180,
        collectionCompactionType: response.length !== 0 && response[0].hasOwnProperty('collectionCompactionType') ? response[0].collectionCompactionType : 'none',
        preventCollision: response.length !== 0 && response[0].hasOwnProperty('preventCollision') ? response[0].preventCollision : true,
      };

      batch(() => {
        dispatch(settingsFetch(settings));
        dispatch(fetchAlternatives());
      });
    }).catch(err => {
      console.log(err);
    })
);

/**
 * @function fetchAlternatives
 * @description fetch the alternative data from the database and update the store with the results
 * @memberof fetchActions
 * @returns {Promise}
 */
export const fetchAlternatives = () => (dispatch) => (
  fetch(urls.get('alternatives_fetch'))
    .then((response) => response.json())
    .then((response) => {
      // ensure no null data gets in
      const alternatives = response.alternatives.filter(alt => alt !== null).map(alt => shapeAlternative(alt));

      batch(() => {
        dispatch(alternativesFetch(alternatives));
        dispatch(keySetsFetch(response.keySets));
        dispatch(valueBandsFetch(response.valueBands));
        dispatch(fetchCollections());
        dispatch(successMessage(toast.success('Alternatives fetched'), 'alternatives fetched ' + time));
      });

    }).catch((err) => {
      console.log(err);
      dispatch(errorMessage(toast.error('Alternatives not fetched'), 'alternatives not fetched ' + time));
    })
);


/**
 * @function fetchCollections
 * @description fetch the collection data and update the store with the results. The collections present in the layout are put filtered and sent, the other collections are put into the sink.
 * @memberof fetchActions
 * @returns {Promise}
 */
export const fetchCollections = () => (dispatch, getState) => (
  fetch(urls.get('collections_fetch'))
    .then((response) => response.json())
    .then((response) => {
      const inputs = getState().alternatives.get('alternatives').size > 0 ? Object.keys(getState().alternatives.get('alternatives').toJS()[0]['params']) : {};
      const outputs = getState().alternatives.get('alternatives').size > 0 ? Object.keys(getState().alternatives.get('alternatives').toJS()[0]['outputs']) : {};

      const layouts = getState().layouts;
      const alternatives = getState().alternatives;
      const altIDs = getAlternativeIDs(alternatives.get('alternatives'));
      const correctedCollections = correctAltIDsInCollections(response, altIDs);
      const collectionsWithoutLayout = filterCollectionsFromLayouts(correctedCollections, layouts);

      for (let i = 0; i < collectionsWithoutLayout.length; i++) {
        
        const col = collectionsWithoutLayout[i];

        if (col) {
          const layoutObject = Immutable.fromJS({x:2, y:2, w:3, h:5 , i: col.viewId, minW: 3, minH: 5, moved: false, static: false});   

          dispatch(addLayoutObject(layoutObject));
        }
      }
      let shouldAddName = shouldAddNameView(correctedCollections)
      if(shouldAddName)
      {
        let addViewName = addCollectionNameView(correctedCollections)
        addViewName.map((col => dispatch(updateCollection(col))))
      }
      let modifiedCollections   = correctedCollections.map(col => shapeCollection(col, inputs, outputs))
      batch(() => {
        dispatch(collectionsFetch(modifiedCollections));
        dispatch(successMessage(toast.success('Collections fetched'), 'collections fetched ' + time));
      });
    }).catch((err) => {
      console.log(err);
      dispatch(errorMessage(toast.error('Collections not fetched'), 'collections not fetched ' + time));
    })
);
