import {urls} from '../constants/urls';
import {
    setCollisionPrevention,
    setCompactionType,
    setAlternativeSize,
} from './updateActions';

// const time = new Date().getHours() + ':' + new Date().getMinutes() + ':' + new Date().getSeconds();
// TODO: add notifications/toasts below

export const updateCollisionPrevention = (payload) => (dispatch) => {
    return fetch(urls.get('settings_fetch'), {
        method: 'PUT',
        headers: {'Content-Type': 'application/json'},
        body: JSON.stringify({"preventCollision": payload}),
      })
      .then(() => {
        dispatch(setCollisionPrevention(payload));
      })
      .catch(err => console.log(err));
}

