import appConstants from '../constants/appConstants';
import Immutable from 'immutable';
import {
  fetchAlternativesNotID,
} from '../util/alternatives';
import {
  addFieldToDatum,
  removeFieldFromDatum,
} from '../util/util';

const initialState = Immutable.fromJS({
  alternatives: [],
  alternativesFetched: false,
  clippedAlternatives: [],
});

export default (state = initialState, action={}) => {
  let newAlternativesList;
  let alternative;
  let parallelData;
  let index;

  switch (action.type) {
  case appConstants.get("FETCHING_PROJECT"):
    return state
        .set('alternativesFetched', false)
        .set("alternatives", Immutable.fromJS([]))
        .set("clippedAlternatives", Immutable.fromJS([]))
  case appConstants.get('FETCH_ALTERNATIVES'):
    return state
        .set('alternatives', Immutable.fromJS(action.payload))
        .set('alternativesFetched', true);

  case appConstants.get('ADD_NEW_ALTERNATIVES'):
    return state.update('alternatives',
      (alternatives) => {
        return alternatives.push(Immutable.fromJS(action.payload));
      });
      
  case appConstants.get('UPDATE_ALTERNATIVE'):
    return state.update('alternatives', (alternatives) => 
      alternatives.map((alternative) => {
        if (alternative.get('viewId') === action.payload.viewId) {
          return alternative.mergeDeepWith((prev, next, key) => {
            if (key==='isSelected') {
              return prev;
            }
            return next;
          }, Immutable.fromJS(action.payload.alternative));
        }
        return alternative;
      }));

  case appConstants.get('SELECT_ALTERNATIVE'):
    return state.update('alternatives', (alternatives) => 
      alternatives.setIn([alternatives.findIndex((alt) => alt.get('viewId') === action.payload.viewId), 'isSelected'], true)
    );

  case appConstants.get('DESELECT_ALTERNATIVE'):
    return state.update('alternatives', (alternatives) => 
      alternatives.setIn([alternatives.findIndex((alt) => alt.get('viewId') === action.payload.viewId), 'isSelected'], false)
    );
    
  case appConstants.get('HOVER_ALTERNATIVE'):
    return state.update('alternatives', (alternatives) => 
      alternatives.map((alternative) => {
        if (alternative.get('viewId') === action.payload.viewId) {
          return alternative.set('isHovered', true);
        }
        return alternative.set('isHovered', false);
      })
    );

  case appConstants.get('DEHOVER_ALTERNATIVE'):
    return state.update('alternatives', (alternatives) => 
      alternatives.setIn([alternatives.findIndex((alt) => alt.get('viewId') === action.payload.viewId), 'isHovered'], false)
    );

  case appConstants.get('DEHOVER_ALL_ALTERNATIVES'):
    return state.update('alternatives', (alternatives) => alternatives.map((alternative) => 
      alternative.set('isHovered', false)));

  case appConstants.get('DESELECT_ALL_ALTERNATIVES'):
    return state.update('alternatives', (alternatives) => alternatives.map((alternative) => 
      alternative.set('isSelected', false)));
  
  case appConstants.get('CLIP_ALTERNATIVE'):
    return state.update('clippedAlternatives',
      (clippedAlternatives) => clippedAlternatives.push(Immutable.fromJS(action.payload)));

  case appConstants.get('REMOVE_ALTERNATIVE'):
      return state.set('alternatives', fetchAlternativesNotID(
        state.get('alternatives'), action.payload
      ));
  
  case appConstants.get('CLEAR_ALTS_CLIPBOARD'):
    return state.update('clippedAlternatives', (clippedAlternatives) => clippedAlternatives.clear());

  case appConstants.get('TOGGLE_ALT_VIEW'):
    return state.update('alternatives', (alternatives) => 
      alternatives.map((alternative) => {
        if (alternative.get('viewId') === action.payload) {
          return alternative.set('altView', !alternative.get('altView'));
        }
        return alternative;
      }));

  case appConstants.get('SET_LOADING_STATE'):
    return state.update('alternatives', (alternatives) => 
      alternatives.setIn([alternatives.findIndex((alt) => alt.get('viewId') === action.payload.viewId), 'isLoading'], action.payload.isLoading)
    );
      
  case appConstants.get('ALTER_ALTERNATIVE'):
    return state.update('alternatives', (alternatives) => alternatives.map((alternative) => {
      if (alternative.get('viewId') === action.payload.alternativeID) {
        return alternative.setIn(['parallelData', action.payload.parameter], action.payload.newValue);
      }
      return alternative;
    }));

  case appConstants.get('ADD_DATA_FIELD'):
    return state.update('alternatives', (alternatives) => alternatives.map((alternative) => {
      if (alternative.get('viewId') === action.payload.alternative.get('viewId')) {
        parallelData = action.payload.alternative.get('parallelData').toJS();
        return alternative.setIn(['parallelData'], Immutable.fromJS(addFieldToDatum(parallelData, action.payload.field, 
          Object.assign(alternative.get('params').toJS(), alternative.get('outputs').toJS()))
        ));
      }
      return alternative;
    }));

  case appConstants.get('REMOVE_DATA_FIELD'):
    return state.update('alternatives', (alternatives) => alternatives.map((alternative) => {
      if (alternative.get('viewId') === action.payload.alternative.get('viewId')) {
        parallelData = action.payload.alternative.get('parallelData').toJS();
        return alternative.setIn(['parallelData'], Immutable.fromJS(removeFieldFromDatum(parallelData, action.payload.field)));
      }
      return alternative;
    }));

  case appConstants.get('ORDER_ALTERNATIVES'):
    return state.update('alternatives',
      (alternatives) => {
        index = alternatives.findIndex((alt) => alt.get('viewId') === action.payload);
        alternative = alternatives[index];
        newAlternativesList = alternatives.delete(index).push(alternative);
        return newAlternativesList;
      });

  default:
    return state;
  }
};
