import appConstants from '../constants/appConstants';
import Immutable from 'immutable';

const initialState = Immutable.fromJS({
    user: {},
    userFetched: false,
    userLoggedIn: false,
})


export default (state = initialState, action={}) => {
    switch(action.type) {
        
        case appConstants.get('FETCH_USER'):
            return state.set('user', Immutable.fromJS(action.payload)).set("userFetched", true).set("userLoggedIn", true);

        case appConstants.get('NOT_FETCH_USER'):
            return state.set('userFetched', false);

        case appConstants.get("LOGOUT_USER"):
            return state.set("userLoggedIn", false).set('user', Immutable.fromJS({})).set("userFetched", false)
        
        default:
            return state;
    }
} 