import React from 'react';
import styles from './navigationItems.module.scss';
import NavigationItem from './navigationItem';

const navigationItems = (props) => {
    return(
        <ul
        className={styles['navigationItems']}
    >
        
         <NavigationItem
            link={"/"}
            exact
        >
            Home
        </NavigationItem>
        {
            props.isLoggedIn ?
            <NavigationItem 
             link={"/projects"}
            >
                Projects
            </NavigationItem>
            :
            null
        }

        {
            !props.isLoggedIn ?
            <NavigationItem
            link={"/signup"}
           >
               Join Us
           </NavigationItem>
           
           :
           null
        }

        {
            !props.isLoggedIn ?
             <NavigationItem
             link={"/login"}
            >
                Login
            </NavigationItem>
            :
            null
        }
        {
            props.user ?
            <li
             className={styles["name"]}
            >
                {props.user}
            </li>
            :
            null
        }
       
    </ul>
    )
}

export default navigationItems;