import collectionObjectConstants from '../constants/collectionObject';
import stringConstants from '../constants/strings';
import memoize from 'lodash/memoize';
import values from "lodash/values";

/**
 * finds the collection with the name passed as a param
 * @param {ImmutableList} collections
 * @param {string} collectionName
 * @return {ImmutableMap} collection
 */
export const findCollectionsFromName = memoize((collections, collectionName) => (
  collections.find((collection) =>
    collection.get(collectionObjectConstants.get('NAME')) === collectionName
  )
), (...args) => values(args).join("_"));

/**
 * finds the collection with id passed
 * @param {ImmutableList} collections
 * @param {string} collectionId
 * @return {ImmutableMap}
 */
export const findCollectionsFromID = memoize((collections, collectionId) => (
  collections.find((collection) => collection.get('viewId') === collectionId)
), (...args) => values(args).join("_"));

/**
 * find refs of a collection with the given ID (except the ID itself)
 * @param {ImmutableList} collections
 * @param {string} collectionID
 * @return {ImmutableMap}
 */
export const findCollectionRelativesFromID = (collections, collectionID) => {
  const collection = collections.find((collection) => collection.get('viewId') === collectionID);
  const refId = collection.get('refId');
  const colRelatives = collections.filter((col) => col.get('refId') === refId)
    .filter((col) => col.get('viewId') !== collection.get('viewId'))
  return colRelatives;
}

/**
 * remove an element (with id = collectionId) from the collections list
 * @param {ImmutableList} collections
 * @param {string} collectionId
 * @return {ImmutableList}
 */
export const fetchCollectionsNotID = memoize((collections, collectionId) => (
  collections.filter((collection) =>
    collection.get(collectionObjectConstants.get('ID')) !== collectionId
  )
), (...args) => values(args).join("_"));

/**
 * gets the name of the collection
 * @param {ImmutableMap} collection
 * @return {string}
 */
export const getCollectionName = (collection) => {
  let collectionName = stringConstants.get('UNTITLED_COLLECTION_NAME');
  if (collection.get(collectionObjectConstants.get('NAME'))) {
    collectionName = collection.get(collectionObjectConstants.get('NAME'));
  }
  return collectionName;
};

/**
 * returns the alternatives present for each collection
 * @param {ImmutableMap} collection
 * @param {ImmutableList} alternatives
 * @returns {ImmutableList} 
 */
export const getAlternativesInCollection = memoize((collection, alternatives) =>
 alternatives.filter((alternative) => collection.get('altsid').includes(alternative.get('viewId'))
  ), (...args) => values(args).join("_"));

/**
 * This formula computes the new value for gridlayout width property
 * when a collection is dragged beyond the existing boundaries
 * @param {int} cols
 * @param {int} margin
 * @param {int} cellDim
 * @param {int} buffer
 * @return {int} grid-layout width
 */
export const calculateWidth = memoize((cols, margin, cellDim, buffer) => (
  cellDim * cols + margin * cols + buffer
), (...args) => values(args).join("_"));

/**
 * This formula computes the new value for gridlayout height property
 * when a collection is dragged beyond the existing boundaries
 * @param {int} rows
 * @param {int} margin
 * @param {int} rowHeight
 * @param {int} buffer
 * @return {int} grid-layout height
 */
export const calculateHeight = memoize((rows, margin, rowHeight, buffer) => (
  rowHeight * rows + margin * rows + buffer
), (...args) => values(args).join("_"));

/**
 * Using React-grid-layout's function to get the new coordinates
 * @param {int} x
 * @param {int} y
 * @param {int} cols
 * @return {object} new x and y coordinates
 */
export const getCoordinates = (x, y) => {

  const newX = Math.round(x / 200);
  const newY = Math.round(y / 60);

  return {x: newX, y: newY};
};

/**
 * removes the collections which do not have a layout
 * @param {ImmutableList} collections
 * @param {ImmutableList} layouts
 * @return {ImmtableList}
 */
export const filterCollectionsFromLayouts = (collections, layouts) => {
  return collections.map((collection) => {
    const collectionId = collection[collectionObjectConstants.get('ID')];
    const colLayout =layouts.get('gridLayout').find((item) => {
      return item.get('i') === collectionId;
    });

    if(!colLayout){
      return collection;
    }
  });
};

/**
 * add properties to default collections list
 * @param {array} collections
 * @return {array}
 */
export const addPropertyToCollections = (collections) => (
  collections.map((collection) => {
    collection.isSelected = false;
    collection.destinations = []
    collection.currentMode = {
      isAltering: false,
      isEditing: false,
      isLiveEditing: false,
      isCloning: false,
      isToggling: false,
      isSampling: false,
      isSketching: false,
      isBrushing: false,
      isCPing: false,
      isPSing: false,
      isSAing: false,
    } // default
    
    return collection;
  })
);

/**
 * removes altsIDs which exist in collections
 * but don't exist in the alternatives
 * @param {Array} collections
 * @param {ImmutableList} alternateIDs
 * @return {Array}
 */
export const correctAltIDsInCollections = (collections, alternateIDs) => {
  return collections.map((collection) => {
    
    const altIDs = collection.altsid.filter((id) => {
      
      const exists = alternateIDs.findIndex((altID) => altID === id);
      
      return (exists !== -1) ? true : false;
    });

    collection.altsid = altIDs;
    return collection;
  });
};

export const getCollectionsByTag = (collections, tag) => {
  return collections.filter(collection => {
    if (collection.get('tags').contains(tag)) {
      return true;
    }
    
    return false;
  });
}


/**
 * @param {String}
 * @param {ImmutableList} collections
 * @param {ImmutableMap} collectionItem
 * @description checks if name exists before editing the collectionName
 * @return {boolean} exists
 */
export const nameExists = (name, collections, collectionItem) => {
  let filteredCollection = collections.filter((col) => col.get("viewId") !== collectionItem.get("viewId") && col.get("refId") !== collectionItem.get("refId"));
  let exists = false;

  for (let collection of filteredCollection) {
    if (collection.get("collectionName").toLowerCase() ===  name.toLowerCase()) {
      exists = true;
    }
  }
  return exists;
}

export const  handleNameCounter = (collections, name, collection) => {
  let counter = 0;
  let compareNumber = 0;
  let filterCollection = collections.filter((col) => col.get("viewId") !== collection.get("viewId"));

  for (let collectionItem of filterCollection) {
    let subName = collectionItem.get("collectionName").split(":");

    if (collectionItem.get("collectionName").toLowerCase() === name.toLowerCase()) {
      counter++;
    }
    else if (subName[0].toLowerCase() === name.toLowerCase()) {
      let integer = parseInt(subName[1], 10);
    
      if (integer > compareNumber) {
        compareNumber = integer;
      }
    }
  }
  
  if (compareNumber >= counter) {
    return compareNumber + 1;
  } else {
    return counter;
  }
  
};


/**
 * @param {ImmutableList} collections
 * @return {ImmutableList} collections
 * @description  sorts collections and add NameView on fetch
 */
export const addCollectionNameView = (collections) => {

    if(collections !== null && collections.length > 0){

      let current = {
        refId: null
      };

      let currentName;
  
      let cnt = 0;
  
      const sortedArray  = collections.sort(function(a, b) {
        let dateA = a.refId.toLowerCase();
        let dateB = b.refId.toLowerCase();
  
        if (dateA < dateB) {
          return -1;
        } else if (dateA > dateB) {
          return 1;
        }
  
        return 0;
        });

        for (var i = 0; i < sortedArray.length; i++) {
          if (sortedArray[i].refId != current.refId) {
              current = sortedArray[i];
              currentName = current.collectionName
              cnt = 1;
              if(currentName.includes("-View(")){
                let getNameIndex = sortedArray[i].collectionName.indexOf("-View(")
                currentName = currentName.slice(0, getNameIndex)
              }
              sortedArray[i].collectionNameView = `View(${cnt})`;
              sortedArray[i].collectionName = currentName + `-View(${cnt})`; 
          } else {
              if(sortedArray[i].collectionName.includes("-View(")){
                let getNameIndex = sortedArray[i].collectionName.indexOf("-View(");
                currentName = currentName.slice(0, getNameIndex);
              }
              cnt++;
              sortedArray[i].collectionNameView = `View(${cnt})`;
              sortedArray[i].collectionName = currentName + `-View(${cnt})`; 
          }
      }
      return sortedArray;
    } else {
      return collections;
    }
}


/**
 * 
 * @param {Array} collections
 * @description checks if the  collectionNameView is null or empty
 * @returns {Boolean} hasViewName
 */
export const shouldAddNameView = (collections) => {
  let hasViewName = false 
  for(let collection of collections) {
    if(typeof(collection.collectionNameView) === "undefined" || collection.collectionNameView === null || collection.collectionNameView.length <= 0)
    {
      hasViewName =  true;
    }
  }

  return hasViewName;
}



/**
 * @param {collectionName} String
 *  @description remove viewName from collection
 * @return {String} collectionName
 */
export const removeViewName = (collectionName) => {
  if (collectionName && collectionName.length > 0) {
    if (collectionName.includes("View(")) {
      let letterIndex = collectionName.indexOf("-View(")
      return collectionName.slice(0, letterIndex);
    }
  } else {
    return collectionName;
  }
}


/**
 * @param {name} String
 * @description removes collectionNameView from collection name
 */
export const sliceViewFromName = (name) => 
{
  if(name.includes("-View("))
  {
    
    let letterIndex = name.indexOf("-View(")
    let newName = name.slice(0, letterIndex)
    return newName;
  }
  else
  {
    return name;
  }

}


