import React from 'react';
import ReactDOM from 'react-dom';
import {Provider} from 'react-redux';
import configureStore from './store';
import AppContainer from './containers/AppContainer';
import AppRouter from './router/AppRouter'
import './index.css';
import appConstants from './constants/appConstants';


// Add a custom event keyed as `optimizedResize`. This event lies in stark
// contrast to `resize`. `resize` can happen in some time spanning less than
// one frame 60 frames per second, and may result in resource hog. This new
// event only fires on the next animation frame, and upon a successful resize.
(function() {
  var throttle = function(type, name, obj) {
    obj = obj || window;
    var running = false;
    var func = function() {
      if (running) { return; }
      running = true;
      requestAnimationFrame(function() {
        obj.dispatchEvent(new CustomEvent(name));
        running = false;
      });
    };
    obj.addEventListener(type, func);
  };

  /* init - you can init any event */
  throttle("resize", "optimizedResize");
})();


// setting userId and projectId for all the requests 
const oldFetch = fetch;
fetch = (url , data) =>{

  const userId = localStorage.getItem(appConstants.get('USER_ID'));
  const projectId = sessionStorage.getItem(appConstants.get('PROJECT_ID'));
  const defualtData = {
    method: 'GET',
    headers: {'Content-Type': 'application/json'},
  };

  if (!data){
    data = defualtData;
  }

  data.headers.userid = userId;
  data.headers.projectid = projectId;
  
  return oldFetch(url,data);
}

ReactDOM.render(
  <Provider store={configureStore()}>
    <AppRouter />
  </Provider>,
  document.getElementById('root')
);
