import React, { PureComponent } from 'react';
import Project from './project'
import styles from './projects.module.scss'; 
import { MdNoteAdd, MdHome,MdContentCopy } from 'react-icons/md';
import ExitToAppOutlinedIcon from '@material-ui/icons/ExitToAppOutlined';
import ProjectContextMenu from './projectContextMenu';
import ProjectBox from "./projectBox/ProjectBox";
import EditProjectBox from './editProjectBox';
import NotificationContainer from "../../containers/NotificationContainer";
import MessageBox from "../messageBox/MessageBox";


class Projects extends PureComponent {

    constructor(props) {
        super(props);

        this.state = {
            shouldWarn: false,
            confirmAction: null,
            warningMessage: ""
        }
      
    }

    componentDidMount() {
        this.props.getUserProjects();
        
    }

    componentDidUpdate = (prevProps, prevState, snapshot) => {
        if(prevProps.auth.get("userLoggedIn") && !this.props.auth.get("userLoggedIn")) {
            this.props.history.push("/");
        }
    }

    /**
     * @description log the user out of the system
     */
    handelLogout = () => {
        this.props.logout();
    }

    /**
     * @function handelOpenEditProject
     * @param {Object} project 
     * @description shows EditProjectBox
     */
    handelOpenEditProject(project) {
        this.props.openEditProject(project);
    }

    /**
     * @function  deleteProject
     * @param {String} id 
     * @description handles delete project
     */
    deleteProject(id){
        this.props.deleteProject(id);
        this.setState({
            shouldWarn: false,
            warningMessage: "",
            confirmAction: null
        })
    }


    /**
     * @function handleDeleteProject
     * @param {String} id 
     * @description shows meesageBox component
     */
    handleDeleteProject(id) {
        this.props.shouldCloseProjectContextMenu()
        this.setState({
            shouldWarn: true,
            warningMessage: 'Do you want to delete all selected project views and their underlying alternative?',
            confirmAction: this.deleteProject.bind(this, id),
        })
    }

    showProjectContextMenu = (xPos, yPos, project) => {
      
        this.props.showProjectMenu(xPos, yPos, project);
    }

    renderProjects = (projects) => (
        projects.map((project) => {
            return (
                <Project
                  key={project.get("id")}
                  project={project}
                  name={project.get("name")}
                  selectProject={this.props.selectProject}
                  deleteProject={this.handleDeleteProject.bind(this)}
                  openEditProject={this.handelOpenEditProject.bind(this)}
                  history={this.props.history}
                  showProjectContextMenu={this.showProjectContextMenu}
                  userName={this.props.auth.get('user').get('username')}
                />
            )
        })
    )

    /**
     * @description shows project create popup
     */
    handleCreateProject = () => {
        this.props.isCreatingProject();
    }

    /**
     * @description resets the state when clicking cancle in delete popup
     */
    handleDeleteCancel = () => {
        this.setState({
            shouldWarn: false,
            warningMessage: "",
            confirmAction: null
        })
    }

    handleClick = (e) => {
        e.preventDefault();
        if(this.props.projects.get("showProjectContextMenu")){
            this.props.shouldCloseProjectContextMenu()
        }
    }

    handleHomeClick = (e) => {
        e.preventDefault();
        this.props.history.push("/")
    }

    handleAboutClick = (e) => {
        e.preventDefault();
        window.open("https://dstar-design-gallery.github.io/dstar-docs/" , '_blank', "docs");      
    }


    render(){
        let username = this.props.auth.get('user') && this.props.auth.get('user').get('username') ? this.props.auth.get('user').get('username') : "No Name"
        return(
            <div 
                className={styles["mainsContainer"]}   
                onClick={this.handleClick}  
            >
                <NotificationContainer />
                <div className={styles["controlsContainer"]}>
                   <div className={styles["controlsContent"]}>
                        <div className={styles["userContainer"]}>
                                <div className={styles["circle"]}>
                                    <p className={styles["initials"]}>{username.charAt(0).toUpperCase()}</p>
                                </div>
                                <p className={styles["userName"]}>{username}</p>
                        </div>
                        <div className={styles["homeContainer"]} onClick={this.handleHomeClick}>
                                <MdHome
                                 style={{
                                     width: 30,
                                     height: 30, 
                                     marginLeft: 10
                                 }} 
                                />
                                <p className={styles["homeText"]}>Home </p> 
                            </div>
                            <div className={styles["homeContainer"]} onClick={this.handleAboutClick}>
                                <MdContentCopy
                                 style={{
                                     width: 30,
                                     height: 30, 
                                     marginLeft: 10
                                 }} 
                                />
                                <p className={styles["homeText"]}>About Us </p> 
                            </div>
                            <div className={styles["addProjectContainer"]} onClick={this.handleCreateProject}>
                                <MdNoteAdd
                                  style={{
                                      width: 30,
                                      height: 30,
                                      marginLeft: 10,
                                      
                                  }}
                                />                                
                                <p className={styles["addProjectText"]}>Add Project</p>
                            </div>
                            <div className={styles["logoutContainer"]} onClick={this.handelLogout}>
                                <ExitToAppOutlinedIcon
                                 style={{
                                    width: 30,
                                    height: 30,
                                    marginLeft: 10,
                                 }} 
                                />
                                <p className={styles["logoutText"]}>logout</p>
                            </div>
                            
                        </div>
                   </div>
                <div className={styles["projectsContainer"]}>
                {
                    !this.props.projects.get("projectFetched") &&
                    !this.props.projects.get("projectLoaded") ?
                    <h3>
                        Loading...
                    </h3>
                    :
                    <div className={styles["projectsRow"]}>
                        {
                         this.renderProjects(this.props.projects.get("projects"))
                        } 
                        {
                            this.props.projects.get("isCreating") ?
                                <ProjectBox
                                    isCreatingProject={this.props.isCreatingProject}
                                    isCreating={this.props.projects.get("isCreating")}
                                    createProject={this.props.createProject}
                                    history={this.props.history}
                                    
                                 
                                />
                                :
                                null
                        }
                        {
                            this.props.projects.get("showEditBox")?
                            <EditProjectBox
                                isCreatingProject={this.props.isCreatingProject}
                                isCreating={this.props.projects.get("showEditBox")}
                                project={this.props.projects.get("editProject")}
                                editProject={this.props.editProject}
                                openEditProject={this.props.openEditProject}
                                history={this.props.history} 
                            />
                            :
                            null
                        }
                        {
                            this.props.projects.get("showProjectContextMenu")?
                            <ProjectContextMenu 
                                xPos={this.props.projects.get("contextMenuPosX")}
                                yPos={this.props.projects.get("contextMenuPosY")}
                                project={this.props.projects.get("contextMenuSelectedProject")}
                                handleDeleteProject={this.handleDeleteProject.bind(this)}
                                openEditProject={this.props.openEditProject}
                                history={this.props.history}
                                shouldCloseProjectContextMenu={this.props.shouldCloseProjectContextMenu}
                                selectProject={this.props.selectProject}
                                />
                            :
                            null 
                        }
                    </div>
                }
                </div> 
                {this.state.shouldWarn ? <MessageBox handleDeleteCancel={this.handleDeleteCancel} confirmAction={this.state.confirmAction} message={this.state.warningMessage}/> : null} 
            </div>
        )
    }
}

export default Projects;