import React, { PureComponent } from 'react';
import TextField from '@material-ui/core/TextField';
import styles from './settingsPanel.module.scss'
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import DialogContentText from '@material-ui/core/DialogContentText';
import Dropdown from 'react-dropdown';
import 'react-dropdown/style.css';

/**
 * @class SettingsPanel
 * @extends React.PureComponent
 * @description this component contains the global DStar settings, such as collection tiling, alternative thumbnail size and etc.
 */
class SettingsPanel extends PureComponent {

  /**
   * @constructor
   * @param {object} props 
   */
  constructor(props) {
      super(props)

      this.state = {
          settingsOpen: true,
          selectBox1Open: false,
          selectBox2Open: false,
          selectBox3Open: false,
          altSize: this.props.settings.get('alternativeImageSize'),
          colType: this.props.settings.get('collectionCompactionType'),
          colPrev: this.props.settings.get('preventCollision'),
      }

      this.selectedProject = {};  
      this.newProjectName = "";
      this.newProjectDescription = "";  
  }

  /**
   * @function handleSizeChange
   * @memberof SettingsPanel
   * @description changes the size of alternative thumbnails
   * @param {event} event
   */
  handleSizeChange = (event) => {
    this.setState({altSize: event.target.value});
    this.props.setAlternativeSize(event.target.value);
  };

    /**
   * @function handleTypeChange
   * @memberof SettingsPanel
   * @description handles the tiling option choice
   * @param {event} event 
   */
  handleTypeChange = (event) => {
    this.setState({colType: event.target.value});
    this.props.setCompactionType(event.target.value);
  };

    /**
   * @function handlePreventionChange
   * @memberof SettingsPanel
   * @description handles the compaction prevention choice
   * @param {event} event 
   */
  handlePreventionChange = (event) => {
    this.setState({colPrev: event.target.value});
    this.props.setCollisionPrevention(event.target.value);
  };

    /**
   * @function handleSettingsClose
   * @memberof SettingsPanel
   * @description hides the settings pane
   */
  handleSettingsClose = () => {
      this.setState({settingsOpen: false});
      this.props.toggleSettings();
  };

  /**
   * @function handleSizeClose
   * @memberof SettingsPanel
   * @description hides size options
   */
  handleSizeClose = () => {
    this.setState({selectBox1Open: false});
  };

  /**
   * @function handleSizeOpen
   * @memberof SettingsPanel
   * @description shows the size options
   */
  handleSizeOpen = () => {
    this.setState({selectBox1Open: true});
  };

  /**
   * @function handleTypeClose
   * @memberof SettingsPanel
   * @description hides the tiling options
   */
  handleTypeClose = () => {
    this.setState({selectBox2Open: false});
  };

  /**
   * @function handleTypeOpen
   * @memberof SettingsPanel
   * @description shows the tiling options
   */
  handleTypeOpen = () => {
    this.setState({selectBox2Open: true});
  };

  /**
   * @function handlePreventionClose
   * @memberof SettingsPanel
   * @description hides the compaction prevention options
   */
  handlePreventionClose = () => {
    this.setState({selectBox3Open: false});
  };

  /**
   * @function handlePreventionOpen
   * @memberof SettingsPanel
   * @description shows the compaction prevention options
   */
  handlePreventionOpen = () => {
    this.setState({selectBox3Open: true});
  };

  /**
   * @function handleSelect
   * @memberof projectBox
   * @description selecting one of the existing projects
   */
  handleSelect = () => {
    if ((typeof(this.selectProject) !== "undefined") && this.selectProject.name) {
      this.props.selectProject(this.selectProject);
      this.setState({settingsOpen: false});
    }        
  };

  /**
   * @function handleCreate
   * @memberof projectBox
   * @description creating a new project 
   */
  handleCreate = () => {
    if (this.newProjectName !== "") {
      this.props.createProject(this.newProjectName, this.newProjectDescription);
      this.setState({settingsOpen: false});
    }    
  };

  onProjectChange = (e) => {    
    var index = this.props.projects.toJS().findIndex((project)=>project.name === e.value);

    if (index > -1) {
      this.selectProject = this.props.projects.toJS()[index];
    }    
  }

  onProjectNameChange = (e) =>{
    this.newProjectName = e.target.value;
  }

  onProjectDescriptionChange = (e) =>{
    this.newProjectDescription = e.target.value;
  }

  /**
   * @returns {ReactPureComponent}
   */
  render() {
      const options = this.props.projects.toJS().map((project)=>project.name);
      const defaultOption = options[0];
      return (
          <div>
            <Dialog
              className={styles['panel']} 
              open={this.state.settingsOpen}
              onClose={this.handleSettingsClose}
              aria-labelledby="settings-dialog-title"
              aria-describedby="settings-dialog-description"
            >
              <DialogTitle id="settings-dialog-title">{"Settings"}</DialogTitle>
              <hr/>
              <DialogContent className={styles['panel-content']}>
                <table>
                <tbody>
                  <tr>
                    <td><p>alternative image size</p></td>
                    <td>
                        <FormControl>
                          <InputLabel id="open-select-label"></InputLabel>
                          <Select
                            labelId="open-select-label"
                            id="open-select"
                            open={this.state.selectBox1Open}
                            onClose={this.handleSizeClose}
                            onOpen={this.handleSizeOpen}
                            value={this.state.altSize}
                            onChange={this.handleSizeChange}
                          >
                            <MenuItem value={150}>Small</MenuItem>
                            <MenuItem value={180}>Medium</MenuItem>
                            <MenuItem value={210}>Large</MenuItem>
                          </Select>
                        </FormControl>
                    </td>
                  </tr>
                  <tr>
                    <td><p>collection tiling method</p></td>
                    <td> 
                        <FormControl>
                          <InputLabel id="open-select-label"></InputLabel>
                          <Select
                            labelId="open-select-label"
                            id="open-select"
                            open={this.state.selectBox2Open}
                            onClose={this.handleTypeClose}
                            onOpen={this.handleTypeOpen}
                            value={this.state.colType}
                            onChange={this.handleTypeChange}
                          >
                            <MenuItem value={'none'}>None</MenuItem>
                            <MenuItem value={'vertical'}>Vertical</MenuItem>
                            <MenuItem value={'horizontal'}>Horizontal</MenuItem>
                          </Select>
                        </FormControl>
                    </td>
                  </tr>

                  <tr>
                    <td><p>prevent collection collision</p></td>
                    <td> 
                        <FormControl>
                          <InputLabel id="open-select-label"></InputLabel>
                          <Select
                            labelId="open-select-label"
                            id="open-select"
                            open={this.state.selectBox3Open}
                            onClose={this.handlePreventionClose}
                            onOpen={this.handlePreventionOpen}
                            value={this.state.colPrev}
                            onChange={this.handlePreventionChange}
                          >
                            <MenuItem value={false}>Yes</MenuItem>
                            <MenuItem value={true}>No</MenuItem>
                          </Select>
                        </FormControl>
                    </td>

                  </tr>
                  </tbody>
                </table>
                <hr />
                <div className={styles["containerForProjectAndName"]}>
                  <p className={styles["projectLabel"]}>User Name: </p>
                  <p
                   className={styles["projectTitle"]}
                  > {this.props.user.get("username")}</p>
                </div>

                <div className={styles["containerForProjectAndName"]}>
                  <p
                   className={styles["projectLabel"]}
                  >Project: </p>
                  <p
                   className={styles["projectTitle"]}
                  > {this.props.selectedProject.name}</p>
                </div>
                <DialogActions>
                  <Button onClick={this.handleSettingsClose} color="primary">
                    OK
                  </Button>
                </DialogActions>
                
                  
              
              </DialogContent>
            </Dialog>
          </div>
      );
  }
}

export default SettingsPanel;