import React, {PureComponent} from 'react';
import styles from './project.module.scss';
import imageAlts from '../../../resources/SquamishThree1.png';

class Project extends PureComponent {

    constructor(props) {
        super(props);
       
    }

    /**
     * @function  handleSelectedProject
     * @param {Object} e 
     * @description select an existing project and render project page
     */
    handleSelectedProject = (e) => {
        e.preventDefault();
        this.props.selectProject(this.props.project.toJS())
        this.props.history.push("./project");
    }

    /**
     * @function handleDelete
     * @param {Object} e 
     * @description deletes project
     */
    handleDelete = (e) => {
        e.preventDefault();
        e.stopPropagation(); 
        this.props.deleteProject(this.props.project.get("id"));
    }

    /**
     * @function handleEdit
     * @param {Object} e 
     * @description edits project
     */
    handleEdit = (e) => {
        e.preventDefault();
        e.stopPropagation();
        this.props.openEditProject(this.props.project)
    }

    onRightClick = (e) => {
        e.preventDefault()
        e.stopPropagation();
        this.props.showProjectContextMenu(e.pageX + "px", e.pageY + "px", this.props.project);
     
    }

   


    render() {
        let username = this.props.userName ? this.props.userName : "No Name";
        return(
            <div 
                className={styles["projectContainer"]} 
                onClick={this.handleSelectedProject}
                onContextMenu={this.onRightClick}
               
            >
                <div className={styles["imageContainer"]}>
                  
                    <img
                        src={imageAlts}
                        alt="image of alternatives"
                        style={{
                            width: 390,
                            height: 130,
                        }}
                    />
                </div>
                <div className={styles["textContainer"]}>

                    <div className={styles["titleContainer"]}>
                        <h3 className={styles["title"]}>{this.props.name}</h3>
                    </div>

                    <div className={styles["descriptionContainer"]}>
                        <p className={styles["description"]}>{this.props.project.get("description")}</p>
                        <div className={styles["userContainer"]}>
                            <div className={styles["circle"]}>
                                <p className={styles["initials"]}>{username.charAt(0).toUpperCase()}</p>
                            </div>                           
                        </div>
                    </div>
                </div>
               
               
            </div>
        )
    }
}

export default Project;