import React, {PureComponent} from 'react';
import styles from './listView.module.scss';
import MaterialTable, {MTableBodyRow} from 'material-table';
import Scene from '../scene/Scene';
import {
  getAlternativeImageURL,
  findAlternativesFromID,
} from '../../util/alternatives';
import {
  Md3DRotation,
  MdInsertPhoto
} from 'react-icons/md';
import debounce from 'lodash/debounce';

/**
 * @class ListView
 * @extends React.PureComponent
 * @description presentational component for the list view
 */
class ListView extends PureComponent {

  handleMouseEnter = (viewId, refId) => {
    this.props.hoverAlternative(viewId, refId);
  }

  handleMouseLeave = (viewId, refId) => {
    this.props.dehoverAlternative(viewId, refId);
  }

  handleRowClick = (e, selectedRow) => {
      if (!this.props.altsInCollection.find(alt => alt.get('viewId') === selectedRow.viewId).get('isSelected')) {
        this.props.selectAlternative(selectedRow.viewId);
      } else {
        this.props.deselectAlternative(selectedRow.viewId);
      }
  }

  /**
   * @returns {ReactPureComponent}
   */
  render() {
    return (
      <div className={styles['list-container']}>
        <MaterialTable
          columns={this.props.columns}
          data={this.props.data}
          options={{
            search: true,
            sorting: true,
            paging: false,
            padding: 'dense',
            showTitle: false,
            maxBodyHeight: this.props.height,
            headerStyle: {
              position: 'sticky',
              top: 0,
            },
            rowStyle: (rowData) => {
              return {
                backgroundColor:                    
                // a ternary expression to select the background color                   
                this.props.altsInCollection.filter((alternative) =>
                  alternative.get('isSelected') && alternative.get('viewId') === rowData.viewId).size === 0 

                  ? this.props.altsInCollection.filter((alternative) => this.props.selectedAlts.includes(alternative.get('refId')) 
                    && alternative.get('viewId') === rowData.viewId).size !== 0 

                  ? styles.colorSelectedRelated : '#ffffff' : styles.colorSelected,
              };
            },
          }}

          components={{
            Row: props => (
              <MTableBodyRow
                {...props}
                id={props.data.viewId}
                key={'tableList-' + props.data.viewId} 
                className={"tableList tableList-ref-" + props.data.refId + " tableList-view-" + props.data.viewId} 
                onMouseEnter={debounce(this.handleMouseEnter.bind(this, props.data.viewId, props.data.refId))}
                onMouseLeave={debounce(this.handleMouseLeave.bind(this, props.data.viewId, props.data.refId))}
              />
            )
          }}

          onRowClick={this.handleRowClick.bind(this)}
  
          detailPanel={[
            {
              icon: () => <MdInsertPhoto/>,
              tooltip: 'Show Image',
              render: (rowData) => {
                return (
                  <div>
                    <img src={getAlternativeImageURL(findAlternativesFromID(this.props.altsInCollection, rowData.viewId))}
                      className={styles['alternative-image']}
                      style={{
                        height: 800,
                        width: 800,
                      }}
                      alt={'image for ' + rowData.viewId}/>
                  </div>
                );
              },
            },
            {
              icon: () => <Md3DRotation/>,
              tooltip: 'Show Model',
              render: (rowData) => {
                return (
                  <div>
                    <Scene
                      customStyle={'position: sticky; left: 0;'}
                      width={800} height={800}
                      gltf={rowData.gltf}
                      id={rowData.viewId}
                    />
                  </div>
                );
              },
            },
          ]}
        />
      </div>
    );
  }
}

export default ListView;
