import appConstants from '../constants/appConstants';
import Immutable from 'immutable';

const initialState = Immutable.fromJS({
  successes: [],
  warnings: [],
  errors: [],
  showHistory: false,
});

export default (state = initialState, action={}) => {
  switch (action.type) {
  case appConstants.get("FETCHING_PROJECT"):
    return state
  case appConstants.get('HANDLE_SUCCESS'):
    return state.update('successes',
      (successes) => successes.push(Immutable.fromJS(action.payload)));

  case appConstants.get('HANDLE_WARNING'):
    return state.update('warnings',
      (warnings) => warnings.push(Immutable.fromJS(action.payload))
        .set('cursorChange', true));

  case appConstants.get('HANDLE_ERROR'):
    return state.update('errors',
      (errors) => errors.push(Immutable.fromJS(action.payload))
        .set('cursorChange', true)
        .set('freezeApp', true));

  case appConstants.get('TOGGLE_HISTORY'):
    return state.set('showHistory', !state.get('showHistory'));

  default:
    return state;
  }
};
