import appConstants from '../constants/appConstants';
import collectionObjectConstants from '../constants/collectionObject';
import Immutable from 'immutable';
import {
  fetchCollectionsNotID,
  findCollectionsFromID,
} from '../util/collections';

const initialState = Immutable.fromJS({
  collections: [],
  collectionsFetched: false,
  clippedCollections: [],
  showHelp: false, 
  shouldRemoveFocus: false,
});

export default (state = initialState, action={}) => {
  let collection;
  let collectionsNotID;
  let newCollection;
  let newCollectionsList;
  
  switch (action.type) { 
    case appConstants.get("FETCHING_PROJECT"):
      return state
          .set('collectionsFetched', false)
          .set('collections', Immutable.fromJS([]))
          .set('shouldRemoveFocus', false)
          .set('showHelp', false)
          .set('clippedCollections', Immutable.fromJS([]))
  case appConstants.get('FETCH_COLLECTIONS'):
    return state.set('collections', Immutable.fromJS(action.payload))
      .set('collectionsFetched', true);

  case appConstants.get('CREATE_COLLECTION'):
    return state.update('collections',
      (collections) => collections.push(Immutable.fromJS(action.payload)));

  case appConstants.get('DELETE_COLLECTION'):
    return state.set('collections', fetchCollectionsNotID(
      state.get('collections'), action.payload
    ));

  case appConstants.get('SELECT_COLLECTION'):
    return state.update('collections', (collections) => 
      collections.setIn([collections.findIndex((col) => col.get('viewId') === action.payload.viewId), 'isSelected'], true)
    );

  case appConstants.get('DESELECT_COLLECTION'):
    return state.update('collections', (collections) => 
      collections.setIn([collections.findIndex((col) => col.get('viewId') === action.payload.viewId), 'isSelected'], false)
    );

  case appConstants.get('DESELECT_ALL_COLLECTIONS'):
    return state.update('collections', (collections) => collections.map((collection) => 
      collection.set('isSelected', false)));

  case appConstants.get('CLIP_COLLECTION'):
    return state.update('clippedCollections',
      (clippedCollections) => clippedCollections.push(Immutable.fromJS(action.payload)));

  case appConstants.get('CLEAR_COLS_CLIPBOARD'):
    return state.update('clippedCollections', (clippedCollections) => clippedCollections.clear());

  case appConstants.get('CHANGE_COLLECTION_NAME'):
    collection = findCollectionsFromID(state.get('collections'), action.payload.collectionId);
    newCollection = collection.set(collectionObjectConstants.get('NAME'), action.payload.collectionName).set("isEditingName", false)
    
    collectionsNotID = fetchCollectionsNotID(state.get('collections'), action.payload.collectionId);
    newCollectionsList = collectionsNotID.push(newCollection);
    return state.set('collections', newCollectionsList );

  case appConstants.get("HANDLE_EDIT_NAME_START"):
    return state.update('collections', (collections) => collections.map((
      (collection) => {
        if (collection.get('refId') === action.payload) {
          return collection.set('isEditingName', !collection.get('isEditingName'))            
        }
        return collection;
      }
    )));
  case appConstants.get('EDIT_COLLECTION_NAME'):
    return state.update('collections', (collections) => collections.map((
      (collection) => {

        if (collection.get('isEditingName')) {
          
          return collection.set('collectionName', action.payload.name)            
        }
        return collection;
      }
    )));     

  case appConstants.get('TOGGLE_SINK'):
    return state.update('collections', (collections) => {
      let index = collections.findIndex((col) => col.get('viewId') === action.payload)
      return collections.setIn([index, 'isSinked'], !collections.get(index).get('isSinked'))
    });

  case appConstants.get('TOGGLE_VIEW_MODE'):
    return state.update('collections', (collections) => 
      collections.setIn([collections.findIndex((col) => col.get('viewId') === action.payload.collectionId), 'viewType'], action.payload.viewType)
    );
  

  case appConstants.get('ADD_ALTERNATIVE_TO_COLLECTION'): 
      return state.update('collections', (collections) => collections.map((
        (collection) => {
          if (collection.get('viewId') === action.payload.collectionId) {
            const oldAlts = collection.get('altsid');
            const newAlts = oldAlts.push(Immutable.fromJS(action.payload.alternativeId));
            
            return collection.set('altsid', newAlts)
              .set('dimOrder',Immutable.fromJS(action.payload.dimOrder))
              .set('inputs', Immutable.fromJS(action.payload.collectionInputs))
              .set('outputs', Immutable.fromJS(action.payload.collectionOutputs))            
          }
          return collection;
        }
      )));

  case appConstants.get('ORDER_COLLECTION_DIMENSIONS'):
    return state.update('collections', (collections) => 
      collections.setIn([collections.findIndex((col) => col.get('viewId') === action.payload.viewId), 'dimOrder'], Immutable.fromJS(action.payload.dimOrder))
    );

  case appConstants.get('SET_COLLECTION_DESTINATION'):
    return state.update('collections', (collections) => 
      collections.setIn([collections.findIndex((col) => col.get('viewId') === action.payload.collection), 'destinations'], Immutable.fromJS(action.payload.destinations))
    );

  case appConstants.get('SET_COLLECTION_MODE'):
    return state.update('collections', (collections) => 
      collections.setIn([collections.findIndex((col) => col.get('viewId') === action.payload.collection), 'currentMode'], Immutable.fromJS(action.payload.mode))
    );

  case appConstants.get('UPDATE_COLLECTION_TAGS'):
    return state.update('collections', (collections) => 
      collections.setIn([collections.findIndex((col) => col.get('viewId') === action.payload.collectionId), 'tags'], Immutable.fromJS(action.payload.tags))
    );
  case appConstants.get("REMOVE_FOCUS"):
    return state.set("shouldRemoveFocus", !state.get("shouldRemoveFocus"))
  default:
    return state;
  }
};
