import React, { Component } from 'react';
import { connect } from 'react-redux';
import ListView from '../components/listView/ListView';

class ListContainer extends Component {

    constructor(props) {
        super(props);
    
        const columns = this.props.dimensions.toJS().map((dim) => {
          return {title: dim, field: dim, headerStyle: {position: 'sticky', top: 0, fontWeight: (Object.keys(this.props.collection.get('outputs')).includes(dim)) ? 'bold' : 'normal'}};
        });
    
        const data = this.props.altsInCollection.map((alt) => {
          return {
            viewId: alt.get('viewId'), 
            gltf: alt.get('gltf'), 
            refId: alt.get('refId'),
            collectionViewId: alt.get('collectionViewId'), 
            ...alt.get('parallelData').toJS()
          };
        }).toJS();
    
        this.state = {
          columns: columns,
          data: data,
        };
    }

    render() {
        return (
                <ListView key={'listView-' + this.props.collection.get('viewId')} columns={this.state.columns} data={this.state.data} {...this.props}/>
        );
    }
}

const mapStateToProps = (state) => ({
    dimensions: state.scales.get('keySets'),
  })

export default connect(mapStateToProps)(ListContainer);