import React, {PureComponent} from 'react';
import styles from "./projectContextMenu.module.scss";

class ProjectContextMenu extends PureComponent {
    constructor(props){
        super(props);
    }

    handleOpenProject = (e) => {
        e.preventDefault();
        this.props.shouldCloseProjectContextMenu();
        this.props.selectProject(this.props.project.toJS());
        this.props.history.push("/project");
    }

    handleEditProject = (e) => {
        e.preventDefault();
        this.props.openEditProject(this.props.project)
    }

    handleDeleteProject = (e) => {
        e.preventDefault();
        this.props.handleDeleteProject(this.props.project.get("id"))
    }

    render(){
        
        return(
            <div
              className={styles["contextMenuContainer"]}
              style={{
                  top: this.props.yPos,
                  left: this.props.xPos,
              }}
            >
                <div
                 onClick={this.handleOpenProject}
                 className={styles["openContainer"]}
                >
                    <p className={styles["listItemText"]}>
                        Open 
                    </p>
                </div>
                <div
                    onClick={this.handleEditProject}
                    className={styles["editContainer"]}
                >
                    <p
                     className={styles["listItemText"]}
                    >
                        Edit 
                    </p>
                </div>
                <div
                    onClick={this.handleDeleteProject}
                    className={styles["deleteContainer"]}
                >
                    <p className={styles["listItemText"]}>
                        Delete
                    </p>
                </div>
            </div>
        )
    }
}

export default ProjectContextMenu;