import React, {PureComponent} from 'react';
import styles from './sinkList.module.scss';
import {getLayoutForCollectionFromClipboard} from '../../util/layouts';
import {getCoordinates} from '../../util/collections';
import Immutable from 'immutable';

// TODO: make it functional component

/**
 * @class SinkList
 * @extends React.PureComponent
 * @description this is the presentational component of the SinkList component
 */
class SinkList extends PureComponent {

/**
 * @function handleClick
 * @memberof SinkList
 * @description handles where to open the sinkList menu upon click on the icon in header/context menu
 * @param {event} event
 * @param {number} collectionID
 */
handleClick = (event, collectionID) => {
  const x = event.clientX;
  const y = event.clientY;
  const newPoint = getCoordinates(x, y - 64);

  const collectionLayout = getLayoutForCollectionFromClipboard(this.props.layouts, collectionID);
  
  const layoutObject = Immutable.fromJS({
    x: newPoint.x,
    y: newPoint.y,
    w: collectionLayout ? collectionLayout.get('w') : 3,
    h: collectionLayout ? collectionLayout.get('h') : 5,
    i: collectionID,
  });

  this.props.addLayoutObject(layoutObject);
  this.props.restoreCollection(collectionID);
}

/**
 * @returns {ReactPureComponent}
 */
render() {

  const collectionsSinked = this.props.collections.get('collections').filter((collection) => (
    collection.get('isSinked') === true
  ));

  const collections = collectionsSinked.map((collection) => {
    return (
      <tr className={styles['items-list']} key={'sink-item-' + collection.get('viewId')} 
          onClick={(e) => this.handleClick(e, collection.get('viewId'))}>
        <td>
          {collection.get('collectionName')}
        </td>
      </tr>
    );
  });

    return (
      <div className={styles['sink-list-menu']}>
        <table>
          <tbody>
            <tr>
              <th> Collections in Sink <hr className={styles['hr-sinkList']} /></th>
            </tr>
            {collections}
          </tbody>
        </table>
      </div>
    );
  }
}

export default SinkList;
