import React, {PureComponent} from 'react';
import styles from './alternative.module.scss';
import classnames from 'classnames';
import {CircularProgress} from '@material-ui/core';
import {getAlternativeImageURL} from '../../../util/alternatives';
import Scene from '../../scene/Scene';
import {
  MdRemoveRedEye,
  MdClear,
} from 'react-icons/md';
import debounce from 'lodash/debounce';
import { fromJS } from 'immutable';

// TODO: test: functional component

/**
 * @class Alternative
 * @extends React.PureComponent
 * @description presentational component for a single alternative in a collection
 */
class Alternative extends PureComponent {

  state = {
    transform: null,
    left: 0,
    top: 0,
    wasDragged: false,
  }
  /**
   * @function toggleAltView
   * @description switches the 3d-view for the alternative with the viewID
   * @memberof Alternative
   * @param {event} e
   */
  toggleAltView = (e) => {
    e.stopPropagation();
    e.preventDefault();
    this.props.toggleAltView(this.props.alternative.get('viewId'));
  }


  
 /**
  * @function handleDoubleClick
  * @description restores the alternative (i.e. round-trip)
  * @memberof Alternative
  * @param {event} e
  */
  handleDoubleClick = (e) => {
    e.stopPropagation();
    this.props.restoreAlternative(this.props.alternative.get('viewId'));

  }

  handleHover = (hasEntered, e) => {
      if (hasEntered) {
        this.props.hoverAlternative(this.props.alternative.get('viewId'), this.props.alternative.get('refId'));
      } else {
        this.props.dehoverAlternative(this.props.alternative.get('viewId'), this.props.alternative.get('refId'));
      }
  }

  /**
   * @function handleDeleteClick
   * @description deletes **this** alternative
   * @memberof Alternative
   * @param {event} e - triggering event
   */
  handleDeleteClick = (e) => {
    e.stopPropagation();
    e.preventDefault();
    this.props.removeAlternativesFromCollection(fromJS([this.props.alternative.toJS()]));
  }

  /**
   * @function handleDragStart
   * @description prevents the ghost image during dragging
   * @memberof Alternative
   * @param {event} e - drag event
   */
  handleDragStart = (e) => {
    e.preventDefault();
  }

  move = (e) => {
    const element = document.getElementById(this.props.alternative.get("viewId"))
    if (element != null && this.state.top && this.state.left) {
      if (this.state.left !== element.style.left || this.state.top !== element.style.top) {
        let topState    = this.state.top.replace("px", "");
        let leftState   = this.state.left.replace("px", "");
        let elementTop  = element.style.top.replace("px", "");
        let elementLeft = element.style.left.replace("px", "");
        
        topState     = parseInt(topState);
        leftState    = parseInt(leftState);
        elementTop   = parseInt(elementTop);
        elementLeft  = parseInt(elementLeft);
        let topDiff  =  topState  >=  elementTop  ?   topState  -  elementTop  : elementTop  - topState;
        let leftDiff =  leftState >= elementLeft  ?   leftState  -  elementLeft : elementLeft - leftState;  

        if (topDiff >= 6 || leftDiff >= 6) {
          if (!this.state.wasDragged) {
            this.setState({
              wasDragged:  true,
            });
          }
        }   
      }
    }
  }


  /**
   * 
   * @param {Object} e
   *  
   */
  handleMouseDown = (e) => {
    const el=e.target
    const element = document.getElementById(this.props.alternative.get("viewId"))

    this.setState({
      transform: element.style.transform,
      left:  element.style.left,
      top: element.style.top
    })
    el.addEventListener('mousemove',this.move, true)
  }

 
/**
 * 
 * @param {object} e
 * 
 */
  handleMouseUp = (e) => {
    e.stopPropagation();
    const el=e.target
    if (this.props.alternative.get('altView') === false  && !this.state.wasDragged) {
      if (!this.props.alternative.get('isSelected')) {
        this.props.selectAlternative(this.props.alternative.get('viewId'));
      } else {
        this.props.deselectAlternative(this.props.alternative.get('viewId'));
      }
    } else {
      this.setState({
        top: 0,
        left: 0,
        wasDragged: false,
      });
      el.removeEventListener('mousemove', this.move, true)
    }  
  }

  /**
   * @description The component should render the view for a single alternative.
   * @returns {ReactPureComponent} an alternative component
   */
  render() {
    // this is to figure out which style to apply to the current alternative
    // alternative selected has a highlit border, other alternatives with the same (ref) id
    // are highlighted with a more muted border. See _colors.scss in styles folder
    let situation = 'C';

    if (this.props.alternative.get('isSelected')) {
      situation = 'A'
    } else if (!this.props.alternative.get('isSelected') && this.props.selectedAlts.includes(this.props.alternative.get('refId'))) {
      situation = 'B'
    }
 
    const selectStyleName = classnames({
      [styles['alternative']]: situation === 'C',
      [styles['alternative-selected']]: situation === 'A',
      [styles['alternative-selected-related']]: situation === 'B',
    });

    let altWidth = this.props.layoutForAlt ? this.props.layoutForAlt.w : 1;
    let altHeight = this.props.layoutForAlt ? this.props.layoutForAlt.h : 1;
    
    const altDimensionStyle = {width: (this.props.alternativeImageSize - 12) * altWidth, height: (this.props.alternativeImageSize - 20) * altHeight};
    const imgDimensionStyle = {width: (this.props.alternativeImageSize - 22) * altWidth, height: (this.props.alternativeImageSize - 30) * altHeight};
    const loadingDimensionStyle = {marginTop: '37.5%', marginLeft: '37.5%'};

    return (
      <div
        id={this.props.alternative.get('viewId')}
        className={"alt " + "alt-ref-" + this.props.alternative.get("refId") + " alt-view-" + this.props.alternative.get('viewId') + " " + selectStyleName}
        style={altDimensionStyle}
        onDoubleClick={this.handleDoubleClick}
        onMouseEnter={debounce(this.handleHover.bind(this, true))} 
        onMouseLeave={debounce(this.handleHover.bind(this, false))}
        onMouseDown={this.handleMouseDown}
        onMouseUp={this.handleMouseUp}
      >
        {(situation === 'A' || situation === 'E') && this.props.alternative.get('isLoading') === false ? 
          <MdRemoveRedEye className={styles['alternative-toggle1']} onMouseUp={this.toggleAltView} /> : null}
        {(situation === 'A' || situation === 'E') && this.props.alternative.get('isLoading') === false ? 
          <MdClear className={styles['alternative-toggle2']}  onMouseUp={this.handleDeleteClick}/> : null}
        <div>
          {this.props.alternative.get('altView') && (situation === 'A' || situation === 'E') ?
            <Scene width={imgDimensionStyle.width} height={imgDimensionStyle.height} customStyle = {'padding: 5px'} 
              className={styles['alternative-image']} 
              gltf={this.props.alternative.get('gltf')} 
              id={this.props.alternative.get('altView')}/> : 
              this.props.alternative.get('isLoading') === true ? 
              <div><CircularProgress style={loadingDimensionStyle}/></div> : 
            <img 
              onDragStart={this.handleDragStart} // to prevent dragging ghost image
              src={getAlternativeImageURL(this.props.alternative)}
              style={imgDimensionStyle}
              className={styles['alternative-image']}
              alt={'img-' + this.props.alternative.get('viewId')}
            />
            }
        </div>
      </div>
    );
  }
}

export default Alternative;
