import React from 'react';
import styles from './infoBar.module.scss';

const InfoBar = props => {

    const selected = props.altsInCollection.filter((alt) => alt.isSelected).length;

    return (
        <div className={styles["infobar"]} ref={props.infoRef}>
          <span>
            <span className={styles["infopar-a"]}>{props.altsInCollection.length}</span>
            <span className={styles["infopar-s"]}>{selected}</span>
            <span className={styles["infopar-r"]}></span>
          </span>
        </div>
    );
};

export default InfoBar;